$carousel-ratio-16-9: 56.25%;
$carousel-ratio-3-2: 66.67%;
$carousel-ratio-4-3: 75%;

.cmp-media-carousel{
    padding-top: 80px;
    padding-bottom: 60px;
    overflow-x: hidden;
    .owl-stage {
        display: flex;
        flex-wrap: nowrap;
    }
    .owl-nav {
        &.disabled{
            display: none;
        }
    }
    .video-image{
        position: relative; 
        cursor: pointer;
        &__image{
            padding-bottom: $carousel-ratio-16-9;
            width: 100%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            transition: all .3s;
        }
        .icon-play{
            width: 60px;
            height: 60px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-image: get-icon('play', $color-white);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
            cursor: pointer;
        }  
        &:hover{
            .video-image__image{
                background-color: rgba($color-true-black, 0.25);
                background-blend-mode: overlay;
                transform: scale(1.1);
            }
        }
    }
    .video-overlay {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 999999;
        .icon-pause{
                width: 60px;
                height: 60px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background-image: url('../images/pause.svg');
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center;
                cursor: pointer;
                opacity: 0;
        }
        &:hover{
            background-color: rgba($color-true-black, 0.25);
            .icon-pause{
                opacity: 1;
            }
        }
    }
    .video-content {
        border-radius: 15px;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
    &__title{
        margin-bottom: 40px;
        text-align: center;
        h3{
        	font-size: 48px;
        }
    }
    &__image{
        padding-bottom: $carousel-ratio-16-9;
        width: 100%;
        position: relative;
        height: 0;
        overflow: hidden;
        border-radius: 15px;

        iframe,
        .iframe {
            border-radius: 15px;
            height: 100%;
            left: 0; 
            position: absolute; 
            top: 0;
            width: 100%; 
        }
    }
    .owl-dots{
        text-align: center;
        margin-top: 20px;
        button{
            background: none;
            border: 0;
            border-radius: 2px;
            cursor: pointer;
            height: 40px;
            margin: 0 6px;
            position: relative;
            width: 40px;
            &::before{
                position: absolute;
                content: '';
                width: 100%;
                height: 3px;
                background-color: $color-gray-300;
                top: 50%;
                transform: translateX(-50%);
            }
            &.active{
                &::before{
                    background-color: $color-green-500;  
                } 
            }
        }
    }

    &.aspect_3-2 {
        .cmp-media-carousel__image, 
        .video-image__image {
            padding-bottom: $carousel-ratio-3-2;
        }
    }

    &.aspect_4-3 {
        .cmp-media-carousel__image, 
        .video-image__image {
            padding-bottom: $carousel-ratio-4-3;
        }
    }
}