input:focus,
select:focus,
textarea:focus,
button:focus,
datalist:focus {
	outline: none;
}

input {
	&::-ms-clear {
		display: none;
	}
}

::-webkit-input-placeholder {
	color: inherit;
}

:-moz-placeholder { 
	color: inherit;
}

::-moz-placeholder {  
	color: inherit;
}

:-ms-input-placeholder { 
	color: inherit;
}
