.cmp-staggered-3 {
	margin-bottom: 60px;
	text-align: center;
	@include media-breakpoint-up(lg) {
		margin-bottom: 120px;
	}
    &.no-mb {
        margin-bottom: 0;
    }

	&__image--desktop{
		display: none;
		@include media-breakpoint-up(lg) {
			display: block;
		}
	}
	&__image--mobile{
		display: block;
		@include media-breakpoint-up(lg) {
			display: none;
		}
	}
	.row--count3{
		@include media-breakpoint-up(lg) {
			.left{
				padding-top: 60px;
			}
			.right{
				padding-top: 120px;
			}
		}
	}
	h2{
		margin: 0 0 6px;

		@include media-breakpoint-up(lg) {
			margin: 0 0 12px;
		}
	}
	h4{
		display: none;
		@include media-breakpoint-up(lg) {
			display: block;
		}
	}
	h3{
		@include media-breakpoint-up(lg) {
			display: none;
		}
	}
	img{
		border-radius: 7px;
		height: auto;
		margin: 24px 0 48px;
		width: 100%;

		@include media-breakpoint-up(lg) {
			border-radius: 12px;
			margin: 36px 0 0;
		}
	}
	.col-12:last-child {
		img {
			margin-bottom: 0;
		}
	}
}