.cmp-tabbed-navigation {
    position: relative;
    padding:24px 0;
    @include media-breakpoint-down(lg) {
        padding: 12px 0;
    }

    &__tabs {
        padding: 0;
        margin: 0;
        list-style: none;
        overflow-x: auto;
        white-space:nowrap;
        display: flex;
        flex-direction: row;
        align-items: center;
        @include media-breakpoint-up(lg) {
            width:auto;
            // float:right;
        }
        
        &-anchor {
            position: relative;
            top: -80px;
            @include media-breakpoint-up(lg) {
                top: -100px;
            }
        }
        &-tab {
            display: inline-block;
            margin-right: 40px;

            &.active a {
                font-weight: bold;
                text-decoration: underline;
            }

            &.parent-cta {
                @include media-breakpoint-up(lg) {
                    margin-left: auto;
                }
            }

            &.new {
                a {
                    position: relative;
                &:before {
                    content : '\2022';
                    color: $color-green-500;
                    display: block;
                    position: absolute;
                    left: -0.5em;
                }
                }
            }
        }
    }
    &.no-mb{
        padding-bottom:0 !important;
    }
}