.cmp-accordion-card-images{
    position: relative;
    img{
        width: 100%;
        object-fit: cover;
    }
    &__block{
        padding: 20px 0 40px 0;
    }
    &__eyebrow{
        margin-bottom: 24px;
    }
    &__toggle{
        margin-bottom: 24px;
    }
    &__background{
        width:100%;
        overflow-x: hidden;
    
    }
    @include media-breakpoint-down(lg) {
        &__wrapper{
            background-image: url() !important;
        }
    }
    @include media-breakpoint-up(xl) {
        &__wrapper {
            padding-top: 80px;
            padding-bottom: 80px;
            width: 100%;
            background-size: cover;
            background-repeat: no-repeat;
            align-items: center;
        }   

        &__block{
            background-color: $color-white;
            border-radius: 10px;
            padding: 60px;
            box-shadow: 0 6px 25px rgba(0,0,0, .1);
        }
    }
}

 
 
.layout-50_image{ 
   
   	.accordion {
        overflow:hidden;   	
        &__image{
            padding-top: 80%;
            border-radius: 10px;
            box-shadow: 0 6px 25px rgba(0, 0, 0, 0.1);
        }
   	}
   	
	.right_50_image{
	    padding-top: 80px;
	    padding-bottom: 80px;
	    width: 100%;
	    padding-top: 80%;
	    background-size: cover;
	    background-repeat: no-repeat;
	    -webkit-box-align: center;
	    -ms-flex-align: center;
	    align-items: center;
	}
	
	
	.right-block{
		border-radius: 20px;
		padding: 60px;
		-webkit-box-shadow: 0 0px 0px rgba(0, 0, 0, 0);
		box-shadow: 0 0px 0px rgba(0, 0, 0, 0);
        float:right;
        margin-top: auto;
		margin-bottom: auto;
	}
	
	.left-block{
		background-color: $color-white;
		border-radius: 10px;
		-webkit-box-shadow: 0 0px 0px rgba(0, 0, 0, 0);
		box-shadow: 0 0px 0px rgba(0, 0, 0, 0);
		float:left;
	}
	
	.cmp-accordion-card-images__wrapper
	{
		padding-top: 0px;
		padding-bottom: 0px;
	}
	.cmp-accordion-card-images{
        &__block{
            padding-left: 0px;
            padding-right: 60px;
            width: 100%;
        }
    }
	@include media-breakpoint-down(xl){
		.cmp-accordion-card-images{
			&__block{
				padding-right: 0px;
			}
		}
	}
	
	@include media-breakpoint-down(md) {
		.accordion{
	        &__body-text{
	            padding-right: 0px;
	            padding-left: 0px;
	        }
	        &__detail{
				width: 100%;
				margin-left:auto;
				margin-right:auto;	
			}
			&__image{
				img{
					border-radius: 10px;
				}
			} 
		}
		.cmp-accordion-card-images__block{
			width:100%;
		}
    }
}	


.layout-no_image{
	.cmp-accordion-card-images__wrapper {
		padding-top: 0px;
		padding-bottom: 0px;
		
		.left-block{
			background-color: $color-white;
			border-radius: 10px;
			-webkit-box-shadow: 0 0px 0px rgba(0, 0, 0, 0);
			box-shadow: 0 0px 0px rgba(0, 0, 0, 0);
			
			.cmp-accordion-card-images__eyebrow{
				text-align: center;
			}
		}
	}
	
	@include media-breakpoint-down(md) {
		.accordion{
	        &__body-text{
	        }
		}
		
		.cmp-accordion-card-images__block{
			width:100%;
		}
    }
    
    @include media-breakpoint-down(md) {
		.accordion{
	        &__body-text{
	        	padding-left:0px;
	        	padding-right:0px;

	        }
	        &__detail{
				width: 100%;
				margin-left:auto;
				margin-right:auto;	
			}	       
		}
    }
}
 

.accordion{ 
    border-top: 1px solid $color-gray-200;
    &__headline{
        cursor: pointer;
        display: block;
        padding: 24px 0;
        position: relative;
    }
    &__icon{
        position: absolute;
        right: 0;
        top: 25px;
        width: 24px;
        height: 24px;
        background-image: url("/wp-content/themes/genesis-meraki/images/plus.svg");
        background-size: contain;
        &.active{
            background-image: url("/wp-content/themes/genesis-meraki/images/minus.svg");
        }
    }
    &__image{
        margin: 20px 0 40px;
        padding-top: 54%;
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;
    }
    &__detail{
        padding-bottom: 24px;
        p{
            color: $color-gray-400;
        }
    }
    &__body-text p a{
        color:$color-blue-500;
    }
    &__detail{
        padding-bottom: 24px;
        padding-right: 120px;
    }
    @include media-breakpoint-down(md) {
        &__body-text{
            padding-right: 24px;
            padding-left: 24px;
        }
    }
    @include media-breakpoint-down(lg) {
        &__detail{
            width: 100vw;
            margin-left: calc(50% - 50vw);
            margin-right: calc(50% - 50vw);
        }
        &__body-text{
            padding-right: 40px;
            padding-left: 40px;
        }
    }
    @include media-breakpoint-down(xl) {
        &__detail{
            padding-bottom: 0;
            padding-right: 0;
        }
    }
    @include media-breakpoint-up(xl) {
        &.active{
            &:hover{
                border-color: $color-gray-200;  
                .accordion__headline{  
                    background-color: $color-white;
                }
            }
        }
        &:hover{
            border-color: $color-white;  
            .accordion__headline{  
                background-color: $color-green-100;
            }
        }
        &__headline{
            padding: 24px 60px;
            margin: 0 -60px;
        }
        &__icon{
            right: 60px;
        }
    }
}

