.page-job_description{
	.select2{
		width: 100% !important;
	}
	.select2-selection{
		border-radius: 3px !important;
		border: 1px solid $color-gray-200 !important;
		padding: 8px 15px !important;
		height: auto !important;
		&__arrow{
			right: 15px !important;
			top: 50% !important;
			transform: translateY(-50%);
		}
	}
	&__confirmation{
		align-items: center;
		margin-bottom: 40px;
	}
	
	 
	&__utility {
		&-list {
			position: relative;
			
			&--item {
				padding-left: 36px;

				a {
					color: $color-blue-500;
					font-weight: 500;
					text-transform: uppercase;

					&:before {
						position: absolute;
						content: '';
						width: 24px;
						height: 24px;
						left: 6px;
						background-repeat: no-repeat;
						background-position: center center;
					}

					&.share-icon:before {
						background-image: url(../images/icon-case_study-share.svg);
					}	

					&.download-icon:before {
						background-image: url(../images/icon-case_study-download.svg);
					}
				}
			}
		}
	}

	&__share {
		border-radius: 12px;
		box-shadow: rgba(0,0,0,0.35) 0 0 12px;
		background-color: $white;
		padding: 24px;
		width: auto;
		display: inline-block;

		@include media-breakpoint-down(md) {
			display: block;
		}
		
		&-list {
			list-style: none;
			position: relative;
			padding-left: 36px;
			margin-bottom: 0;

			&--item {
				@include media-breakpoint-down(md) {

				}
				&:last-child {

					@include media-breakpoint-up(lg) {
						margin-bottom:0 !important;
					}
				}

				a {
					color: $color-gray-500;

					&:before {
						position: absolute;
						content: '';
						width: 24px;
						height: 24px;
						left: 0px;
						background-repeat: no-repeat;
						background-position: center center;
					}

					&.email-icon:before {
						background-image: url(../images/icon-case_study-email.svg);
					}

					&.linkedin-icon:before {
						background-image: url(../images/icon-case_study-linkedin.svg);
					}
				}
			}
		}
	}
	
	.job-submission{
	
		max-width: 1120px;
		margin-left: auto;
		margin-right: auto;
		width: 100%;
		
		.half_width{float:left;}
		
		.full_width{
			clear:both;
			width:100%
		}
		
		.title{float:left;}
		.upload-button-wrapper{float:right;}
		.uploaded-wrapper{float:right;}
		
		.fileupload.uploaded{
		
			.upload-button-wrapper{display:none;}
			.uploaded-wrapper{display:block!important;}
		
		}
		
		
		.show-green .mktoError{
			
			display:block!important;
			left: 0;
			bottom: 0;
			width: calc(100% - 10px);
			background: $color-green-200;
			height: 3px!important;
			position: absolute;
			z-index:99;	
			
			.mktoErrorMsg{display:none;}
		}
		
		.show-error .mktoError{
		
			display:block!important;
			
			left: 0;
			bottom: 0;
			width: calc(100% - 10px);
			background: $color-red-200;
			height: 3px!important;
			position: absolute;
			z-index:99;	
			
			.mktoErrorMsg{
				display:block!important;
			}
		}
	}
	
	label, input{width:100%} 
		
}