.post-form.page-job-description{
    .backbtn{
        padding: 24px 0 40px;
        margin-bottom: 0;
        .btn::before{
            width: 40px;
        }
    }
    h1.h4 {
        margin-bottom: 24px !important;
        padding-left: 60px;
        background-image: url(../images/clouds.svg);
        background-position: left center;
        background-repeat: no-repeat;
        background-size: 48px;
    }
    .mktoForm .mktoError {
        width: 100% !important;
        border-radius: 0 0 3px 3px;
    }
    .mktoForm .mktoError .mktoErrorMsg {
        padding: 5px 0 0;
        color: $color-red-300;
        font-size: 14px;
    }
}

.page-job-description{
	
	padding-bottom:20px;
	
	.backbtn {
		a.btn {
		
			color: $color-green-500;
	        
		}
	}	
	
	#content {

        h1, h2, h3, h4 {
            letter-spacing: 0.2px;
            line-height: 24px;
        }
		
		h1 {
			font-size: 20px;
            line-height: 26px;
            @include non-latin($lang: ja, $line-height: 30px);
            @include non-latin($lang: zh, $line-height: 30px);
		}
		h2 {
			font-size: 18px;
		}
		h3 {
			font-size: 18px;
		}
		h4 {
			font-size: 18px;
		}
	
	}
	
	#team-info{
		margin-bottom: 20px;
	}

    &__jobdata{
        display: flex;
        flex-wrap: nowrap;
        font-weight: 500;
        margin-bottom: 20px;
        
        &__meta{
            display: flex;
            flex-wrap: nowrap;
            .meta-field{
                margin-right: 24px;
                display: inline-block;
            }
            @include media-breakpoint-down( sm ){
                flex-wrap: wrap;
                .meta-field{
                    flex: 0 0 100%;
                    max-width: 100%;
                }
            }
        }

        &__utility{
            position: relative;
        }
        
        @include media-breakpoint-down( sm ){
            flex-wrap: wrap;
            .page-job_description__utility-list--item {
                margin-bottom: 0 !important;
                padding-top: 12px;
            }
        }
    }
    &__share-list,
    &__share-list--item{
        margin-right: 0;
    }
    &__share {
        position: absolute;
        left: 0;
        border-radius: 12px;
        box-shadow: rgba(0,0,0,0.35) 0 0 12px;
        background-color: $white;
        padding: 24px;
        width: 240px;
        display: inline-block;
        z-index: 3;

        @include media-breakpoint-down(md) {
            display: block;
        }
        
        &-list {
            list-style: none;
            position: relative;
            padding-left: 36px;
            margin-bottom: 0;

            &--item {
                @include media-breakpoint-down(md) {

                }
                &:last-child {

                    @include media-breakpoint-up(lg) {
                        margin-bottom:0 !important;
                    }
                }

                a {
                    color: $color-gray-500;

                    &:before {
                        position: absolute;
                        content: '';
                        width: 24px;
                        height: 24px;
                        left: 0px;
                        background-repeat: no-repeat;
                        background-position: center center;
                    }

                    &.email-icon:before {
                        background-image: url(../images/icon-case_study-email.svg);
                    }

                    &.linkedin-icon:before {
                        background-image: url(../images/icon-case_study-linkedin.svg);
                    }
                }
            }
        }
    }
    &__content{
        padding-top: 40px;
        padding-bottom: 40px;
    }
    .form-container{
        padding-bottom: 80px;
        
        @include media-breakpoint-down( sm ){
        	padding-bottom: 0px!important;
        }
    }
    .form-content{
        max-width: 1000px;
        margin: 0 auto;
        padding: 60px 80px;
        border-radius: 10px;
        box-shadow: 0 0 18px rgba($color: $color-black, $alpha: .1);
        
        @include media-breakpoint-down( sm ){
        	padding: 0px!important;
        }

        #job-submission{
        
        	

        	
        	@include media-breakpoint-down( sm ){
        		
        		display:none;
        		
        		&.show-up{
        		
		    		dispay:block!important;
		    		
		    		.form-content__close{ 
		    			dispay:block!important;
		    		}
		
		    	}
        
	        	.form-content__close{   
	        		
	        		display:none;
	        	 			
			        width: 18px;
			        height: 18px;
			        background-image: get-icon('close', $color-blue-500);
			        background-size: contain;
			        display: block;
					position: absolute;
					right: 36px;
					margin-top: 0px;   
					z-index: 100000;     
	    		}
        	
	        	padding: 40px 24px 0!important;
	            background-color: $color-white;
	            position: fixed;
	            top: 0px;
				bottom: 0px;
				right: 0px;
				left: 0px;
				height: 100vh;            
	            z-index: 10000;
	            overflow-y: scroll;
	            
	            .sectiontitle:nth-child(2){
	            	margin-top:0px;
	            	
	            	h5{
	            	font-weight: 500;
	            	font-size:24px;
	            	}
	            }

	    		
	    		.submit{
	    			width: 100%;
	    			max-width: 100%;
	    			margin-left: 0px;
	    			padding-left: 12px;
		    		button{
		    			width:calc(100% - 12px);
		    			margin-bottom: 20px;
		    		}
		    		margin-bottom: 100px;
	    		}
	    	}
		}   

	    .apply-button 
	    {
	    	display:none;
	    	
	    	@include media-breakpoint-down( sm ){
	    		
	    		button{width:100%;height:49px;}
	    		
	    		box-shadow: 0px 10px 30px 15px gray;
	    		
	    		display:block;
			    border: 1px solid #C9C9C9;
				padding: 12px 24px;
				position: fixed;
				background-color: $color-white;
				left: 0;
				right: 0;
				bottom: 0;
				
			  	font-size: 16px;
			  	font-weight: 500;
			  	letter-spacing: 1.4px;
			  	line-height: 25px;
				
				z-index: 1;
			}
	    }
        
        @include media-breakpoint-down( sm ){
        	padding:24px;
        }
    }
    

   
    
    .job-submission{
    
        display: flex;
        flex-wrap: wrap;
        margin: 0 -12px;
        .submit{
        	width: 100%;
            margin-top: 24px;
            margin-left: 12px;
            .btn{
                font-size: 16px;
                line-height: 19px;
                letter-spacing: 1.2px;
                padding: 10px 24px 10px;
            }
        }
        .input-wrapper{
            margin-bottom: 24px;
            p{
                padding: 24px 0 0;
                margin-bottom: 0;
            }
            
            
            &.fileupload 
			{
				.mktoErrorMsg
				{
					margin-top: -10px;
				}
				.mktoError
				{
				background-color: $color-white !important;
				}
			}			
        }
        
        .sectiontitle {
		    margin-top: 40px;
		    margin-bottom: 16px;
		}
        
        .input-wrapper label{
            display: block;
        }
        .input-wrapper input{
            line-height: 1;
            padding: 12px 14px !important;
        }
        .input-wrapper.-select{
            position: relative;
            &::before{
                position: absolute;
                right: 30px;
                bottom: 16px;
                content: '';
                width: 12px;
                height: 12px;
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                background-image: get-icon('arrow-down', $color-black);
            }
        }
        .input-wrapper select{
            line-height: 1.2;
            padding: 14px !important;
            max-height: 60px;
            
            overflow: hidden;
			text-overflow: ellipsis;
			padding-right: 40px !important;
        }
        input, select{
            width: 100%;
        }
        
		.show-error {
			.mktoError{
		    	display:block!important;
		    }
		    input{
				border-bottom:0px!important;
			}
		} 
		
		.show-green input,
		.show-green select{
		    border-bottom:3px #67B346 solid!important;
		} 
		
        .fileupload{
        
        
        
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            .title{
                font-size: 24px;
                letter-spacing: 0.4px;
                line-height: 32px;
                flex: 0 0 calc(100% - 150px);
                max-width: 0 0 calc(100% - 150px);
            }
            &.uploaded .uploaded-wrapper{
                display: flex !important;
                flex-wrap: nowrap;
                align-items: center;
                justify-content: flex-end;
                padding: 0;
                .filename{
                    display: none;
                }
                .file-icon{
                    width: 48px;
                    height: 48px;
                    background-image: url(../images/check-file.svg);
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-position: center;
                    display: block;
                }
                .resetfile{
                    margin-left: 12px;
                    width: 16px;
                    height: 16px;
                    background-image: get-icon('close', $color-gray-500);
                    background-repeat: no-repeat;
                    background-size: contain;
                    background-position: center;
                    display: block;
                }
            }
            .uploaded-wrapper,
            .upload-button{
                flex: 0 0 150px;
                max-width: 0 0 150px;
                text-align: right;
            }
            @include media-breakpoint-down( sm ){
                &.uploaded .uploaded-wrapper{
                    justify-content: flex-start;
                }
                .title{
                    margin-bottom: 12px;
                }
                .uploaded-wrapper,
                .title,
                .upload-button{
                    flex: 0 0 100%;
                    max-width: 0 0 100%;
                    text-align: left;
                }   
            }
        }
    }
    .success-message{
        text-align: center;
        padding: 10px;
        h3{
            margin-bottom: 40px;
        }
        p{
            font-size: 20px;
        }
        img{
            max-width: 390px;
            height: auto;
            margin: 24px 0 0;
        }
    }
}