.cmp-3070 {
    margin: 60px 0;

    &.no-mb {
        margin-bottom: 0;
    }

    &.no-mt {
        margin-top: 0;
    }

    @include media-breakpoint-up(lg) {
        margin: 120px 0;
    }
	
	.row.reverse {
	    flex-direction: row-reverse;
	}

	.col-12:first-of-type {
	    margin-bottom: 36px;
	    @include media-breakpoint-up(lg) {
	    	margin-bottom: 0px;
	    }
	}

	&__text-column {
		z-index: 1;
	}
	
	&__title {
		margin-bottom: 36px;
		@include media-breakpoint-up(lg) {
			margin-top: 36px;
		}
	}

	&__subheadline {
		text-transform: none;
		// margin-bottom: 24px;
		color: $color-gray-400;
		font-weight: normal;
		line-height: 28px;
		letter-spacing: 0.2px;
	}

	&__body-copy {

		* {
            color: $color-black;
            text-transform: none;
        }

        h1,h2,h3,h4,h5,h6 {
            margin-bottom: 24px;
        }

        //normalize the h6 to match the subtitle
        h6 {
            letter-spacing: 0.011em;
            text-transform: none;
            font-weight: normal;
        }

        ul { list-style: none;}
        li {
            margin-bottom: 24px;
            position: relative;
            &:before {
                position: absolute;
                display: block;
                content: '';
                width:24px;
                height: 24px;
                background-image: url(../images/icons_uitility_check-mark.png);
                background-repeat: no-repeat;
                margin-left: -24px;
                left: -14px;
                // margin-right: 5px;
            }
        }

        P {
            color: $color-gray-400;
            a{
            	color:$color-blue-500;
            }
        }
		*:last-child {
            margin-bottom: 0;
        }
	}
	
	
	&__image {
		width: 100%;
		height: auto;
	}

}