@keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes spinTop { 
  0%{
    transform:rotate( 0 );  
  }
  100% { 
    transform:rotate( 180deg ); 
  } 
}

@keyframes spinDown { 
  0%{
    transform:rotate( 180deg );  
  }
  100% { 
    transform:rotate( 0 ); 
  } 
}