.cmp-job-location-map {
    
    &.no-mb {
        margin-bottom: 0 !important;
    }

    &__title, &__sub-headline {
        max-width: 880px;
        margin-left: auto;
        margin-right: auto;
    }

    &__sub-headline {
        font-weight: normal;
        font-size: 20px;
    }

    &__map {
        &-col {
            &::-webkit-scrollbar {
                width: 0px;
                background: transparent;
            }

            @include media-breakpoint-down(md) {
                overflow:scroll;

            }
            
        }

        &-container {
            position: relative;
            @include media-breakpoint-down(md) {
                width: fit-content;
            }

            & img {
                width: auto;
                height: 340px;

                // @include media-breakpoint-up(sm) {
                //     height: 600px;
                    
                // }

                @include media-breakpoint-up(md) {
                    height: auto;
                    width: 100%;
                }
            }
        }

        &-item {
            position: absolute;
            z-index: 1000;
            content : '';
            border-radius: 50%;
            
            background-color: $color-blue-400;
            opacity: 0.5;
            transition: 0.25s all ease-in-out;
            text-align: center;
            cursor: pointer;

            width: 24px;
            height: 24px;
            margin-left: -12px;
            margin-top: -12px;

            @include media-breakpoint-up(md) {
                width: 50px;
                height: 50px;
                margin-left: -25px;
                margin-top: -25px;
            }

            &__jobs {
                vertical-align: middle;
                color: $color-white;
                opacity:0;
                
                font-size: 18px !important;
                line-height: 22px;


                @include media-breakpoint-up(md) {
                    font-size: 24px !important;
                    line-height: 50px;
                }

            }


            &:hover, &.active {
               
                opacity: 1;
                z-index: 1010;

                width: 44px;
                height: 44px;
                margin-left: -22px;
                margin-top: -22px;

                @include media-breakpoint-up(md) {
                    width: 90px;
                    height: 90px;
                    margin-left: -45px;
                    margin-top: -45px;
                }

            }

            &:hover &__jobs, &.active &__jobs {
                opacity: 1;
                line-height: 44px;

                @include media-breakpoint-up(md) {
                    line-height: 90px;
                }
            }
           
            
        }
    }

    &__navigation {

        &-container {
            width: auto;
            // margin-left: 50px;
            // margin-right: 50px;
            overflow-x: hidden;
           display: inline-block;
        }

        list-style: none;
        margin: 0;
        padding: 0;
        white-space: nowrap;
        position: relative;
        &-item {
            display: inline-block;
            text-align:center;            

            a {
                display: inline-block;
                border-bottom-style: solid;
                border-bottom-width: 2px;
                border-bottom-color: transparent;
                
            }

            &.active a {
                border-color: $color-blue-400;
                font-weight: 500;
            
            }
        }

        &-control {
            cursor: pointer;
            // position: absolute;
            // top: 0;
            display: inline-block;
            vertical-align: text-bottom;
            border: 0;
            background: none;
            margin:0;
            padding:0;
            width: 48px;
            height: 48px;
            background-color: transparent;

            &:hover, &:focus {
                background-color: transparent;
            }

            &:disabled {
                cursor:arrow;
                opacity: 0.3;
                pointer-events: none;
            }

            img {
                width: 20px;
            }

            &.previous {
                left : 0px;
            }

            &.next {
                right: 0px;
            }

        }
    }
}