.woocommerce {

	.single-product {
		margin-top: 72px;

		@include media_breakpoint_up(lg) {
			margin-top: 36px;
		}

		.product {

			.product_title {
				@extend .h2;
				color: $color-black;
				margin: 0 0 24px;
			}

			.product-headline {
				margin: 0 0 24px;

				&__subheadline {
					margin-top: 12px;

					@include media_breakpoint_up(lg) {
						font-size: 20px;
						@include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 30px);
						@include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 30px);
					}
				}
			}

			.product-detail-image {

				img {
					max-height: 400px;
					width: auto;
				}
			}

			.product-detail-price {
				font-size: 32px;
				letter-spacing: 0.5px;
				line-height: 36px;
			}

			.images,
			.summary {
				clear: both;
				float: none;
				margin: 24px 0;
				width: 100%;
			}

			.wc-tabs-wrapper,
			.wc-tab {
				&:after {
					clear: both;
					content: '';
					display: table;
				}
			}
		}

		.product-type-grouped {

			.product-detail-image {

				img {
					max-height: 300px;
				}
			}
		}

		// Imported Meraki old list styles
		// (updated based on design/QA feedback)
		.list {
			color: $color-gray-400;
			font-size: 14px;

			h6 {
				color: $color-black;
				font-size: 16px;
				font-weight: 700;
				letter-spacing: 0;
				text-transform: none;
			}

			&.flat,
			&.lined,
			&.horiz,
			&.divided, 
			ul {
				list-style: none;
				margin: 0;
				padding: 0
			}

			&.files {
				list-style: none;
				margin: 0
			}

			&.horiz>li,
			&.divided>li {
				float: left;
				padding: 0 15px 0 0
			}

			&.divided>li:after {
				content: "|";
				padding: 0 0 0 15px
			}

			&.divided>li:last-child:after {
				display: none
			}

			&.lined>li {
				border-bottom: 1px solid $color-gray-100;
				margin-bottom: 1em;
				padding: 0 0 1em;
			}

			&.lined>dd {
				border-bottom: 1px solid $color-gray-100;
				margin-bottom: 2em;
				padding: 0 0 2em;
			}

			&.lined>li:last-child,
			&.lined>dd:last-child {
				border: none
			}

			&.lined>dt {
				margin: 1em 0 0.5em;
			}

			&.lined.horiz>li {
				border-bottom: 0;
				border-right: 1px solid $color-gray-100;
				margin: 0 1em 0 0;
				padding: 0 1em 0 0
			}

			&.lined.horiz>li:last-child {
				border: none
			}

			&.lined.horiz.center>li {
				margin: 0;
				padding: 0 1em 0;
				-moz-box-sizing: border-box;
				-webkit-box-sizing: border-box;
				box-sizing: border-box
			}

			&.files li {
				float: left;
				width: 15%;
				height: 16em;
				position: relative;
				padding: 2.5%;
				opacity: .9;
				overflow: hidden
			}

			&.files img {
				max-width: 98%;
				height: 75%;
				border: 1px solid $color-gray-100;
			}

			&.files li:nth-child(5n+1) {
				clear: left
			}

			&.files li a {
				color: $color-gray-500;
				display: block
			}

			&.files li:hover,
			&.files li:focus {
				opacity: 1;
				background: rgba(240, 240, 240, 0.5);
				-webkit-box-shadow: 0 0 5px rgba(100, 100, 100, 0.1) inset;
				-moz-box-shadow: 0 0 5px rgba(100, 100, 100, 0.1) inset;
				box-shadow: 0 0 5px rgba(100, 100, 100, 0.1) inset
			}

			&.files li:hover a,
			&.files li:focus a {
				text-decoration: none;
				color: $color-gray-600;
			}

			&.files li:active {
				-webkit-box-shadow: 0 0 5px rgba(100, 100, 100, 0.4) inset;
				-moz-box-shadow: 0 0 5px rgba(100, 100, 100, 0.4) inset;
				box-shadow: 0 0 5px rgba(100, 100, 100, 0.4) inset
			}


			&.lined.text-columns>.end+dd,
			&.lined.text-columns>li.end {
				@include media_breakpoint_up(md) {
				 	-webkit-column-break-after: always;
				 	-moz-column-break-after: always;
				 	column-break-after: always
				}
			}

			&.lined.text-columns>li.end,
			&.lined.text-columns .end+dd {
				@include media_breakpoint_up(md) {
					padding-bottom: 0;
					margin-bottom: 0;
				}
			}

			&.lined.text-columns>li.end,
			&.lined.text-columns .end+dd,
			&.lined.text-columns>dd:last-child {
				@include media_breakpoint_up(md) {
					border: none;
				}
			}
		}
		
		@include media_breakpoint_up(md) {

			// Imported Meraki old column styles
			.text-columns {

				&.two {
					-webkit-column-count: 2;
					-webkit-column-gap: 1.6em;
					-moz-column-count: 2;
					-moz-column-gap: 1.6em;
					column-count: 2;
					column-gap: 1.6em
				}

				&.three {
					-webkit-column-count: 3;
					-webkit-column-gap: 1.6em;
					-moz-column-count: 3;
					-moz-column-gap: 1.6em;
					column-count: 3;
					column-gap: 1.6em
				}
			}

			.column {
				margin: 0 10px 0 0;
				float: left;
				-moz-box-sizing: border-box;
				-webkit-box-sizing: border-box;
				-ms-box-sizing: border-box;
				box-sizing: border-box;

				&.pad-10 {
					padding: 10px
				}

				&.pad-20 {
					padding: 20px
				}

				&._100 {
					width: 960px
				}

				&._80 {
					width: 794px
				}

				&._75 {
					width: 74%;
					margin-right: 1.5%
				}

				&._75.space-4 {
					width: 71.5%;
					margin-right: 4%
				}

				&._66 {
					width: 66.166666666%;
					margin-right: 1.5%
				}

				&._60 {
					width: 561px
				}

				&._55 {
					width: 550px
				}

				&._50 {
					width: 49.5%;
					margin-right: 1%
				}

				&._50.space-2 {
					width: 49%;
					margin-right: 2%
				}

				&._50.space-4 {
					width: 48%;
					margin-right: 4%
				}

				&._40 {
					width: 379px
				}

				&._40.space-2 {
					width: 369px;
					margin-right: 20px
				}

				&._33 {
					width: 32.33333333%;
					margin-right: 1.5%
				}

				&._25 {
					width: 24.5%;
					margin-right: 1%
				}

				&._25.space-4 {
					margin-right: 4%
				}

				&._20 {
					width: 156px
				}

				&._100,
				&._50:nth-of-type(2n+2),
				&._33:nth-of-type(3n+3),
				&:last-of-type,
				&.space-4:last-of-type {
					margin-right: 0
				}
			}
		}
	}

}