$grid-gutter-width: 24px;


$grid-breakpoints: (
    xs: 0,
    sm: 576px,
    md: 768px,
    lg: 960px,
    xl: 1024px,
    xxl: 1160px,
    xxxl: 1440px
);


$container-max-widths: (
    sm: 768px,
    md: 960px,
    lg: 1024px,
	xl: 1160px,
    xxl: 1440px,
    xxxl: 1600px
);

$spacer: 12px;

$spacers: (
    0: 0,
    1: $spacer,
    2: ($spacer * 2),
    3: ($spacer * 3),
    4: ($spacer * 4),
    5: ($spacer * 5),
    6: ($spacer * 6),
    7: ($spacer * 7),
    8: ($spacer * 8),
    9: ($spacer * 9),
    10: ($spacer * 10),
    11: ($spacer * 11),
);

$primary-color: $color-black;

$link-color: $primary-color;
$link-color-hover: $primary-color;
$link-decoration: 'underline';

$input-color: $primary-color;
$input-placeholder-color: $primary-color;
$input-height: 36px;

$font-family-base: $font-sans-serif;
$font-family-serif: $font-serif;
$font-family-sans-serif: $font-sans-serif;
$font-weight-base: 400;
$line-height-base: 1.5;
$body-color: $color-black;
$body-bg: $color-white;

$modal-backdrop-bg: transparent;