.cmp-text-heavy {
	&.no-mb {
		margin-bottom: 0 !important;
	}
	&__menu {
		list-style: none;
		padding-left: 24px;
		height:0 ;
		overflow: hidden;
		transition: height 0.5s ease-in;

		@include media-breakpoint-up(lg) {
			display: block;
			padding-left: 0px;
			height: auto;
		}

		.open & {
			max-height: 100%;
			height: 1024px;

			@include media-breakpoint-up(lg) { 
				height: auto;
			}
		}

		&-item {
			// margin-top: 12px;
			margin-bottom: 12px;
			a {
				color: $color-green-600;
				font-weight: 600;
			}

			&.active {
				& > a {
					text-decoration: underline;
				}
			}
		}
		
		&-container {
			background-color: $color-white;
			padding-top: 10px;
			@include media-breakpoint-up(lg) { 
				padding-top: 0;
			}

			&.open {
				background-color: $color-gray-50;
				position: fixed;
				top: 60px; 
				right: 0; 
				bottom: 0; 
				left: 0;
				padding: 12px 24px;
				z-index: 1020;

				@include media-breakpoint-up(lg) { 
					top:0;
					background-color: $color-white;
					position: relative;
					padding: 0;
					height: auto;
				}
			}
		}
		&-mobile-control {

			@include media-breakpoint-up(lg) {
				display: none;
			}

			a {
				text-decoration: none;
			}

			&-icon:before {
				content: '\22ee';
				width:20px;
				display: inline-block;
				text-align: center;
				font-weight: 700;
				font-style: normal;
				
				.open & {
					content: '\2715';
				}
			}
		}
	}


	&__submenu {
		list-style: none;
		padding-left: 24px;
		display: none;
		
		.active & {
			display: block;
		}
		&-item {
			margin-top: 12px;
			margin-bottom: 12px;
			a { 
				color: $color-gray-600;
				font-weight: normal;
			}

			&.active {
				& > a {
					text-decoration: underline;
					font-weight: 600;
				}
			}
		}
	}

	&__section {
		display:none;

		&:first-child {
			display:block;
		}
	
	/* styles from meraki website */
	p,li,a {
		font-size: 16px;
	}


	table {
		td,th{
			padding-right:12px;
			@include media-breakpoint-up(lg) {
				padding-right:24px;
			}
		}
	}
	.eol-disclaimer{
		margin-top: 24px;
		font-style: italic;
	}
	.infobox{ 
		background: $color-gray-50;
		padding: 12px;
		margin: 1em 0;
		border: 1px solid $color-gray-200;
		border-radius: 7px;
		@include media-breakpoint-up(lg) {
			padding: 24px; 
		}
		h6{
			padding-bottom:12px;
			@include media-breakpoint-up(lg) {
				padding-bottom:24px;
			}
		}
		table{
			width: calc(100% + 24px);
			font-size: 12px;
			margin-left: -12px;

			@include media-breakpoint-up(lg) {
				font-size:16px;
				width: calc(100% + 48px);
				margin-left: -24px;
			}
			tr{
				td,th{
					width:25%;
					padding: 6px 0;
					@include media-breakpoint-up(lg) {
						width:auto;
						padding: 12px 0;
					}
				}
				td:first-child,th:first-child{
					padding-left:12px;
					@include media-breakpoint-up(lg) {
						padding-left:24px;
					}
				}
				td:last-child,th:last-child{
					padding-right:12px;
					@include media-breakpoint-up(lg) {
						padding-right:24px;
					}
				}
			}
			tbody tr:nth-child(even), thead tr{
				background-color:$color-gray-100;
			}
		}
		ul{
			padding-left:1.5em;
		}
	}
	.clickable {
	    cursor: pointer;
	}
	.divTable {
	    display: table;
	    width: 100%;
	    background-color: $color-gray-50;
	    border-collapse: collapse;
	    margin-bottom: 1em;

	    .divTableBody {
		    display: table-row-group;

		    .divTableRow {
			    display: table-row;

			    .divTableCell, .divTableHead {
				    border: 1px solid $color-gray-200;
				    display: table-cell;
				    padding: 12px;
				    text-align: center;
				    @include media-breakpoint-up(lg) {
						padding: 12px 24px;
					}
				}

				.divTableHeader {
				    font-weight: 600;
				}
			}
		}
	}

	.box-inset {
	    padding: 12px;
	    background: $color-gray-50;
	    border-radius: 7px;
	    @include media-breakpoint-up(lg) {
			padding: 24px;
		}
	}

	.list.flat, .list.lined, .list.horiz, .list.divided {
	    list-style: none;
	    margin: 0;
	    padding: 0;
	}

	.vert-spacer, .row {
	    margin-top: 12px;
	    margin-bottom: 12px;
	    padding-top: 12px;
	    padding-bottom: 12px;
	    // display: block;
	}

	.list.lined > li, .list.lined > dd {
	    border-bottom: 1px dotted $color-gray-200;
	    padding: 12px 0;
	}

	.sup-nav-map{
		width: 100%;
		@include media-breakpoint-up(lg) {
			width:auto;
		}
	}
	.sup-nav-img {
	    width: 100%;
	    @include media-breakpoint-up(lg) {
	    	width: 380px;
	    }
	}

	.mantle {
	    position: relative;
	    width: 100%;
	    background: $color-gray-50;
	    overflow: hidden;
		margin: 0 0 24px;
		border-radius: 7px;

		&.bullets {
		    padding: 0 0 36px;
		}

		#technologies-mantle {
			text-align: center;
			padding:24px 12px;
		    margin-top: 0;
		    border-radius: 7px 7px 0 0;
		    background: $color-gray-400;
		    color: $color-white;
		    @include media-breakpoint-up(lg) {
		    	padding: 60px 24px;
		    }
		}
		#tech-bullets {
		    margin: 36px 0 0 24px;
		    color: $color-gray-400;
		    @include media-breakpoint-up(lg) {
		    	margin: 36px 24px 0 60px;
		    }
		}
		.column{
			@include media-breakpoint-up(lg) {
				float:left;
			}
			&._33 {
				margin-bottom:24px;
				@include media-breakpoint-up(lg) {
				    width: 32.33333333%;
				}
			}
		}
		ul li {
			padding: 0 36px 0 12px;
		    border-left: 3px solid $color-gray-200;
		    @include media-breakpoint-up(lg) {
		    	padding: 0 48px 0 12px;
		    }
		}
	}

	.doc-block {
	    margin: 2em 0 0;
	    border-top: 1px solid $color-gray-200;
	    padding: 12px 0 0;
	    @include media-breakpoint-up(lg) {
	    	margin: 4em 0 0;
	    }
	    .column{
	    	@include media-breakpoint-up(lg) {
				float:left;
			}
	    	&.column._25 {
			    width:100%;
			    position: relative;
				top: -12px;
				border-top: 5px solid $color-green-400;
				padding: 12px 0 0;
				@include media-breakpoint-up(lg) {
					width: 24.5%;
					margin-right: 1%;
				}
			}
			&.column._75 {
				width:100%;
				@include media-breakpoint-up(lg) {
			    	width: 74%;
			    }
			}
	    }
	}

	.cta.bar {
	    margin: 48px 0 0;
	    border-radius: 7px;
	    overflow: hidden;

	    .column {
		    display: table;
		    height: 100px;
		    @include media-breakpoint-up(lg) {
		    	float: left;
		    }
		}
	    &.dark {
		    background: $color-gray-100;
		}
		.left {
			@include media-breakpoint-up(lg) {
		    	float: left;
		    }
		}
		.column._66 {
			width:100%;
			@include media-breakpoint-up(lg) {
		    	width: 66.166666666%;
		    }
		}
		.column._33 {
			width:100%;
			@include media-breakpoint-up(lg) {
		    	width: 32.33333333%;
		    }
		    text-align:center;
		}
		.text {
		    display: table-cell;
		    vertical-align: middle;
		    padding:24px 24px 0;
		    @include media-breakpoint-up(lg) {
		    	padding:24px;
		    }
		    p{
		    	width:100% !important;
		    	margin:0;
		    }
		}
		a.button.cta, button.button.cta {
		    text-align: center;
		    background: $color-green-600;
		    color: $color-white;
		    padding: 13px 25px 10px;
		    margin: 36px 0;

			border-radius: 6px;
			font-family: $font-sans-serif;
			font-size: 14px;
			font-weight: 500;
			letter-spacing: .035em;
			padding: 10px 25px 7px;
			text-decoration: none;
			text-transform: uppercase;
			display: inline-block;
			@include non-latin-fonts();
		}
	}
}
}