.anchor-navigation {
    
    & .desktop {
        @include media_breakpoint_down(sm) {
            display: none;
        }
    }

    & .mobile {
        @include media_breakpoint_up(md) {
            display: none;
        }
    }

    &__wrapper {
        background-color: $color-gray-50;
        
        .sticky & {
            background-color: $color-gray-50;
            position:fixed;
            top: 0px;
            bottom:auto;
            z-index: 1000;
            width: 100%;
        }
    }

    &__tabs {
        list-style: none;
        width: auto;
        position: relative;
        display: flex;
        
        flex-direction: row;
        align-items: center;
        margin:0;
        padding-left:0;

       
        .mobile &-wrapper & {
            height: 60px;
            position: absolute;
            left:0;
            top:0;
        }

        &-wrapper {
            overflow: hidden;
            // display: block;
            height: 60px;
            // width: 100%;
            position: relative;

            
        }

       

        &-tab {
            // display: inline-block;
            margin-right: 40px;
            cursor: pointer;
            display: inherit;

            &:hover {
                font-weight:700;
            }

            &.active {
                color: black;
                font-weight: 700;
                
            }

           
            .mobile & {
                display: block;
                position: relative;
                margin-right: 0;
                flex: 0 0 100%;
                
                a {
                    display: inline-block !important;
                    position: relative;
                    vertical-align: middle;
                    
                }
            }
            
        }
        &-cta {
            margin-left: auto;
           
            .mobile & {
                margin-right:0;
                padding-left: auto;
                padding-right: 0;
            }

           
        }

        &-indicator {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 50px;
            border-bottom: 2px solid green;

            .mobile & {
                height: 2px;
            }
        }
        
    }

    &__arrow {

        content : '';
            display: inline-block;
            color: black;
            width: 24px;
            height: 24px;
            vertical-align: middle;
            background-image: url(../images/icon-anchor-nav-expand.svg);
            background-position: center center;
            background-repeat: no-repeat;
            transform: rotate(-90deg);
        
    }
}