.cmp-carousel-quote{

    overflow-x: hidden;
    background-color: $color-gray-100;
    .owl-stage {
        display: flex;
        flex-wrap: nowrap;
    }
    
    /*
    .row{
        align-items: center;
    }*/
    
    .owl-nav {
        &.disabled{
            display: none;
        }
    }

     
    &.witheffect 
    {
	   	.owl-item{
		    transition-property: padding-left, opacity, height, color, margin-left, margin-right;
			transition-duration: 1s, 0.5s, 3s, 4s, 0.4s, 0.5s;
		}
		.owl-item cite.auhor1{
		    transition-property: padding-left, opacity, height, color, margin-left, margin-right!important;
			transition-duration: 1s, 1s, 3s, 4s, 1.7s, 2s;
		}
		.owl-item cite.position{
		    transition-property: padding-left, opacity, height, color, margin-left, margin-right!important;
			transition-duration: 1s, 2s, 3s, 4s, 1.8s, 1s;
		}
		.owl-item blockquote{
		    transition-property: padding-left, opacity, height, color, margin-left, margin-right!important;
			transition-duration: 1s, 1s, 3s, 4s, 1.4s, 2s;
		}
	} 
 
    .quote-sign{
    	content: "";
        width: 36px;
        height: 36px;
        background-image: url(../images/left-quote.svg);
        background-size: contain;
        background-position: center;
        z-index: 2;
        position: absolute;
        
        @include media-breakpoint-up(sm) {
        	margin-left: 0; 
        }

        @include media-breakpoint-up(md) {
            width: 24px;
            height: 24px;
        } 

        @include media-breakpoint-up(lg) {
            width: 36px;
            height: 36px;

        }
    }
   
    
    .quote-sign-slide{
    	content: "";
        width: 30px;
        height: 30px;
        background-image: url(../images/left-quote.svg);
        position: absolute;
        background-size: contain;
        background-position: center;
        z-index: 0;
        top: -15px;
        left: 0;
        
        @include media-breakpoint-up(sm) {
            top: -15px;
        	margin-left: 0; 
        }

        @include media-breakpoint-up(md) {
            width: 24px;
            height: 24px;
            top: -7px;
        	margin-left: 0; 
        } 

        @include media-breakpoint-up(lg) {
            width: 36px;
            height: 36px;
            margin-left: 36px;
            top: 5px;
        }
    }

    blockquote{
        font-family: $font-sans-serif;
        font-weight: 400;
        margin-bottom: 40px;
        position: relative;
        
        font-size: 20px;
        line-height: 30px;
        letter-spacing: 0.2px;
        padding: 0;
        @include non-latin-fonts();

        @include media-breakpoint-up(lg) {
            font-size: 32px;
            letter-spacing: 0.5px;
            line-height: 36px;
            padding: 24px 0 0; 
            @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 38px);
            @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 38px);
        }

    }

    cite{
        color: $color-gray-300;
        display: block;
        font-size: 20px;
        font-style: normal;
		line-height: 30px;
        text-transform: capitalize;
        @include non-latin($lang: ja, $letter-spacing: 0.2px);
        @include non-latin($lang: zh, $letter-spacing: 0.2px);
    }

    &__image{
        .quote-image{
            width: 100%;
            border-radius: 10px;
            padding-bottom: 100%;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
        }

        @include media-breakpoint-up(lg) {
            margin-bottom: 0;
        }
    }

    &__text{
    	margin-top: 0;
    	
    	@include media-breakpoint-up(md) {
    		margin-top: 30px;
    	}
    	
        @include media-breakpoint-up(lg) {
            margin-top: 40px;
            padding: 0 0 0 60px;
        }
        
        @include media-breakpoint-up(xl) {
            margin-top: 50px;
        }
        
        @include media-breakpoint-up(xxl ){
            margin-top: 90px;
        }
    }

    &__nav{
        display: flex;
        flex-wrap: nowrap;
        position: relative;
        align-items: center;

        @include media-breakpoint-up(md) {
            justify-content: flex-end;
            top: -30px;
        }

    }

    &__stat{
        font-weight: 500;
    }
 
    .quote-nav{
        cursor: pointer;   
        &__prev {
            width: 40px;
            height: 40px;
            background-repeat: no-repeat;
            background-size: 24px 24px;
            background-position: center;
            background-image: url(../images/arrow-left.svg);
        }
        &__next {
            width: 40px;
            height: 40px;
            background-repeat: no-repeat;
            background-size: 24px 24px;
            background-position: center;
            background-image: url(../images/arrow-right.svg);
        }
    }
}