.cmp-product-carousel {
    position: relative;
    overflow: hidden;
    margin-top: 60px;
    margin-bottom: 60px;

    &.no-mb {
        margin-bottom: 0 !important;
    }

    &__mask {
        position: absolute;
        top:0;left:0;bottom:0;
        background-color:$color-white;
        z-index: 100;
        width: 0px;

        &-trailing {
            position: absolute;
            top:0;right:0;bottom:0;
            background-color: rgba(255,255,255,0.5);
            z-index: 100;
            width: 100px;
        }
    }

    &__sidebar {
        background-color: $color-white;
        z-index: 1;
    }

    &__headline {
        margin-bottom: 24px;
    }

    &__body-copy {
        font-size: 20px;
        color: $color-gray-400;
        @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 30px);
        @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 30px);
    }

    &__carousel {
        &-parent {
            overflow-x:hidden;
            @include media_breakpoint_up(lg) {
                overflow-x: visible;
            }
        }

        width: 284px;
    
        @include media_breakpoint_up(sm) {
            width: 556px;
        }

        @include media_breakpoint_up(lg) {
            width: 456px;
        }

        @include media_breakpoint_up(xxl) {
            width: 888px;
        }

        .owl-stage {
            display: flex;
        }

        .owl-item {
            padding: 12px 0;

            &.active {
                opacity: 1;
            }
        }
        .owl-dots {
            width: 100%;
            text-align: center;
            margin-top: 24px;
            @include media_breakpoint_up(lg) {
                display: none;
            }
        }
        .owl-dot {
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: $color-gray-100;
            border: 0;
            margin-left: 5px; 
            margin-right: 5px;
            padding: 0;

            &.active {
                background-color: $color-green-500

            }

            span { display: none}
        }

        .owl-nav {
            display: none;
            @include media_breakpoint_up(lg) {
                display: block;
            }

            button {
                cursor: pointer;
            position: absolute;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            border: 0;
            background-repeat: no-repeat;
            background-position: center center;
            background-color: white;
            box-shadow: 0 0 6px 0 rgba(0,0,0,0.23);

            top: 25%;
            z-index: 101;
            transition: all 0.5s ease;

            &:hover {
                transform: scale(1.1);
            }

            &.disabled {
                display: none;
                opacity: 0.5;
            }

            &.owl-prev {
                left:-13px;
                background-image: url(../images/icons_utility_arrow-left.png);
            }

            &.owl-next {
                right:-13px;
                background-image: url(../images/icons_utility_arrow-right.png);
            }
            
            span { display: none}
            }   
        }
    }
}