.cmp-3-card {
    background: $color-gray-100;
    padding: 40px 0;
    margin-bottom: 40px;
    @include media-breakpoint-up(lg) {
        padding: 64px 0;
        margin-bottom: 64px;
    }

    

    &.no-mb {
        margin-bottom: 0;
    }

    &__wrapper{
        margin-bottom:24px;
    }

    &__item{
        border-radius: 7px;
        overflow: hidden;
        background: $color-white;
        border-radius: 7px;
        display: flex;
        flex-direction: column;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        height: 100%;
    }
    
    &__image {
        width: 100%;
        height:0;
        padding-bottom:54%;
        background-position:center;
        background-size:100% auto;
        background-repeat: no-repeat;
    }
    &__title{
        padding: 12px 12px 24px;
        font-size: 18px;
        font-weight: 500;
        line-height: 26px;
        letter-spacing: 0.2px;

        @include media-breakpoint-up(lg) {
            padding: 24px;
            font-size: 24px;
            line-height: 32px;
            letter-spacing: 0.4px;
        }
    }
    &__cta{
        padding: 12px 12px 24px;
        margin-top:auto;

        @include media-breakpoint-up(lg) {
            padding: 24px;
            font-size: 24px;
        }
    }
    
}