.woocommerce {

	.product-archive {
		margin-top: 72px;
	}

	.product-back {
		margin: 0 0 24px;
	}

	.group-result-ordering {

		.woocommerce-result-count {
			line-height: 45px;
			margin: 0;
		}

		.woocommerce-ordering {

			select {
				background: $color-white;
				height: 45px;
			}
		}
	}

	.products {
		margin-bottom: 36px;
		margin-top: 36px;

		.col {
			padding-bottom: 24px;
		}

		.product {
			border: 1px solid $color-gray-100;
			border-radius: 7px;
			height: 100%;

			@include media_breakpoint_up(lg) {
				border-radius: 12px;
			}

			&:hover {
				box-shadow: 0 0 12px 0 rgba(0,0,0,0.25);
				text-decoration: none;
			}

			.woocommerce-loop-product {

				&__link {
					display: block;
					padding: 24px 24px 0;
					text-decoration: none;
				}

				&__thumbnail {

					img {
						display: block;
						height: 240px;
						width: 100%;
						object-fit: contain;

						html.ie-11 & {
							height: auto;
						}
					}
				}

				&__title {
					font-size: 20px;
					letter-spacing: 0.2px;
					line-height: 26px;
					margin: 6px 0 0;
					@include non-latin($lang: ja, $line-height: 30px);
					@include non-latin($lang: zh, $line-height: 30px);
				}
			}

			.button {
				display: block;
				padding: 12px 24px 24px;

				&:hover {
					border: 0 !important;
				}
			}
		}
	}

	.meraki-grouped-products {
		margin: 36px 0;

		.col {
			padding-bottom: 24px;
		}

		&__header {
			border-bottom: 1px solid $color-gray-300;
			margin-bottom: 36px;
			padding-bottom: 6px;
		}

		&__item {
			border: 1px solid $color-gray-100;
			border-radius: 7px;
			display: block;
			height: 100%;
			padding: 24px;

			@include media_breakpoint_up(lg) {
				border-radius: 12px;
			}

			img {
				display: block;
				height: auto;
				width: 100%
			}

			&:hover {
				box-shadow: 0 0 12px 0 rgba(0,0,0,0.25);
				text-decoration: none;

				.btn-tertiary-green {
					cursor: pointer;
				}
			}
		}
	}
}
