.cmp-2-up {
    &.no-mb {
        margin-bottom: 0px !important;
    }
    
    &__image-container {
        border-radius: 12px;
        background-color: $color-gray-500;
        overflow:hidden;
        
        img {
            width: 100%;
        }
    }

    &__play-icon {
        position: absolute;
        top: 50%; left: 50%;
        opacity: 0.6;
        transition: 0.25s opacity ease-in;

        margin-left: -30px;
        margin-top: -30px;
        width: 60px !important;
        
        @include media_breakpoint_up(lg) {
        
        margin-left: -42px;
        margin-top: -47px;
        width: 95px !important;
        }
        a:hover & {
            opacity:1;
        }
    }

    &__body {
        max-width: 80%;
    }
}