.woocommerce {

	div.product {

		.woocommerce-tabs {

			.panel {
				/*
				margin: 0 0 2em;
				padding: 0;
				*/
				margin-bottom: 24px;

				@include media-breakpoint-up(lg) {
					margin-bottom: 48px;
				}
			}

			ul.tabs {
				/*
				list-style: none;
				padding: 0 0 0 1em;
				margin: 0 0 1.618em;
				overflow: hidden;
				position: relative;
				*/
				margin: 0 0 24px;
				overflow-x: auto;
				padding: 0;
				white-space: nowrap;

				@include media-breakpoint-up(lg) {
					margin-bottom: 48px;
				}

				&::before {
					/*
					position: absolute;
					content: " ";
					width: 100%;
					bottom: 0;
					left: 0;
					border-bottom: 1px solid #d3ced2;
					z-index: 1;
					*/
					display: none;

				}

				li {
					/*
					border: 1px solid #d3ced2;
					background-color: #ebe9eb;
					display: inline-block;
					position: relative;
					z-index: 0;
					border-radius: 4px 4px 0 0;
					margin: 0 -5px;
					padding: 0 1em;
					*/
					background-color: transparent;
					border: none;
					margin: 0 16px 0 0;
					padding: 0;

					@include media-breakpoint-up(sm) {
						margin-right: 48px;
					}

					&:last-child {
						margin-right: 0;
					}

					a {
						/*
						display: inline-block;
						padding: .5em 0;
						font-weight: 700;
						color: #515151;
						text-decoration: none;
						*/
						border-bottom: 2px solid transparent;
						color: $color-black;
						font-size: 14px;
						font-weight: 500;
						padding: 6px 0 0;
						text-transform: uppercase;

						@include media-breakpoint-up(sm) {
							font-size: 16px;
						}

						&:hover {
							/*
							text-decoration: none;
							color: #6b6a6b;
							*/
							border-bottom-color: $color-black;
							color: $color-black;
						}
					}

					&::after,
					&::before {
						/*
						border: 1px solid #d3ced2;
						position: absolute;
						bottom: -1px;
						width: 5px;
						height: 5px;
						content: " ";
						box-sizing: border-box;
						*/
						display: none;
					}

					&.active {
						/*
						background: #fff;
						z-index: 2;
						border-bottom-color: #fff;
						*/

						a {
							border-bottom-color: $color-black;
							color: inherit;
							text-shadow: inherit;
						}
					}
				}
			}
		}
	}
}
