.page-404 {
	padding: 80px 0 48px;

	@include media_breakpoint_up(lg) {
		padding: 96px 0;
	}

	.entry-content {
		margin-top: 48px;
	}

	.search-form {

		&-input {
			border-color: $color-gray-300;
			border-width: 0 0 1px;
			margin-right: 2%;
			padding: 6px 0;
			width: 100%;
			
			border-radius: 0;

			@include media_breakpoint_up(sm) {
				width: 100%;
				max-width: 70%;
			}
		}

		&-submit {
			cursor: pointer;
			// max-width: 25%;
			
			margin-top:15px;

			@include media_breakpoint_up(sm) {
				margin-top: 0;
			}

			@extend .btn;
			@extend .btn-primary-green;
		}

		::placeholder {
			color: $color-gray-200;
			opacity: 1;
		}

		:-ms-input-placeholder {
			color: $color-gray-200;
		}

		::-ms-input-placeholder {
			color: $color-gray-200;
		}
	}
}