.cmp-4-column-icon {

	.text-center {
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 72px;

		h2 {
			margin-bottom: 24px;
		}

		h6 {
			letter-spacing: 0.2px;
		}
	}
	
	&__title {
        // margin-bottom: 60px;
        _:-ms-lang(x), & { margin-top: 24px; }
    }

	.container {
		padding-top: 74px;
		padding-bottom: 28px;

		@include media-breakpoint-up(lg) {
			padding-top: 110px;
			padding-bottom: 66px;
		}
	}
	
	.cmp-4-column-icon__wrapper{
	
		&.col-lg-6{
		
			padding-left: 20px;
			padding-right: 20px;
		
		}
		
		&.col-lg-4{
		
			padding-left: 20px;
			padding-right: 20px;
		
		}
	
	}
	
	&__description{
		font-size: 16px;
		
		@include media-breakpoint-up(lg) {
			font-size: 20px;
			font-weight: 400;
			@include non-latin($lang: ja, $line-height: 30px);
			@include non-latin($lang: zh, $line-height: 30px);
		}

		a:hover, p:hover, h6:hover {
			text-decoration: underline;
			font-size: 16px;
			font-weight: 500;
			
			@include media-breakpoint-up(lg) {
				font-size: 20px;
			}
		}	
	}
	
	&__item{
		text-align: center;	
		margin-bottom: 44px;
		padding-left: 10px;
		padding-right: 10px;
			
		&__icon{
	        position: relative;
	        width: 40px;
	        height: 40px;
	        background-size: contain;	
	        margin-left: auto;
			margin-right: auto;   
			// margin-bottom: 48px; 
			opacity: 0.7;		
		}
		.icon-facebook{
			background-image: get-icon('facebook', $color-gray-400);
		}
		
		.icon-instagram{
			background-image: get-icon('instagram', $color-gray-400);
		}
		
		.icon-youtube{
			background-image: get-icon('youtube', $color-gray-400);
		}
		
		.icon-pinterest{
			background-image: get-icon('pinterest', $color-gray-400);
		}
		
		.icon-twitter{
			background-image: get-icon('twitter', $color-gray-400);
		}
	}
	
	

	.col-lg-4 {
		padding-bottom: 96px;
	}

    &.no-mb {
    	.col-lg-4 {
	        padding-bottom: 0;
	    }
    }

	h6 {
		text-transform: none;
	}

	h4 {
		margin-bottom: 20px;
		font-size:24px;
	}

	&__image {
		margin-bottom: 15px;
		height: 75px;
	}

	
	p {
		color: $color-black;
		font-family: Roboto;
		letter-spacing: 0.2px;
		font-weight: normal;

		font-size: 14px;
		line-height: 20px;

		@include media-breakpoint-up(md) {
			font-size: 20px;
			line-height: 32px;
		}

		a{
			color: $color-black;
		}
	}

	.body-text{
		p:last-child{
			margin-bottom: 36px;
		}
	}

	.btn {
		display: inline;
	}
	
	
}
