.post-form {
    // margin-top: 60px;

    @include media-breakpoint-up(lg) {
        margin-top: 0px;
    }

    .container--top {
        background-color: $color-gray-50;
    }

    &__image {
        width: 100%;
        height: 0;
        padding-bottom: 51.4%;
        background-size: cover;
        background-position: center;
    }

    .container {

        img {
            width: 100%;
            max-width: 350px;
        }

        #row--top {
            position: relative;
        }

        #row--bottom {
            @media (max-width: 959px) {
                min-height: auto !important;
            }
        }

        .backbtn {
            padding: 20px 0px;

            @include media-breakpoint-up(lg) {
                padding: 64px 0px;
            }
        }

        .head-post {
            padding-bottom: 30px;

            @include media-breakpoint-up(lg) {
                padding-bottom: 60px;
            }

            h2 {
                margin-bottom: 30px;
            }
        }

        .col-promo--top {
            h3, h4, h5 {
                padding-bottom: 12px;
            }
        }

        .promo-section {
            text-align: center;

            h3 {
                padding-bottom: 12px;
            }

            img {
                padding-bottom: 24px;
            }

            @include media-breakpoint-up(lg) {
                margin: 50px;
            }
        }

        .mktoForm {
            visibility: visible !important;
            padding: 0 !important;
            margin-bottom: 40px;

            @include media-breakpoint-up(lg) {
                padding: 10px !important;
                margin-bottom: 0px;
            }
        }

        #col-form {
            background-color: $color-white;

            @include media-breakpoint-up(lg) {
                max-width: 665px;
                overflow: hidden;
                border-radius: 7px;
                padding: 0 !important;
                box-shadow: 0 0 20px rgba(0,0,0,.35);
                position: absolute;
                right: 12px;
                top: 64px;
            }
        }

        #col-form img {
            display: none;

            @include media-breakpoint-up(lg) {
                width: 100%;
                max-width: 100%;
                display: block;
            }
        }

        .marketo_form {
            padding-top: 60px;

            @include media-breakpoint-up(lg) {
                min-height: 1200px;
            }

            @include media-breakpoint-up(lg) {
                padding: 48px;
            }

            h5 {
                margin-bottom: 12px;
            }
        }

        .col {
            width: 100%;
            flex-basis: auto;

            @include media-breakpoint-up(lg) {
                width: 48.2%;
                padding: 0 12px;
                flex-grow: unset;
            }
        }

        .type-banner {
            display: inline-block;
            margin-top: 24px;
            margin-bottom: 20px;
            font-weight: 700;
            text-transform: uppercase;
            font-size: 14px;
            line-height: 14px;
            padding: 5px 8px;
            letter-spacing: 1.5px;
            background-color: $color-yellow-300;
            color: $color-black;

            &.on-demand {
                background: $color-blue-300;
            }
        }

        .confirmation {
            display: none;
        }

        .btn-tertiary-green::after {
            display: none;
        }

        .btn-tertiary-green::before {
            @include tertiary-arrow();
            transform: rotate(180deg);
        }
    }


    .mktoForm {
        .mktoLabel, .mktoHtmlText, .mktoCheckboxList label {
            color: $color-gray-400;
            font-size: 14px;
            font-weight: 400 !important;
            line-height: 20px !important;
            letter-spacing: 0.2pt;
        }

        .mktoHtmlText, .mktoCheckboxList label {
            color: $color-black !important;
        }

        .mktoHtmlText {
            a {
                text-decoration: underline;
                padding: 0;
            }
        }

        .mktoFieldWrap.customFieldWrap {
            margin-bottom: 0;
        }

        .mktoFormCol {
            margin-bottom: 40px !important;
        }

        .mktoButtonRow .mktoButtonWrap {
            margin-left: 0 !important;

            button {
                font-family: $font-sans-serif;
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                letter-spacing: 1.2px;
                padding: 10px 24px 10px;
                @include non-latin-fonts();
            }
        }

        .mktoField {
            border: solid 1px $color-gray-200;
            background: $color-white;
            padding: 14px !important;
            border-radius: 3px;
            height: auto !important;
        }
        /*IE hide select style */
        select.mktoField {
            padding-top: 14px !important;
            padding-bottom: 14px !important;
            -webkit-appearance: none !important;
        }

        .mktoLabel {
            width: 100% !important;

            .mktoAsterix {
                display: none !important;
            }
        }

        .mktoError {
            left: 0 !important;
            bottom: 0 !important;
            width: calc(100% - 10px) !important;
            background: $color-red-300 !important;
            height: 3px !important;

            .mktoErrorArrowWrap {
                display: none !important;
            }

            .mktoErrorMsg {
                //display: none !important;
                background-color: transparent;
                background-image: none;
                border: none;
                box-shadow: none;
                text-shadow: none;
                padding: 0;
            }
        }

        .mktoValid:not(.mktoCheckboxList) {
            border-bottom: 3px $color-green-500 solid;
        }

        input:focus {
            border-bottom: 3px $color-gray-200 solid;
        }

        .mktoCheckboxList {
            label {
                cursor: pointer;
            }

            &:hover input ~ .checkmark {
                background-color: $color-gray-200;
            }

            input {
                position: absolute;
                opacity: 0;
                cursor: pointer;

                &:checked {
                    & ~ .checkmark {
                        background-color: transparent;
                        border: 2px $color-green-500 solid;
                        border-radius: 2px;

                        &:hover {
                            background-color: transparent;
                        }
                    }

                    & ~ .text {
                        color: $color-black;
                        font-weight: 500;
                    }

                    & ~ .checkmark:after {
                        display: block;
                    }
                }
            }

            .checkmark {
                position: absolute;
                top: 10px;
                left: 0;
                height: 20px;
                width: 20px;
                border: 2px gray solid;
                border-radius: 2px;
                // border-radius: 50%;
                &:after {
                    content: "";
                    position: absolute;
                    display: none;
                    left: 4px;
                    top: 1px;
                    width: 7px;
                    height: 11px;
                    border: solid $color-green-500;
                    border-width: 0 2px 2px 0;
                    -webkit-transform: rotate(45deg);
                    -ms-transform: rotate(45deg);
                    transform: rotate(45deg);
                }
            }
        }
    }
}

.post-webinar {

    .container {

        .time_list {
            margin-bottom: 36px;

            .select-time {
                font-size: 16px;
                letter-spacing: 0.2pt;
                line-height: 24px;
                margin-bottom: 24px;
            }

            .time-row {
                display: block;
                position: relative;
                padding-left: 35px;
                margin-bottom: 24px;
                cursor: pointer;
                -webkit-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;

                .text {
                    color: $color-gray-400;
                    font-size: 20px;
                    font-weight: 400;
                }

                &:hover input ~ .checkmark {
                    background-color: $color-gray-200;
                }

                input {
                    position: absolute;
                    opacity: 0;
                    cursor: pointer;

                    &:checked {
                        & ~ .checkmark {
                            background-color: transparent;
                            border: 2px $color-green-500 solid;

                            &:hover {
                                background-color: transparent;
                            }
                        }

                        & ~ .text {
                            color: $color-black;
                            font-weight: 500;
                        }

                        & ~ .checkmark:after {
                            display: block;
                        }
                    }
                }

                .checkmark {
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 25px;
                    width: 25px;
                    border: 1px gray solid;
                    border-radius: 50%;

                    &:after {
                        content: "";
                        position: absolute;
                        display: none;
                        left: 7px;
                        top: 4px;
                        width: 7px;
                        height: 11px;
                        border: solid $color-green-500;
                        border-width: 0 2px 2px 0;
                        -webkit-transform: rotate(45deg);
                        -ms-transform: rotate(45deg);
                        transform: rotate(45deg);
                    }
                }
            }
        }
    }
}

.post-collateral {
    #row--top {
        padding-top: 30px;
        padding-bottom: 0px;
        @include media-breakpoint-up(lg) {
            padding-top: 60px;
            padding-bottom: 20px;
        }
    }
}
.cmp-3-card {
    &__headline {
        margin-bottom:24px ;
    }
}
