.collateral-result{
    min-height: 300px;
    text-align: center;
    .content-row{
        width: auto;
        text-align: left;
    }
    .col-12{
        margin-bottom: 12px;
        margin-top: 12px;
    }
    .btn{
        margin-top: 80px;
    }
    .noresult{
        strong{
            font-size: 36px;
            font-family: $font-headline;
            color: $color-gray-400;
            line-height: 1.2;
            @include non-latin-fonts();
        }
        p{
            font-size: 16px;
            color: $color-gray-400;
        }
    }
    @include media-breakpoint-up(lg) {
        .noresult{
            strong{
                font-size: 72px;
            }
            p{
                font-size: 20px;
                @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 38px);
                @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 38px);
            }
        }
    }
}
