	#respond input#submit,
	a.button,
	button.button,
	input.button {
		background-color: transparent;
		border: 0 !important;
		border-bottom: 1px solid transparent !important;
		border-radius: 0;
		color: $color-green-600;
		font-family: $font-sans-serif;
		font-size: 14px;
		font-weight: 500;
		letter-spacing: .035em;
		padding: 0;
		text-align: left;
		text-decoration: none;
		text-transform: uppercase;
		vertical-align: middle;
		@include non-latin-fonts();

		&.btn-tertiary-green {
			@include non-latin($lang: ja, $letter-spacing: 0.5px);
			@include non-latin($lang: zh, $letter-spacing: 0.5px);
		}

		&:focus {
			outline: 0 !important;
			box-shadow: none !important;
		}

		@include media_breakpoint_up(lg) {
			font-size: 16px;
			letter-spacing: .086em;
		}

		&:after {
			@include tertiary-arrow();
		}

		&:hover,
		&:focus {
			color: $color-green-600;
			border-bottom: 1px solid $color-green-600 !important;
		}
	}

	#respond input#submit:hover,
	a.button:hover,
	button.button:hover,
	input.button:hover {
		background-color: transparent;
		color: $color-green-600;
	}

	#respond input#submit.alt,
	a.button.alt,
	button.button.alt,
	input.button.alt {
		background-color: transparent;
		color: $color-blue-400;
	}

	#respond input#submit.alt:hover,
	a.button.alt:hover,
	button.button.alt:hover,
	input.button.alt:hover {
		background-color: transparent;
		border-bottom: 1px solid $color-blue-400 !important;
		color: $color-blue-400;
	}

	#respond input#submit.alt.disabled,
	#respond input#submit.alt.disabled:hover,
	#respond input#submit.alt:disabled,
	#respond input#submit.alt:disabled:hover,
	#respond input#submit.alt:disabled[disabled],
