.single-webinars{
	.webinars-calendar,
	.select2{
		width: 100% !important;
	}
	.select2-selection{
		border-radius: 3px !important;
		border: 1px solid $color-gray-200 !important;
		padding: 8px 15px !important;
		height: auto !important;
		&__arrow{
			right: 15px !important;
			top: 50% !important;
			transform: translateY(-50%);
		}
	}
	&__confirmation{
		align-items: center;
		margin-bottom: 40px;
	}
	.social-share{
		a{
			display: block;
			width: 24px;
			height: 24px;
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center;
		}
		&__twitter{
			a{
				background-image: url('/wp-content/themes/genesis-meraki/images/twitter.png');
			}
		}
		&__facebook{
			a{
				background-image: url('/wp-content/themes/genesis-meraki/images/facebook.png');
			}
		}
	}
	@include media-breakpoint-down(md){
		&__select{
			margin-bottom: 24px;
		}

		.social-share{
			padding-left: 12px;
			padding-right: 12px;
			.col{
				flex: 0 0 50px;
				max-width: 50px;
			}
			a{
				width: 30px;
				height: 30px;
			}
		}
	}
}