#bs4_overlay {
	display: none;
	left: 0;
	opacity: 0.4;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 9999;

	&.active {
		display: block;
	}

	i {
		display: block;
		background: red;
		opacity: 0.4;
	}
}

#bs4_toggle {
	background: rgba(0,0,0,0.75);
	border: 2px solid #fff;
	box-shadow: 0 0 2px #000;
	color: #fff;
	cursor: pointer;
	display: block;
	font-weight: bold;
	padding: 0.5em 4em 0.5em 2em;
	position: fixed;
	right: 10px;
	top: 10px;
	z-index: 10000;

	.close {
		font-size: 10px;
		position: absolute;
		right: 10px;
		top: 13px;
	}

	.toolbar-horizontal & {
		top: 90px;
	}

	.admin-bar & {
		top: 42px;
	}
}
