@import "icons.scss";

#colorbox{
    &.video-modal{
        z-index: 99999;
        overflow: hidden;
        width: 100% !important;
        left: 0 !important;

        @include  media-breakpoint-down(sm) {
            height: 100% !important;
            top: 380px !important;
            #cboxWrapper{
                height: 100% !important;
            }    
        }

        @media screen and (orientation:landscape) {
            overflow-y: auto !important;
            height: calc(100% * 2) !important;
            #cboxWrapper{
                height: calc(100% * 2) !important;
            }
        }

        #cboxClose{
            position: absolute;
            top: 24px;
            right: 24px;
            background: none;
            border: none;
            .close-icon {
                width: 24px;
                height: 24px;
                background-image: get-icon('close', $color-blue-300);
                background-repeat: no-repeat;
                background-size: contain;
                cursor: pointer;
            }
        }
    }
    &.youtube-overlay{
	    z-index: 99999;
        overflow: hidden;
        #cboxClose{
            position: absolute;
            top: calc(50% - 250px);
            left: calc(50% + 420px);
            background: none;
            border: none;
            @include  media-breakpoint-down(lg) {
                top: 120px;
                right: 80px;
                left: auto;
            }
            @include  media-breakpoint-down(sm) {
                right: 20px;
            }
            .close-icon {
                width: 24px;
                height: 24px;
                background-image: get-icon('close', $white);
                background-repeat: no-repeat;
                background-size: contain;
                cursor: pointer;
                &:hover{
                    background-image: get-icon('close', $color-gray-300);
                }
            }
        }
    }
}

#cboxOverlay{
    overflow: hidden;
    background-color: rgba(0,0,0,0.75);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    animation: fadeIn .3s;
}

.open-popup{
    overflow: hidden;
    max-height: 100vh;
    
    .video-modal{
    
        #cboxContent{
            padding: 80px 0;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
            border-radius: 10px;
            background: $white;
            max-width: 100%;
            height: calc(100vw + 120px) !important; 
            min-height: 520px;
            text-align: center;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center 120px;
            @include media-breakpoint-down(sm) {
                width: 100% !important;
                video,
                iframe{
                    width: 100%;
                    height: calc((100vw / 100) * 56);
                    margin-top: 60px;
                }
            }
            @include media-breakpoint-up(md) {
                height: 560px !important;
                background-position: top center;
            }
            @include media-breakpoint-up(lg) {
                left: 50%;
                top: 50%;
                transform: translate(-50%,-50%);
                padding: 60px;
                width: 800px !important;
            }

            @media screen and (orientation:landscape) {
                max-width: 90%;
                left: 50%;
                top: 50px;
                transform: translateY(0);
                transform: translateX(-50%);
                height: auto !important; 
                min-height: 1px;
                margin-bottom: 50px;
            }

        }   
        #cboxTitle{
            font-family: $font-headline;
            font-size: 36px;
            letter-spacing: 0.5px;
            line-height: 42px;
            margin-bottom: 24px;
            padding-left: 24px;
            color: $color-green-500;
            width: 80%;
            text-transform: capitalize;
            text-align: left;
            float: none !important;
            @include non-latin-fonts();
            
            @include media-breakpoint-up(md) {
                text-align: center;
                padding-left: 0;
                width: 100%;
            }
        }
        #cboxLoadedContent{
            width: 100% !important;
            height: auto !important;
        }
    }
    .youtube-overlay{
        #cboxContent {
            position: relative;
            float: none !important;
            width: 100vw !important;
            height: 100% !important;
            #cboxLoadedContent {
                width: 100vw !important;
            }
            video,
            iframe{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                width: 750px;
                height: 420px;
                @include  media-breakpoint-down(lg) {
                    width: 560px;
                    height: 314px;
                    top: 320px;
                }
                @include  media-breakpoint-down(sm) {
                    width: 320px;
                    height: 180px;
                    top: 280px;
                }
            }
        }
    }
}