body {
    color: $color-black;
    font-family: $font-sans-serif;
    font-size: 16px;
    letter-spacing: initial;
    line-height: 24px;
    margin: 0;
    text-rendering: auto;
    font-smooth: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    @include non-latin-fonts();
}

.serif {
    font-family: $font-serif;
    @include non-latin-fonts();
}

.sans-serif {
    font-family: $font-sans-serif;
    @include non-latin-fonts();
}

a {
    text-decoration: none;
    &:hover {
        color: inherit;
        text-decoration: underline;
    }
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
    color: inherit;
}

p a {
    color: $color-blue-500;
    &:hover {
        color: $color-blue-500;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-family: $font-headline;
    font-weight: 700;
    margin: 0;
    @include non-latin-fonts();
}

h1,
.h1 {
    font-size: 46px;
    line-height: 50px;
}

h2,
.h2 {
    font-size: 36px;
    letter-spacing: 0.5px;
    line-height: 42px;
}

h3,
.h3 {
    font-size: 24px;
    line-height: 32px;
}

h4,
h5,
h6,
.h4,
.h5,
.h6 {
    font-family: $font-sans-serif;
    @include non-latin-fonts();
}

h4,
.h4 {
    font-size: 20px;
    letter-spacing: 0.2px;
    line-height: 26px;
}

h5,
.h5 {
    font-size: 18px;
    font-weight: 500;
    letter-spacing: 0.2px;
    line-height: 26px;
}

h6,
.h6,
.eyebrow {
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0.2px;
    line-height: 24px;
    text-transform: uppercase;
}

.eyebrow {
    @include non-latin($lang: ja, $line-height: 20px);
    @include non-latin($lang: zh, $line-height: 20px);
}

.footnote,
.eyebrow-sm {
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 0.2px;
    line-height: 16px;
}

.eyebrow-sm {
    text-transform: uppercase;
}

figure {
    margin: 0;
}

.text-upper {
    text-transform: uppercase;
}

@include media-breakpoint-up(lg) {
    h1,
    .h1 {
        font-size: 96px;
        letter-spacing: -1px;
        line-height: 96px;
        @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 105px);
        @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 115px);
    }
    h2,
    .h2 {
        font-size: 56px;
        letter-spacing: 0.1px;
        line-height: 56px;
        @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 80px);
        @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 86px);
    }
    h3,
    .h3 {
        font-size: 48px !important;
        line-height: 52px !important;
        letter-spacing: 0.2px;
        @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 62px);
        @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 68px);
    }
    h4,
    .h4 {
        font-family: $font-headline;
        font-size: 32px;
        letter-spacing: 0.2px;
        line-height: 40px;
        -moz-font-feature-settings: "onum" 1, "ss01" 1;
        -ms-font-feature-settings: "onum" 1, "ss01" 1;
        -o-font-feature-settings: "onum" 1, "ss01" 1;
        -webkit-font-feature-settings: "onum" 1, "ss01" 1;
        font-feature-settings: "onum" 1, "ss01" 1;
        @include non-latin($lang: ja, $line-height: 38px);
        @include non-latin($lang: zh, $line-height: 38px);
        @include non-latin-fonts();
    }
    h5,
    .h5 {
        font-size: 24px;
        letter-spacing: 0.2px !important;
        line-height: 32px;
        @include non-latin($lang: ja, $line-height: 36px);
        @include non-latin($lang: zh, $line-height: 36px);
    }
    h6,
    .h6 {
        font-size: 20px;
        line-height: 30px;
        letter-spacing: 0.2px;
        @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 30px);
        @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 30px);
    }
    .footnote {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.2px;
    }
    .eyebrow {
        font-size: 20px;
        letter-spacing: 0.2px;
        line-height: 30px;
    }
}