.cmp-job-filter {
    #scrollright{
        display: none;
        height: 18px;
        background-repeat: no-repeat;
        background-image: get-icon('arrow-right', $color-gray-300);
        z-index: 100; 
        background-color: $color-white;
        position: absolute;
        right: -7px;
        width: 60px;
        height: 30px;
        top: 5px;
        @include media-breakpoint-down(sm) {
            display: block;
        }
    }
    .loading{
        display: block;
    }
    .container_result{
        background-color: $color-white;
        padding-top: 0;
        @include media-breakpoint-down(sm) {
        	margin-top: -10px!important;
        }
        @include media-breakpoint-down(md) {
        	margin-top: -30px;
        }
        @include media-breakpoint-down( xl ) {
        	margin-top: -30px;
        }
    }
    .table-responsive{
        width: 100%;
        @include media-breakpoint-down( xxl ) {
            margin-left: auto;
            margin-right: auto;
        }
        @include media-breakpoint-down( xl ) {
           	margin-left: auto;
            margin-right: auto;
        }

        @include media-breakpoint-down( md ) {
            margin: 0 calc(50% - 50vw);
            width: calc(50% + 100vw);
        }
        @include media-breakpoint-down( sm ) {
            overflow: hidden;
            overflow-x: scroll;
        }
    }
    table{
        width: 100%;
        tr{
            display: flex;
            flex-wrap: nowrap;
        }
        th,td{
            width: auto;
            p{
                margin-bottom: 0;
            }
        }
        thead{
            @include media-breakpoint-down(sm) {
                position: relative;
                /*
                &::before{
                    content: '';
                    position: absolute;
                    right: 24px;
                    top: 5px;
                    width: 18px;
                    height: 18px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-image: get-icon('arrow-right', $color-gray-300);
                }
                */
            }
            th{
            	font-size: 16px;
			  	font-weight: 500;
			  	letter-spacing: 0.2px;
			  	line-height: 18px;
            }
            
            @include media-breakpoint-down(sm) {           
            	th{
            		font-size: 14px!important;
            	}
            }
            th,td{
                @include media-breakpoint-down( xl ) {
                    padding-bottom: 24px;
                    &:first-child{
                        padding-left: 80px;
                    }
                    &:last-child{
                        padding-right: 80px;
                    }
                }
                @include media-breakpoint-down( lg ) {
                    &:first-child{
                        padding-left: 60px;
                    }
                    &:last-child{
                        padding-right: 60px;
                    }
                }
                @include media-breakpoint-down(sm) {
                    &:first-child{
                        padding-left: 24px;
                    }
                    &:last-child{
                        padding-right: 24px;
                    }
                }
            }
        }
        tbody{
            font-size: 16px;
            line-height: 26px;
            letter-spacing: 0.2px;
            a {
                color: $color-blue-500;
                @include media-breakpoint-down(sm){
                    margin-top: 12px;
                }
            }
            @include media-breakpoint-up(lg) {
                font-size: 20px;
                line-height: 26px;
                a {
                    font-size: 18px;
                }
            }
            tr{
                &:nth-child( 2n ){
                    th,td{
                        background-color: $color-gray-100;
                    }
                }
                @include media-breakpoint-down( xl ) {
                    &:nth-child( 2n - 1 ){
                        th,td{
                            background-color: $color-gray-100;
                        }
                    }
                    &:nth-child( 2n ){
                        th,td{
                            background-color: $color-white;
                        }
                    }
                }
            }
            
            @include media-breakpoint-down(sm) {
            	
            	td .apply{
	              font-size: 16px;
				  font-weight: 500;
				  letter-spacing: 1.4px;
				  line-height: 25px;
	            }
            } 
            
            th,td{
                padding: 24px 12px;
                &:last-child{
                    text-align: right;
                    padding-right: 24px;
                }
                @include media-breakpoint-down(xl) {
                    &:first-child{
                        padding-left: 80px;
                    }
                    &:last-child{
                        padding-right: 80px;
                    }
                }
                @include media-breakpoint-down(lg) {
                    &:first-child{
                        padding-left: 60px;
                    }
                    &:last-child{
                        padding-right: 60px;
                    }
                }
                @include media-breakpoint-down(sm) {
                    &:first-child{
                        padding-left: 24px;
                    }
                    &:last-child{
                        padding-right: 24px;
                    }
                }
            }
            .apply{
            	a {
            		font-weight: 500;
			  		letter-spacing: 1.4px;
			  		line-height: 25px;
            	}
            }
            th{
                font-weight: normal;
            }
        }
        @include media-breakpoint-up( md ){
            .mobile-only{
                display: none;
            }
            .desktop-only{
                display: block;
            }
        }
    }
}