.cmp-featured-webinars{
	padding-top:54px;
	padding-bottom:54px;
	&__eyebrow{
        font-family: $font-headline;
        font-size: 24px;
        font-weight: bold;
        line-height: normal;
		margin-bottom: 40px;
		@include media-breakpoint-up(lg) {
			font-size: 54px;
		}
    }

    &__wrapper{
		border-radius: 7px;
		overflow: hidden;
		height: 100%;
		position:relative;

		display: flex;
   		flex-direction: column;
   		background-color: $color-white;
	}
	&.color-white{
		background-color: $color-white;
	}

	&.color-gray{
		background: $color-gray-50;
	}

	&__image{
		width:100%;
		height:0;
		padding-bottom:54%;
		background-size:cover;
		background-position:center;
	}

	
	&__wrapper:hover{
		box-shadow: 0 0 20px rgba(0,0,0,.35);
		cursor: pointer;
		.cmp-featured-webinars__title a{
			text-decoration: underline;
		}
	}

	&.color-gray &__wrapper{
		background-color: $color-white;
	}

	&.color-white &__wrapper{
		background: $color-gray-50;
	}

	&__copyarea{
		padding: 12px 12px 24px;
		color: $color-gray-400;
		.extra ul{
			
			margin-bottom: 16px;
			padding-left: 0;

			@include media-breakpoint-up(lg) {
				display: flex;
			flex-wrap: wrap;
			}
		}
		.extra li{
			padding-right: 23px;
			list-style-type: none;

			@include media-breakpoint-up(lg) {
				list-style-type: disc;
				&:first-child{
					list-style-type: none;
				}
			}
		}

		.extra li a{
			font-size: 16px;
			font-weight: 500;
			color: $color-gray-300;
		}
		@include media-breakpoint-up(lg) {
			padding: 24px;
		}
	}

    &__webtype{
    	display:inline-block;
    	font-weight:700;
    	text-transform:uppercase;
    	font-size:14px;
   		line-height:14px;
    	padding: 5px 8px;
    	letter-spacing: 1.5px;
    	background: $color-yellow-300;
    	margin-bottom:12px;
    	color: $color-black;
    }
	&__webtype.on-demand{
		background: $color-blue-300;
	}
    &__title{
	    font-size: 18px;
		font-weight: 500;
		line-height: 26px;
		margin-bottom: 12px;
		letter-spacing: 0.2px;
		color: $color-black;

		@include media-breakpoint-up(lg) {
			font-size: 24px;
			font-weight: 500;
			line-height: 32px;
			letter-spacing: 0.4px;
		}
    }

    & .col-12{
    	margin-bottom: 12px;
    	margin-top: 12px;
    	@include media-breakpoint-up(lg) {
    		margin-bottom: 16px;
    		margin-top: 16px;
    	}
    }

    &__cta{
    	margin-top:auto;
    	padding: 0px 12px 24px;

		@include media-breakpoint-up(lg) {
			padding: 0 24px 24px;
		}
    } 

	&__eyebrow{
		font-size: 24px;
		font-weight: 400;
		line-height: 32px;

		@include media-breakpoint-up(lg) {
			font-size: 54px;
			font-weight: 400;
			line-height: 58px;
		}
	}
	
	.webinar-card-link{ 	
    	&:hover{        	
			text-decoration:none!important;
		}
    	.cmp-featured-webinars__cta div.btn
    	{
    		border: 0px !important;
    	}
    }
}