.dataTables_wrapper{
    * {
        font-size: 14px;
        font-weight: normal !important;
        line-height: 1.4;
        strong,
        b{
            font-weight: 700 !important;
        }
    }
    label {
        margin-bottom: 24px;
        input,
        select{
            padding: 3px 8px;
            margin: 0 5px !important;
            border: 0;
            border-bottom: 1px solid #ccc;
        }
    }
    .paginate_button{
        text-indent: 100%;
        overflow: hidden;
        width: 14px;
        height: 14px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        &::before,
        &::after{
            display: none;
        }
        &.previous{
            background-image: get-icon( 'arrow-left', $color-gray-300 );    
        }
        &.next{
            background-image: get-icon( 'arrow-right', $color-gray-300 ); 
        }
    }
}

.tablepress{
    width: 100% !important;
	tr {
		border-top: 1px solid #ccc;
		padding-top: 40px;
        vertical-align: top;
        &.highlighted{
            th,
            td{
                background-color: $color-gray-100 !important;
            }
        }
    }

    th,
    td{
        &.highlighted{
            background-color: $color-gray-100 !important;
        }
    }

    thead {
        tr {
            border-top: none
        }
        th {
            padding: 0.5em 1% 1em;
            font-weight: 700 !important;
        }
    }
    tfoot th,
    tbody td {
        border-left: none !important;
        padding: 0.5em 1% 2em !important;
        background-color: $color-white !important;
    }
    
    th:first-child,
    td:first-child {
        width: 100px !important;
    }

    td:first-child{
        font-weight: 700 !important;
    }

    th {
        text-align: center !important;
    }

    thead th {
        background-color: $color-white !important;
    }

    tbody th {
        padding-top: 15px;
        padding-bottom: 20px;
    }

    tbody td {
        padding: 20px 10px !important;
    }
}

.dataTables{
    &_scrollHeadInner,
    &_scrollFootInner{
        width: 100% !important;
    }
    &_scroll{
        overflow: auto;
    }
}