.cmp-featured-casestudy{
	&__wrapper{
		border-radius: 2px;
		box-shadow: 0px 3px 12px rgba(0,0,0,0.1);
		background-color: $color-white;
		&.col-12{
			margin-top: 0;
			margin-bottom: 24px;
		}
	}
    &__image{
		width: 100%;
		padding-bottom: 54%;
		background-size: cover;
		background-position: center;
	}
	&__title{
		margin-top: 12px;
		margin-bottom: 12px;
		text-transform: capitalize;
		font-size: 18px;
		font-weight: 600;
	}
	&__highlights{
		ul{
			list-style-type: none;
			padding-left: 0;
			li{
				padding-bottom: 8px;
				margin-bottom: 8px;
				border-bottom: 2px solid $color-gray-100;
			}
		}
	}
	&__cta{
		width: 100%;
		margin-top: 24px;
		@include media-breakpoint-up(lg){
			position: absolute;
			left: 10px;
			right: 24px;
			bottom: 0;
			margin-top: 0;
		}
		ul{
			list-style-type: none;
			padding-left: 0;
			margin-bottom: 0;
			display: flex;
			flex-wrap: wrap;
			li{
				display: inline-block;
				flex: 0 0 100%;
				max-width: 100%;
				margin-bottom: 10px;
				@include media-breakpoint-up(lg){
					flex: 0 0 50%;
					max-width: 50%;
				}
				a{
					color: $color-gray-400;
				}
			}
		}
	}
	&__icon{
		width: 24px;
		height: 24px;
		background-image: url(../images/article-icon.png);
		background-size: contain;
		display: inline-block;
		vertical-align: sub;
		margin-right: 10px;
	}
	&__info{
		position: relative;
	}
}