.cmp-language-switcher {
	font-size: 14px;
	position: relative;

	@include media-breakpoint-up(lg) {
		font-size: 14px;
	}

	&__selector {
		align-items: center;
		color: $color-gray-400;
		display: flex;
		flex-direction: row;

		.globe-icon {
			background-image: get-icon('globe', $color-blue-400);
		}

		.chevron-icon {
			background-image: get-icon('arrow-down', $color-gray-400);
			height: 5px;
			transition: transform .3s ease;
			width: 9px;
		}

		&:hover {
			color: $color-green-500 !important;
			text-decoration: none;

			.globe-icon {
				background-image: get-icon('globe', $color-green-500) !important;
			}

			.chevron-icon {
				background-image: get-icon('arrow-down', $color-green-500) !important;
			}
		}

		@include media-breakpoint-up(lg) {

			.transparent-header:not(.menu-open) .header-util & {
				color: $color-white;

				.globe-icon {
					background-image: get-icon('globe', $color-white);
				}

				.chevron-icon {
					background-image: get-icon('arrow-down', $color-white);
				}
			}
		}
	}

	&__menu {
		display: none;
		padding: 4px 0;
		min-width: 10rem;

		@include media-breakpoint-up(lg) {
			background: $color-white;
			box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
			margin: 4px 0 0;
			padding: 8px 0;
			position: absolute;
			z-index: 9;
		}

		&__item {
			display: block;
			padding: 4px 0 4px 26px;

			@include media-breakpoint-up(lg) {
				padding: 6px 26px;
				white-space: nowrap;
			}

			&.active {
				font-weight: 700;
			}

			&:hover {
				color: $color-green-500;
				text-decoration: none;

				@include media-breakpoint-up(lg) {
					background: $color-green-100;
					color: $color-gray-400 !important;
				}
			}
		}
	}

	&.active {

		.cmp-language-switcher__selector {

			.chevron-icon {
				transform: rotate(180deg);
			}
		}

		.cmp-language-switcher__menu {
			display: block;
		}
	}
}