.cmp-product-family-grid {
	padding:40px 0;

	@include media_breakpoint_up(lg) {
		padding: 80px 0;
	}

	&__header {
		border-bottom: 1px solid $color-gray-200;
		margin: 24px 0;
		padding-bottom: 12px;
	}

	&__tile {
		border-radius: 7px;
		display: block;
		font-size: 14px;
		margin: 24px 0 0;
		padding: 12px;

		@include media_breakpoint_up(lg) {
			border-radius: 12px;
			padding: 24px 16px 24px 0;
		}

		&__title {
			color: $color-green-500;
		}

		&__img {

			&__wrap {
				height: 200px;
			}

			img {
				height: auto;
				margin: 0 auto;
				max-height: 100%;
				max-width: 100%;
				width: auto;
			}
		}

		&__body {
			margin-top: 24px;

			@include media_breakpoint_up(md) {
				margin-top: 0;
			}
		}

		&__details {
			margin-top: 24px;

			&--custom {
				margin-top: 24px;

				ul {
					list-style: none;
					margin: 0;
					padding: 0;

					li {
						border-bottom: 1px solid $color-gray-100;
						margin: 0 0 9px;
						padding: 0 0 9px;
						
						&:last-child{
							border-bottom: 0
						}
					}
				}
			}
		}

		.product_labels {
			margin-top: 12px;
			text-align: center;
		}

		.product_label {
			display: inline-block;
			vertical-align: top;

			&--new {
				border: 2px solid $color-blue-500;
				border-radius: 4px;
				color: $color-blue-500;
				font-size: 12px;
				font-weight: 700;
				letter-spacing: 1px;
				line-height: 20px;
				margin: 0 4px;
				padding: 0 12px;
				text-transform: uppercase;
			}

			&--bluetooth {
				&:after {
					background: url('../images/icon-bluetooth.png') no-repeat 50%;
					background-size: auto 100%;
					content: '';
					display: inline-block;
					height: 24px;
					margin: 0 4px;
					width: 20px;
				}
			}
			&--lte {
				&:after {
					background: url('../images/icon-lte.png') no-repeat 50%;
					background-size: 100% auto;
					content: '';
					display: inline-block;
					height: 24px;
					margin: 0 4px;
					width: 20px;
				}
			}
			&--radio {
				&:after {
					background: url('../images/icon-radio.png') no-repeat 50%;
					background-size: 100% auto;
					content: '';
					display: inline-block;
					height: 24px;
					margin: 0 4px;
					width: 20px;
				}
			}
			&--wifi {
				&:after {
					background: url('../images/icon-wifi.png') no-repeat 50%;
					background-size: 100% auto;
					content: '';
					display: inline-block;
					height: 24px;
					margin: 0 4px;
					width: 20px;
				}
			}
		}

		&:hover {
			box-shadow: 0 0 12px 0 rgba(0,0,0,0.25);
			text-decoration: none;
		}

		hr {
			border-color: $color-gray-100;
			margin: 9px 0;
		}

		h5,
		.mx-cat-text {
			color: $color-gray-400;
			font-size: 11px;
			font-weight: 500;
			text-transform: uppercase;
		}

		h5 {
			margin: 0 0 12px;
			letter-spacing: 0;
			line-height: 24px;
		}

		.list {

			p {
				margin: 0;
			}

			&.flat,
			&.lined,
			&.horiz,
			&.divided {
				list-style: none;
				margin: 0;
				padding: 0
			}

			&.lined>li,
			&.lined>dd {
				border-bottom: 1px solid $color-gray-100;
				margin: 0 0 9px;
				padding: 0 0 9px;
			}

			&.lined>li:last-child,
			&.lined>dd:last-child {
				border: none
			}

			&.lined>dt {
				margin: 0;
			}
		}	
	}
}
