.cmp-sub-navigation {

    &__wrapper {
        border-top: 1px solid $color-gray-200;
        border-bottom: 1px solid $color-gray-200;
        position: relative;
        width: 100%;

            background-color: #F8F8F8;
        .sticky & {
            position:fixed;
            top: 0px;
            bottom:auto;
            z-index: 1000;
            width: 100%;
        }

        .block-editor-block-list__layout & {
            position: relative;

        }
    }
    
    &__items {
        display: flex;
        flex-direction: row;
        align-items: center;
        list-style: none;
        margin: 0;
        padding: 0;
    
        &-item {
            cursor: pointer;
            display: inherit;

            a {
                font-weight: normal;

                &:hover {
                    font-weight: 500;
                    text-decoration: none;
                }
            }

            &.active {
                border-bottom: 2px solid $color-black;

                a {
                    font-weight: 500;
                }
            }
        }
    }

    .mobile {
        position: relative;
        
        &.open .cmp-sub-navigation__arrow {
            transform: rotate(180deg);
        }
    }

    &__dropdown {
        position: absolute;
        width: 100%;
        top: 100%;
        left: 0px;
        z-index: 1000;
        min-height: 400px;
        background-color: rgba(57,57,57,0.75);

        .cmp-sub-navigation__items {
            flex-direction: column;

            &-item {
                
                background-color: $color-gray-100;
                width: 100%;
                border-bottom: 1px solid $color-gray-200;
                
                .container {
                    padding-left: 24px;
                    padding-right: 24px;

                    @include media_breakpoint_up(md) {
                        padding-left: 40px;
                        padding-right: 40px;
                    }
                }

                a {
                    
                    display: inherit;
                }

            }
        }

        

        
    }

    &__arrow {

        content : '';
            display: inline-block;
            color: black;
            width: 24px;
            height: 24px;
            vertical-align: middle;
            background-image: url(../images/icon-anchor-nav-expand.svg);
            background-position: center center;
            background-repeat: no-repeat;
            // transform: rotate(-90deg);
        
        
    }

    &__tabs-anchor {
        
        position :  relative;

        @include media_breakpoint_up(lg) 
        {
            top: -80px;
        }

    }
}

.cmp-sub-navigation-anchor {

    &__name {
        display : none;
    
        .block-editor-block-list__layout & {
            display: block;
        }
    }
}