.cmp-collage{
    text-align: center;
    padding-top: 80px;
    padding-bottom: 80px;
    h3{
        margin-bottom: 40px;
    }
  
  
    &__items{
        overflow: hidden;
        width: auto;
        margin: 0 -12px;
        &:after {
            content: '';
            display: block;
            clear: both;
        }
        .collage-block{
            width: 20%;
            float: left;
            padding: 0 12px 12px;
            &:last-child{
                padding-bottom: 0;
            }
            .sub-item{
                width: 100%;
                text-align: center;
                background-position: 50%;
                background-repeat: no-repeat;
                background-size: cover;
                border-radius: 10px;
            }
        }
    }
    .sub-item{
       position: relative; 
       &__text{
           width: 100%;
           position: absolute;
           padding: 12px;
           left: 50%;
           top: 50%;
           transform: translate(-50%, -50%);
           * {
               margin-bottom: 0;
           }
       }
       &.text-gray-100{
            border: 5px solid $color-gray-100;
       }
       &.text-green-500{
            border: 5px solid $color-green-500;
        }
        &.text-gray-500{
            border: 5px solid $color-gray-500;
       }
       &.text-blue-500{
            border: 5px solid $color-blue-500;
        }
        &.text-blue-50{
            border: 5px solid $color-blue-50;
            color: $color-blue-50;
       }
       &.text-blue-800{
            border: 5px solid $color-blue-800;
            color: $color-blue-800;
        }

    }
    @include media-breakpoint-down( sm ){
        &__items{
            margin: 0 -6px;
            .collage-block{
                padding: 0 6px 12px;
                &:nth-child(1){
                    width: 50%;
                    .sub-item{
                        padding-bottom: 125%;
                    }
                }
                &:nth-child(2){
                    width: 50%;
                    position: relative;
                    padding-bottom: calc(62% - 1px);
                    .sub-item{
                        position: absolute;
                        width: calc(100% - 12px);
                        left: 8px;
                        bottom: 12px;
                        padding-bottom: calc(100% - 10px);
                    }
                }
                &:nth-child(3){
                    width: 100%;
                    .sub-item{
                        padding-bottom: 75%;
                    }
                }
                &:nth-child(4){
                    width: 50%;
                    .sub-item{
                        padding-bottom: 100%;
                    }
                }
                &:nth-child(5){
                    width: 50%;
                    .sub-item{
                        padding-bottom: 75%;
                    }
                }
                &:nth-child(6){
                    width: 50%;
                    .sub-item{
                        padding-bottom: 100%;
                    }
                }
                &:nth-child(7){
                    width: 50%;
                    margin-top: calc(-35% - 12px);
                    .sub-item{
                        padding-bottom: 100%;
                    }
                }
            }
        }
    }
    @include media-breakpoint-between( md, lg ){
        &__items{
            .collage-block{
                padding: 0 12px 24px;
                &:nth-child(1){
                    width: 50%;
                    .sub-item{
                        padding-bottom: 125%;
                    }
                }
                &:nth-child(2){
                    width: 40%;
                    padding-bottom: 50%;
                    position: relative;
                    .sub-item{
                        position: absolute;
                        left: 12px;
                        bottom: 24px;
                        padding-bottom: calc(100% - 10px);
                    }
                }
                &:nth-child(3){
                    width: 50%;
                    .sub-item{
                        padding-bottom: 75%;
                    }
                }
                &:nth-child(4){
                    width: 50%;
                    margin-top: calc(-25% - 12px);
                    .sub-item{
                        padding-bottom: 100%;
                    }
                }
                &:nth-child(6){
                    width: 50%;
