                    .sub-item{
                        padding-bottom: 100%;
                    }
                }
                &:nth-child(5){
                    width: 50%;
                    .sub-item{
                        padding-bottom: 75%;
                    }
                }
                &:nth-child(7){
                    width: 50%;
                    .sub-item{
                        padding-bottom: 125%;
                    }
                }
            }
        }
    }
    @include media-breakpoint-up( lg ){
