@mixin tertiary-arrow($image: '../images/arrow_green.png') {
	background-image: url($image);
	background-position: center;
	background-repeat: no-repeat;
	content: '';
	display: inline-block;
	height: 17px;
	vertical-align: -3px;
	width: 18px;
}

@mixin non-latin($lang: ja, $letter-spacing: nochange, $line-height: nochange) {
	html[lang|="#{$lang}"] & {
		@if $letter-spacing != nochange {
			letter-spacing: $letter-spacing;
		}
		@if $line-height != nochange {
			line-height: $line-height;
		}
	}
}

@mixin non-latin-fonts() {
	html[lang|="ja"] & {
		font-family: $font-japanese;
	}
	html[lang|="zh"] & {
		font-family: $font-chinese;
	}
}