.cmp-masthead-hero {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    margin-bottom: 60px;
    position: relative;
    .cmp-masthead-hero__headline {
        -moz-font-feature-settings: "onum" 1, "ss01" 1;
        -ms-font-feature-settings: "onum" 1, "ss01" 1;
        -o-font-feature-settings: "onum" 1, "ss01" 1;
        -webkit-font-feature-settings: "onum" 1, "ss01" 1;
        font-feature-settings: "onum" 1, "ss01" 1;
    }
    @include media-breakpoint-up(lg) {
        margin-bottom: 96px;
    }

    &.no-mb {
        margin-bottom: 0;
    }

    &__text-black {
        .cmp-masthead-hero__headline,
        .cmp-masthead-hero__subheadline,
        .cmp-masthead-hero__eyebrow,
        .cmp-masthead-hero__body,
        .cmp-masthead-hero__statistic-copy,
        .cmp-masthead-hero__statistic-value {
            color: $color-gray-500;
        }
    }

    /* variations */
    &.masthead-full,
    &.masthead-statistics {
        min-height: 580px;
        overflow: hidden;
        height: auto;
        @include media-breakpoint-down(lg) {
            min-height: 540px;
        }
        background-color: $color-gray-200;
        .container {
            padding-top: 112px;
            padding-bottom: 120px;

            @include media-breakpoint-down(lg) {
                padding-bottom: 60px;
            }
        }

        &.text-center {
            padding-top: 264px;
            padding-bottom: 192px;

            @include media-breakpoint-down(lg) {
                padding-top: 0px;
                padding-bottom: 120px;
            }

            .cmp-masthead-hero__cta-wrap {
                margin-top: 48px !important;
            }
        }

        &.transparent-header {
            @include media-breakpoint-up(lg) {
                padding-top: 120px;
            }
        }
    }

    &.masthead-statistics & {
        &__headline,
        &__statistic-value {
            line-height: 40px;
            font-size: 32px;
            @include media-breakpoint-up(md) {
                line-height: 72px;
                font-size: 54px;
            }
        }

        &__statistic-copy {
            font-family: $font-sans-serif;
            font-weight: 500;
            @include non-latin-fonts();

            @include media-breakpoint-up(md) {
                font-size: 24px;
                line-height: 32px;
            }
        }
    }

    &.hero {
        background-size: 0;

        &.no-mb {
            margin-bottom: 0;
        }

        .container {
            @include media-breakpoint-down(lg) {
                padding-top: 36px;
                padding-bottom: 72px;
            }
        }
        .cmp-masthead-hero__headline {
            color: $color-black;
            font-weight: 400;
            margin-bottom: 0.5em;
            -moz-font-feature-settings: "onum" 1, "ss01" 1;
            -ms-font-feature-settings: "onum" 1, "ss01" 1;
            -o-font-feature-settings: "onum" 1, "ss01" 1;
            -webkit-font-feature-settings: "onum" 1, "ss01" 1;
            font-feature-settings: "onum" 1, "ss01" 1;
            font-size: 36px;
            line-height: 42px;
            font-weight: 400;
            margin-bottom: 0.5em;
            @include media-breakpoint-up(lg) {
                font-size: 72px;
                line-height: 72px;
                letter-spacing: 0.1px;
            }
        }

        .cmp-masthead-hero__subheadline,
        .cmp-masthead-hero__body {
            color: $color-gray-400;
            font-size: 20px;
            line-height: 30px;
            letter-spacing: 0.2px;
        }

        .cmp-masthead-hero__subheadline {
            color: $color-gray-400;
            // font-size: 16px;
            // font-weight: 400;
            // letter-spacing: 0.2px;
            // line-height: 24px;
            @include non-latin($lang: ja, $line-height: 20px);
            @include non-latin($lang: zh, $line-height: 20px);

            @include media-breakpoint-up(lg) {
                font-size: 24px;
                line-height: 32px;
                @include non-latin($lang: ja, $line-height: 30px);
                @include non-latin($lang: zh, $line-height: 30px);
            }
        }

        .cmp-masthead-hero--Image__mobile {
            width: 100%;
            height: auto;
        }
        @include media-breakpoint-up(lg) {
            background-size: cover;
            margin: 0 0 120px;
            padding: 96px 0;

            .cmp-masthead-hero--Image__mobile {
                display: none;
            }

            .text-center .row,
            .text-left .row,
            .text-right .row {
                text-align: left;
                background-color: $color-white;
                width: calc(50% - 12px);
                padding: 60px 60px 84px;
                border-radius: 12px;
                margin: 0;
                box-sizing: border-box;
                display: inline-block;

                -moz-box-shadow: 0 0 12px rgba(0, 0, 0, 0.5);
                -webkit-box-shadow: 0 0 12px rgba(0, 0, 0, 0.5);
                box-shadow: 0 0 12px rgba(0, 0, 0, 0.5);
            }
        }
    }
    // &.hero.transparent-header{
    //     margin-top:0;
    //     @include media-breakpoint-up(lg) {
    //         padding-top:120px;
    //         .container{
    //             padding-top:60px;
    //         }
    //     }
    // }

    /* end variations */

    &__body,
    &__headline,
    &__subheadline,
    &__eyebrow,
    &__statistic-value,
    &__statistic-copy {
        color: $color-white;
    }

    &__body {
        *:last-child {
            margin-bottom: 0;
            font-size: 20px;
            letter-spacing: 0.2px;
            line-height: 30px;
        }
    }

    &__subheadline {
        max-width: 650px;

        @include media-breakpoint-up(lg) {
            @include non-latin($lang: ja, $line-height: 30px);
            @include non-latin($lang: zh, $line-height: 30px);
        }

        .text-left & {
            margin-left: 0;
            margin-right: auto;
        }

        .text-center & {
            font-family: $font-headline;
            -moz-font-feature-settings: "onum" 1, "ss01" 1;
            -ms-font-feature-settings: "onum" 1, "ss01" 1;
            -o-font-feature-settings: "onum" 1, "ss01" 1;
            -webkit-font-feature-settings: "onum" 1, "ss01" 1;
            font-feature-settings: "onum" 1, "ss01" 1;
            margin-left: auto;
            margin-right: auto;
            position: relative;
            display: block;
            @include non-latin-fonts();

            @include media-breakpoint-down(lg) {
                align-items: center;
            }

            @include media-breakpoint-up(lg) {
            }
        }

        .text-right & {
            margin-left: auto;
            margin-right: 0;
        }
    }

    &__cta-2 {
        margin-top: 24px;
        display: block;
        border: 0 !important;

        &:hover {
            border: 0 !important;
        }

        @include media-breakpoint-up(lg) {
            display: inline;
            padding: 4px 24px 4px;
        }
    }

    /* video card */
    &__video-card {
        background-color: $color-gray-100;
        .video-play {
            color: inherit;
            padding: 22px 22px;
            display: block;
            text-decoration: none;
            @include media-breakpoint-up(md) {
                padding: 13px 22px;
            }
            .row {
                align-items: center;
                margin: 0;

                .avatar {
                    flex: 0 0 70px;
                    max-width: 70px;
                    margin-right: 20px;
                    padding: 0;
                    position: relative;
                    @include media-breakpoint-up(lg) {
                        flex: 0 0 94px;
                        max-width: 94px;
                    }
                    img {
                        width: 70px;
                        height: 70px;
                        border-radius: 100%;
                        border: 2px solid $color-white;
                        box-shadow: 0 0 12px rgba(0, 0, 0, 0.5);
                        @include media-breakpoint-up(lg) {
                            width: 94px;
                            height: 94px;
                        }
                    }
                    .icon-play {
                        width: 70px;
                        height: 70px;
                        border-radius: 50%;
                        background-image: get-icon("play", $color-white);
                        background-size: 34px;
                        background-position: center;
                        background-repeat: no-repeat;
                        background-color: rgba(0, 0, 0, 0.1);
                        position: absolute;
                        left: 0;
                        top: 0;
                        z-index: 9;
                        @include media-breakpoint-up(lg) {
                            width: 94px;
                            height: 94px;
                        }
                    }
                }
                .card-text {
                    flex: 0 0 0 calc(100% - 90px);
                    max-width: calc(100% - 90px);
                    text-align: left;
                    @include media-breakpoint-up(lg) {
                        flex: 0 0 0 calc(100% - 114px);
                        max-width: calc(100% - 114px);
                    }
                    .eyebrow {
                        color: $color-gray-400;
                        font-size: 14px;
                        font-weight: 400;
                        letter-spacing: 0.2px;
                        line-height: 20px;
                        text-transform: capitalize;

                        @include media-breakpoint-up(lg) {
                            font-size: 16px;
                            line-height: 24px;
                            @include non-latin($lang: ja, $line-height: 20px);
                            @include non-latin($lang: zh, $line-height: 20px);
                        }
                    }
                    p {
                        margin-bottom: 0;
                        font-weight: 600;
                    }
                    .video-name {
                        align-items: center;
                        color: $color-black;
                        display: flex;
                        font-size: 18px;
                        font-weight: 500;
                        letter-spacing: 0.2px;
                        line-height: 26px;
                        width: 200px;

                        @include media-breakpoint-up(lg) {
                            font-size: 20px;
                            line-height: 28px;
                            @include non-latin($lang: ja, $line-height: 30px);
                            @include non-latin($lang: zh, $line-height: 30px);
                        }
                    }
                }
            }
            &:hover {
                p {
                    text-decoration: underline;
                }
            }
        }

        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        @include media-breakpoint-up(lg) {
            right: 0;
            bottom: 0;
            left: auto;
            min-width: 470px;
            min-height: 120px;
            width: auto;
            border-radius: 10px 0 0 0;
        }
    }

    .container {
        .btn-secondary-blue {
            border-color: $color-blue-600 !important;
        }

        .owl-nav {
            display: none;
        }
        .darkPill {
            padding: 12px;
            background-color: rgba(64, 66, 65, 0.5);
            color: #ffffff;
            text-transform: uppercase;
            font-size: 12px;
            margin-bottom: 20px;
            display: inline-block;
            font-family: "Sharp Sans Semibold", helvetica, arial, sans-serif;
            letter-spacing: 1.5px;
            line-height: 10px;
            border-radius: 17.5px;
        }

        .lightPill {
            padding: 12px;
            background-color: rgba(255,255,255,0.75);
            font-family: "Sharp Sans Semibold", helvetica, arial, sans-serif;
            color: #404241;
            text-transform: uppercase;
            font-size: 12px;
            letter-spacing: 1.5px;
            margin-bottom: 20px;
            display: inline-block;
            line-height: 10px;
            border-radius: 17.5px;
        }
        .lightonWhitePill {
            padding: 12px;
            background-color: rgba(201, 201, 201, 0.75);
            color: #404241;
            text-transform: uppercase;
            font-family: "Sharp Sans Semibold", helvetica, arial, sans-serif;
            font-size: 12px;
            letter-spacing: 1.5px;
            line-height: 10px;
            margin-bottom: 20px;
            display: inline-block;
            border-radius: 17.5px;
            opacity: 0.75;
        }
    }
}
