.cmp-3-up-customization {

	&.bg-gray-50 {
		background-color: $color-gray-50;
	}

	.text-center {
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 72px;

		h2 {
			margin-bottom: 24px;
		}

		h6 {
			letter-spacing: 0.2px;
		}
	}

	.container {
		padding-top: 64px;

		@include media-breakpoint-up(lg) {
			padding-top: 96px;
		}
	}

	.col-lg-4 {
		padding-right: calc(8.33% + 12px);
		padding-bottom: 96px;
	}

    &.no-mb {
    	.col-lg-4 {
	        padding-bottom: 0;
	    }
    }

	h6 {
		text-transform: none;
	}

	h4 {
		margin-bottom: 20px;
		font-size:24px;
	}


	.icon-img {
		margin-bottom: 15px;
		height: 75px;
	}

	.percentage {
		margin-bottom: 30px;
		margin-top: 15px;

		display: block;
		width: 100%;
		height: 55px;

		color: $color-black;
		font-family: $font-headline;
		font-size: 72px;
		font-weight: bold;
		letter-spacing: -0.75px;
		line-height: 72px;
		@include non-latin-fonts();

        img {
            height: 55px;
			margin: 0 0 0 5px;
			padding: 0;
			vertical-align: top;
        }
	}

	p {
		color: $color-gray-400;
		font-family: $font-sans-serif;
		letter-spacing: 0.2px;
		@include non-latin-fonts();
		
		a{
			color: $color-blue-500;
		}
	}

	.body-text{
		p:last-child{
			margin-bottom: 36px;
		}
	}

	.btn {
		display: inline;
	}
}
