.cmp-form {

    .container {

        .mktoForm {
            visibility: visible !important;
        }

        #col-form {
            padding: 48px 24px;

            @include media-breakpoint-up(lg) {
                padding: 30px 0;
            }
        }

        .confirmation {
            display: none;
            padding: 48px 24px;
            text-align: center;
            h4 {
                margin-bottom: 2rem;
            }
            .desc {
                margin-bottom: 2rem;
            }

            @include media-breakpoint-up(lg) {
                padding: 30px 0;
            }
        }
    }

    .mktoForm .mktoLabel {
        color: $color-gray-400;
        font-size: 14px;
        font-weight: 400 !important;
        line-height: 20px !important;
        letter-spacing: 0.2px;
    }

    .mktoField {
        border: solid 1px $color-gray-200;
        background: $color-white;
        padding: 14px !important;
        border-radius: 3px;
    }

    .mktoLabel {
        width: 100% !important;
    }

    .mktoCheckboxList {
        width: 30px !important;
    }

    .mktoInvalid {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        border-bottom: solid 3px $color-red-300;
    }
    
    .mktoError {
        display: none !important;
    }

    .mktoCheckboxList{
        label{
            padding-left: 30px;
            margin-left: 0 !important;
            position: relative;
            input{
                opacity: 0;
                position: absolute;
            }
            input ~ .checkmark{
                content: '';
                width: 18px;
                height: 18px;
                border: 1px solid $color-gray-200;
                position: absolute;
                left: 0;
            }
            input:checked ~ .checkmark {
                background-image: get-icon('check', $color-green-500);
                border-color: $color-green-500;
                background-size: contain;
            }
        }
    }

    .mktoButtonRow{
        .mktoButtonWrap{
            margin-left: 0 !important;
        }
    }

    .btn{
        font-family: $font-sans-serif;
    }
}
