.cmp-casestudy-filter{

    .container{
        position: relative;
    }

    .container_result{
        background-color: $color-gray-50;
        // padding-top: 80px;
        // padding-bottom: 80px;
        padding-top: 36px;
        padding-bottom: 36px;
    }

    padding-top: 80px;

    &__eyebrow{
        font-family: $font-headline;
        font-size: 24px;
        font-weight: bold;
        line-height: normal;
        margin-bottom: 40px;
        @include non-latin-fonts();
    }
    
    &__sorting{
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 40px;
        text-align: right;

        @include media-breakpoint-up( xl ){
            flex: 0 0 325px;
            max-width: 325px;
        }
    }

    &__count{
        flex: 0 0 120px;
        max-width: 120px;
    }

    &__sort{
        width: auto;
        position: relative;
        text-align: left;
        &::before{
            content: '';
            width: 18px;
            height: 18px;
            background-image: url(../images/sort.svg);
            background-size: contain;
            background-position: left center;
            position: absolute;
            z-index: 9;
            left: 24px;
            top: 50%;
            transform: translateY(-50%);
        }

        .select2{
            width: 100% !important;
            .select2-container--default,
            .select2-selection--single{
                border: 0;
                padding: 0;
            }
        }

        .select2-selection{
            &__rendered{
                padding: 0 12px 0 50px !important;
                font-weight: 500;
                //text-transform: uppercase;
                color: $color-blue-500 !important;
            }
            &__arrow{
                display: none;
            }
        }

    }
    
    &__keyword{
        @include media-breakpoint-up( md ){
            position: absolute;
            right: 40px;
            top: 0;
        }

        @include media-breakpoint-up( xl ){
            right: 80px;
        }

        @include media-breakpoint-up( xxl ){
            top: 20px;
            right: 128px;
        }
        .mobile_loading__icon,
        .loading__icon{
            top: 65px;
            position: absolute;
            z-index: 9999;
            left: 50%;
            transform: translateX(-50%);
        }
    }
    .keyword-input{
        width: 260px;
        position: relative;

        @include media-breakpoint-down(sm){
            width: 100%;
            margin-bottom: 24px;
        }

        &::before{
            content: '';
            width: 18px;
            height: 18px;
            background-image: get-icon('search', $color-black);
            background-size: contain;
            background-position: left center;
            position: absolute;
            z-index: 9;
            left: 24px;
            top: 50%;
            transform: translateY(-50%);
        }
        input{
            width: 100%;
            padding: 8px 50px 8px 50px;
            border: 1px solid $color-gray-200;
            border-radius: 5px;
            font-size: 14px;
            color: $color-black;
        }
        .mobile-autocomplete-close-button,
        .autocomplete-close-button{
            width: 24px;
            height: 24px;
            background-repeat: no-repeat;
            background-image: get-icon('closex', $color-white);
            background-size: 24px 24px;
            background-position: center;
            //background-color: $color-gray-200;
            position: absolute;
            z-index: 9;
            //border-radius: 50%;
            right: 12px;
            top: 10px;
            //transform: translateY(-50%);
        }
    }
    
    ul{
        list-style-type: none;
        padding: 0;
        margin-bottom: 0;
        li{
            color: $color-gray-300;
            position: relative;
            text-transform: capitalize;
        }
        li input{
            position: absolute;
            left: 0;
            right: 0;
            opacity: 0;
        }
        li label{
            padding-left: 24px;
            position: relative;
            margin-bottom: 0;
            width: 100%;
        }
        li label::before{
            content: '';
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            border: 2px solid $color-black;
            border-radius: 2px;
            width: 14px;
            height: 14px;
        }
        li input:checked ~ label::before{
            display: none;
        }
        li input:checked ~ label::after{
            content: '';
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            background: url(../images/plus-white.svg);
            background-size: contain;
            background-color: $color-blue-500;
            border-radius: 2px;
            width: 14px;
            height: 14px;
        }
        &.-checkbox{
            li{
                margin-bottom: 10px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
            li label{
                padding-left: 30px;
            }
            li label::before{
                content: '';
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                border: 1px solid $color-black;
                border-radius: 50%;
                width: 18px;
                height: 18px;
            }
            li input:checked ~ label::before{
                display: block;
            }
            li input:checked ~ label::after{
                content: '';
                position: absolute;
                left: 4px;
                top: 50%;
                transform: translateY(-50%);
                background: $color-black;
                border-radius: 50%;
                width: 10px;
                height: 10px;
            }
        }
    }

    .filter-group{
        position: relative;
        padding-right: 20px;
        margin-right: 18px;
        display: flex;
        align-items: center;
        a{
            display: block;
            color: $color-black;
            overflow: hidden;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            text-overflow: ellipsis;
        }
        label{
            margin-bottom: 0;
        }
        &::before{
            content:'';
            position: absolute;
            right: 0;
            top: 10px;
            width: 12px;
            height: 12px;
            background-image: get-icon('arrow-down', $color-black);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: right center;
        }
        &.active{
            .filter-dropdown{
                height: auto;
                overflow: auto;
                padding: 24px 0;
                //transition-delay: .3s;
                animation: fadeIn .5s;
            }
            &::before{
                animation: spinTop .5s;
                background-image: get-icon('arrow-down', $color-blue-500);
                transition-delay: .3s;
                transform: rotate( 180deg );
            }
        }

        &.close{
            .filter-dropdown{
                animation: fadeOut .5s;
            }
            &::before{
                animation: spinDown .5s;
                background-image: get-icon('arrow-down', $color-black);
                transition-delay: .3s;
                transform: rotate( 0 );
            }
        }

        &__mobile{
            display: flex;
            flex-wrap: nowrap;
            padding-bottom: 12px;
            border-bottom: 1px solid $color-gray-200;
            margin-bottom: 40px;
            &-button{
                flex: 0 0 50%;
                max-width: 50%;
                padding-left: 24px;
                background-image: url(../images/filter-ist.svg);
                background-repeat: no-repeat;
                background-size: 18px;
                background-position: left center;
            }
            &-count{
                flex: 0 0 50%;
                max-width: 50%;
                text-align: right;
            }
        }
        
        
        &.filter-group-add-filter:before
        {
        	background-image: get-icon('plus', $color-black);
        	animation: none;
        }

    }

    .filter__buttons{
        display: flex;
        flex-wrap: nowrap;
        margin-top: 14px;
        button {
            flex: 0 0 50%;
            max-width: 50%;
            &:first-child{
                @include media-breakpoint-up( xl ){
                    border-right: 1px solid $color-blue-500;
                }
            }
        }
        button{
            font-size: 14px;
            border: 0;
            background: transparent;
            color: $color-blue-500;
            cursor: pointer;
            &:hover{
                opacity: .8;
            }
        }
        @include media-breakpoint-down( xl ){
            border: 1px solid $color-gray-200;
            padding: 24px 40px;
            position: fixed;
            background-color: $color-white;
            left: 0;
            right: 0;
            bottom: 0;
            max-width: 50%;
            * {
                flex: 0 0 calc(50% - 10px);
                max-width: calc(50% - 10px);
            }
            .btn:first-child{
                margin-right: 10px;
            }
            .btn:last-child{
                margin-left: 10px;
            }
        }
        @include media-breakpoint-down( sm ){
            max-width: 100%;
        }
    }

    .filter-dropdown{
        height: 0;
        overflow: hidden;
        top: 40px;
        transition: .3s ease-in-out;
        label{
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }
        li:last-child{
            padding-bottom: 0;
        }
    }

    .autocomplete-content{
        padding: 24px 0 14px;
        top: 60px;
        left: 0;
        li{
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }
        @include media-breakpoint-down(sm){
            left: 24px;
            top: 120px;
            width: auto !important;
            right: 24px;
        }
    }

    .autocomplete-content,
    .filter-dropdown{
        position: absolute;
        z-index: 99;
        background-color: $color-white;
        border-radius: 10px;
        box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.25);
        li{
            padding: 0 24px 10px;
        }
        ul:last-child{
            li a{
                color: $color-blue-500;
            }
        }
    }
    
    
	.autocomplete-content{
		width: 260px;
	}

    &__select{
        margin-bottom: 40px;
    }
    &__button{
        margin-bottom: calc(40px - 12px);
        font-weight: 500;
        color: $color-gray-300;
        display: flex;
        flex-wrap: nowrap;
        .title{
            flex: 0 0 165px;
            max-width: 165px;
            margin-right: 80px;
        }
        .filter-solution{
            margin-left: 12px;
            margin-bottom: 12px;
            font-size: 14px;
            font-weight: 500;
            line-height: normal;
            text-transform: uppercase;
            padding: 11px 25px;
            cursor: pointer;
            color: $color-gray-300;
            background-color: $color-white;
            border-radius: 99px;
            border: 2px solid $color-gray-300;
            &:hover,
            &.active{
                border-color: $color-green-600;
                background-color: $color-green-600;
                color: $color-white;
            }
        }
    }
    &__close{
        display: none;
        width: 18px;
        height: 18px;
        background-image: get-icon('close', $color-blue-500);
        background-size: contain;
        background-repeat: no-repeat;
    }
    .filter-showup{
        .cmp-casestudy-filter__close{
            display: block;
            position: absolute;
            top: 40px;
            right: 24px;
            width: 24px;
            height: 24px;
        }
    }
    &__mobile{
        .my-menu{
            .mg-flat {
                list-style: none;
                margin: 0;
                padding: 0;

                li a {
                    border: 0;
                    border-bottom: 1px solid $color-gray-200;
                    background: $color-white;
                    padding: 20px 0;
                    &.openItem{
                        background: $color-white;
                        border-color: $color-white;
                        color: $color-black;
                    }
                }
                li:last-child a{
                    border-bottom: 0;
                    &.openItem{
                        border-bottom: 1px solid $color-gray-200;
                    }
                }
                ul.submenu {
                    padding: 10px 0;
                    margin: 0;
                    list-style: none;
                    background: $color-gray-100;
                    li{
                        margin: 0;
                        a{
                            border-bottom: 0;
                            background: none;
                            padding: 10px 24px;
                            margin: 0;
                            &.menu-selected{
                                color: $color-white;
                                background-color: $color-green-500; 
                            }
                        }
                    }
                }
            }
            .fa-caret-down{
                color: $color-green-600;
            }
        }  
    }
 
    .mg-flat li.dropdown {
        margin-left: 0px; 
        span.toggler{
            transition: .3s ease;
            width: 18px;
            height: 18px;
            margin-top: 2px;
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        	background-image: get-icon('arrow-down', $color-black);  
        }
        .openItem {
            span.toggler{
                background-image: get-icon('arrow-down', $color-blue-500);  
            }   
        }

	}
	
	.btn-tertiary-green::after {
	   	width:0px;
	}  
	
	#desktop-reset-filter{
		display:none;
	}
	
    
    #filter-option-clear{
        border: 1px solid $color-blue-500 !important;
    }

	#mobile-reset-filter{
	   	margin-top:15px;
	   	display:none;
	}  

    
   	#loadmore-desktop{
    	margin-left:auto;
    	margin-right:auto;
    }
    #loadmore-mobile{
    	margin-left:auto;
    	margin-right:auto;
    }    
    
    .autocomplete-openned {
        .mobile-autocomplete-close-button,
        .autocomplete-close-button,
        .autocomplete-content {
            display: block !important;
        }
    }   
    #loadmore-mobile {
        margin-left: auto;
        margin-right: auto;
    }

    .webinar-result .btn { margin-top: 36px; }
}

.cmp-casestudy-filter.filter-selected #desktop-reset-filter, .cmp-casestudy-filter.filter-selected #mobile-reset-filter{
    display: block;
}


@include media-breakpoint-up(lg) {
    .cmp-casestudy-filter{
        padding-top: 120px;
        /*padding-bottom: 120px;*/
        &__eyebrow{
            font-size: 54px;
        }
    }
}

