.cmp-product-carousel-card {
    &__card {
        height: 100%;
        display: flex;
        flex-direction: column;
        width: 272px;
        margin: 0px auto;
        border-radius: 12px;
        overflow: hidden;
        position: relative;
        transition: all 0.5s ease;
        
        @include media_breakpoint_up(lg) {
            width: 432px;
        }

        &:hover {
            box-shadow: 0 0 12px rgba(0,0,0,0.5);
        }
    }

    &__image {
        display: block;
        width: 100%;
        height: 170px; 
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        @include media_breakpoint_up(lg) {
            height: 269px; 
        }
    }

    &__body {
        background-color: $color-gray-50;
        display: flex;
        flex-direction: column;
        flex: 1 0 0%;

        &-copy {
            p {
                margin-bottom: 0.5em;
            }
        }
        &-cta {
            margin-top: auto;

            a:before {
                position: absolute;
                content: ' ';
                top: 0;  right: 0; bottom: 0; left: 0;
            }
        }
    }
}