.cmp-featured-collateral {

    margin-bottom: 12px;
	margin-top: 12px;
	
    @include media-breakpoint-up(lg) {
        margin-bottom: 16px;
        margin-top: 16px;
    }

	&__wrapper {
		border-radius: 7px;
		overflow: hidden;
		height: 100%;
		position:relative;

		display: flex;
		flex-direction: column;
		background-color: $color-white;
	}

	&__image {
		display: block;
		width: 100%;
		padding-bottom: 75%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		position: relative;
		.btn{
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			margin-top: 0;
			border-color: $color-white;
			color: $color-white;
			padding: 12px;
			opacity: 0;
			z-index: 9;
		}
		.bg-overlay{
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background-color: rgba(0,0,0,.75);
			opacity: 0;
		}
		&:hover{
			.btn{
				opacity: 1;
			}
			.bg-overlay{
				opacity: 1;
			}
		}
	}
	
	&__info{
		padding: 24px 24px 80px;
		min-height: 185px;
		position: relative;
		.type{
			margin-bottom: 6px;
		}
		.btn-download{
			padding: 0;
			text-transform: uppercase;
			color: $color-green-500;
			font-weight: 500;
			position: absolute;
			bottom: 24px;
			left: 24px;
			padding-left: 30px;
			background-repeat: no-repeat;
			background-size: 24px 24px;
			background-position: center left;
			
		}
		
		.copyarea.h5
		{
			min-height: 60px;
		}
		.type
		{
			min-height: 25px;
		}
	}

	.btn{
		&-pdf{
			background-image: url(../images/icon-dl-pdf.svg);
		}
		&-video{
			background-image: url(../images/icon-dl-video.svg);
		}
		&-link{
			background-image: url(../images/icon-dl-link.svg);
		}
	}

	&__wrapper:hover {
		box-shadow: 0 0 20px rgba(0,0,0,.35);
		a {
			text-decoration: none;
		}
	}

	&__copyarea {
		font-size: 18px;
		font-weight: 500;
		line-height: 26px;
		letter-spacing: 0.2px;
		color: $color-black;
		text-align: center;
		width: 100%;
		padding: 20px 0;
	} 

}

.modal-collateral {
    position: fixed;
    top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	height: 100vh;
	z-index: 10001;
	overflow: scroll;
}

.modal-overlay {
    position: fixed;
    top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	height: 100vh;
    z-index: 10000;
    background: $color-black;
    opacity: 0.5;
}

.modal-collateral{
	.modal-dialog{
		width: 80%;
		left: 10%;
		margin-top: 15vh;
    	margin-bottom: 15%;
		padding: 40px 24px;
		border-radius: 10px;
		position: relative;
		z-index: 10002;
		background-color: $color-gray-50;

		@include media-breakpoint-up( lg ) {
			padding: 80px;
		}
		
		.close{
			cursor: pointer;
			position: absolute;
			border: 0;
			background: transparent;
			right: 12px;
			top: 12px;
			width: 18px;
			height: 18px;
			background-repeat: no-repeat;
			background-size: contain;
			background-image: get-icon('close', $color-black);
			@include media-breakpoint-up( lg ) {
				right: 24px;
				top: 24px;
			}
		}
	}
	.modal-body{
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 24px;
		@include media-breakpoint-up( md ) {
			margin-bottom: 40px;
		}
		.modal-left{
			flex: 0 0 100%;
			max-width: 100%;
			margin-bottom: 24px;
			.btn{
				margin-top: 24px;
				display: block;
				text-align: center;
				padding-left: 60px;
				position: relative;
				padding-top: 10px;
				padding-bottom: 10px;
				&::before{
					content: '';
					position: absolute;
					background-repeat: no-repeat;
					background-size: contain;
					width: 24px;
					height: 24px;
					top: 10px;
					left: calc(50% - 60px);
				}
				&-pdf{
					&::before{
						background-image: url(../images/icon-dl-pdf.svg);	
					}
				}
				&-video{
					&::before{
						background-image: url(../images/icon-dl-video.svg);	
					}
				}
				&-link{
					&::before{
						background-image: url(../images/icon-dl-link.svg);	
					}
				}
			}
			&__eyebrow{
				font-style: 18px;
				margin-bottom: 12px;
			}
			&__copyarea{
				margin-bottom: 24px;
			}
			@include media-breakpoint-up( md ) {
				padding-left: 40px;
				flex: 0 0 50%;
				max-width: 50%;
				margin-bottom: 0;
				.btn{
					display: inline-block;
					&::before{
						top: 8px;
						left: 24px;
					}
				}
			}
		}
		.modal-right{
			flex: 0 0 100%;
			max-width: 100%;
			.cmp-featured-collateral__image{
				border-radius: 10px;
			}
			@include media-breakpoint-up( md ) {
				padding-left: 40px;
				flex: 0 0 50%;
				max-width: 50%;
			}
		}
	}
	.modal-footer{
		h4{
			margin-bottom: 24px;
		}
	}
}

