.cmp-3-card-tabbed {
    background: $color-gray-100;
    padding: 36px 0 60px;
    margin-bottom: 60px;
    @include media-breakpoint-up(lg) {
        padding: 60px 0;
    }

    &.no-mb {
        margin-bottom: 0;
    }

    &__wrapper{
        margin-bottom:24px;
    }

    &__item{
        border-radius: 7px;
        overflow: hidden;
        background: $color-white;
        border-radius: 7px;
        display: flex;
        flex-direction: column;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        height: 100%;
        padding: 0 0 16px;

        &.permalink:hover {
            box-shadow: 0 0 12px 0 rgba(0,0,0,0.25);
            text-decoration: none !important;

            .btn-tertiary-green {
                border-bottom: 1px solid $color-green-600 !important;
            }
        }

        @include media-breakpoint-up(lg) {
            padding: 0 0 24px;
        }
       
        &-image {
            width: 100%;
            height: 0;
            padding-bottom: 54%;
            height: auto;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
        }
        &-title{
            padding: 16px 16px 6px;

            @include media-breakpoint-up(lg) {
                padding: 24px;
            }
        }
        &-copy{
            flex: 1 0 0%;
            padding: 0 16px;

            @include media-breakpoint-up(lg) {
                padding: 0 24px;
            }
        }
        &-cta{
            padding: 24px 16px 0;

            @include media-breakpoint-up(lg) {
                padding: 48px 24px 0;
             }
        }
    }
    
    &__tabs {
        margin: 0 -24px 24px;

        ul {
            list-style: none;
            margin: 0;
            overflow-x: auto;
            padding: 0 0 0 12px;
            white-space: nowrap;
        }

        li {
            display: inline-block;
            margin: 12px;
            text-transform: uppercase;
        }

        @include media-breakpoint-up(lg) {
            margin: 12px 0 24px;

            ul {
                padding: 0;

                li {
                    margin: 12px 40px 12px 0;

                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
    }

    &__tab {
        
        a{
         text-decoration:none;   
        }

        &.active {
            border-bottom:solid 2px;
            a {
                font-weight: bold;
            }
        }
    }

    &__tab-container {
        display:none;

        &.active {
            display:flex;
        }
    }

    .cmp-3-card-tabbed__cta{
        padding-top: 12px;

        @include media-breakpoint-up(lg) {
            padding-top: 24px;
        }
    }
}