// grayscale palette
$color-black: #393939;
$color-white: #fff;
$color-true-black: #000;

$color-gray-50: #F8F8F8;
$color-gray-100: #F2F2F2;
$color-gray-200: #C9C9C9;
$color-gray-300: #898B8E;
$color-gray-400: #636363;
$color-gray-500: #393939; // chnaged from #404241
$color-gray-600: #1A1A1A;

$color-blue-50: #EBF7FA;
$color-blue-100: #DCE3F9;
$color-blue-200: #B9C7EF;
$color-blue-300: #9FB7FA;
$color-blue-400: #7C91CA;
$color-blue-500: #5576D1;
$color-blue-600: #3E579D;
$color-blue-700: #1D2847;
$color-blue-800: #25408A;

$color-green-100: #E6F5DE;
$color-green-200: #C6E9B2;
$color-green-300: #A4DE83;
$color-green-400: #9DCB89;
$color-green-500: #67B346; // meraki green
$color-green-600: #4D8433;
$color-green-700: #2C4C1E;

$color-red-100: #EDCFCF;
$color-red-200: #E38686;
$color-red-300: #D85656;
$color-red-400: #C24D4D;
$color-red-500: #883636;

$color-yellow-100: #FFEA96;
$color-yellow-200: #ECD782;
$color-yellow-300: #FFCD00;
$color-yellow-400: #FFB600;
$color-yellow-500: #D78900;


// color map
$theme-color-map: (
	black: $color-black,
	white: $color-white,

	gray-50: $color-gray-50,
	gray-100: $color-gray-100,
	gray-200: $color-gray-200,
	gray-300: $color-gray-300,
	gray-400: $color-gray-400,
	gray-500: $color-gray-500,
	gray-600: $color-gray-600,

	blue-100: $color-blue-100,
	blue-200: $color-blue-200,
	blue-300: $color-blue-300,
	blue-400: $color-blue-400,
	blue-500: $color-blue-500,
	blue-600: $color-blue-600,
	blue-700: $color-blue-700,

	green-100: $color-green-100,
	green-200: $color-green-200,
	green-300: $color-green-300,
	green-400: $color-green-400,
	green-500: $color-green-500,
	green-600: $color-green-600,
	green-700: $color-green-700,

	red-100: $color-red-100,
	red-200: $color-red-200,
	red-300: $color-red-300,
	red-400: $color-red-400,
	red-500: $color-red-500,

	yellow-100: $color-yellow-100,
	yellow-200: $color-yellow-200,
	yellow-300: $color-yellow-300,
	yellow-400: $color-yellow-400,
	yellow-500: $color-yellow-500,
);

@each $name, $color in $theme-color-map {
	.text-#{$name} {
		color: $color;
	}

	.bg-#{$name} {
		background-color: $color;
	}
}
