.cmp-5050 { 
    margin: 60px 0;

    &.no-mb {
        margin-bottom: 0;
    }

    &.no-mt {
        margin-top: 0;
    }

    @include media-breakpoint-up(lg) {
        margin: 120px 0;
    }

    &.bg-color {
        margin-top: 0;
        padding: 60px 0;

        @include media-breakpoint-up(lg) {
            margin-top: 0;
            padding: 120px 0;
        }
    }

    & .reverse {
        flex-direction: column-reverse;
    
        @include media-breakpoint-up(lg) {
            flex-direction: row-reverse; 
        }
    }

    & .normal {
        flex-direction: column-reverse;
        @include media-breakpoint-up(lg) {
            flex-direction: row; 
        }
    }

    &__content {
        &-row {
            align-items: flex-start;
        }
    }

    &__image {
        max-width: 100%;
        border-radius: 10px;

        .has-shadow & {
            box-shadow: 0 0 20px rgba(0,0,0,0.35);
        }

        &-wrapper {
            margin-bottom: 46px;
            @include media-breakpoint-up(lg) {
                margin-bottom: 0px;
            }
        }
    } 

    &__eyebrow { 
        font-size: 14px;
        line-height: 20px;
    }

    &__title {
        margin-bottom: 24px;
        _:-ms-lang(x), & { margin-top: 24px; }
    }

    &__image-box{
        display: block;
        position: relative;

        &:hover {
            .cmp-5050__icon-play {
                background-image: get-icon( 'play', $color-gray-100 );
            }
        }
    }

    &__icon-play{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 80px;
        height: 80px;
        background-image: get-icon( 'play', $color-white );
        background-size: contain;
    }

    &__subtitle {
        color: $color-black;
        letter-spacing: 0.011em;
        text-transform: none;
        font-weight: normal;
        .separator {
            display: inline-block;
            content: "*";
            color: $color-black;
        }   
    }

    &__body {
        * {
            color: $color-black;
            text-transform: none;
        }

        h1,h2,h3,h4,h5,h6 {
            margin-bottom: 24px;
        }

        //normalize the h6 to match the subtitle
        h6 {
            letter-spacing: 0.011em;
            text-transform: none;
            font-weight: normal;
        }

        ul {
            list-style: none;
        }

        li {
            margin-bottom: 24px;
            position: relative;
            &:before {
                position: absolute;
                display: block;
                content: '';
                width:30px;
                height: 30px;
                background-image: url(../images/icon-check-mark.svg);
                background-repeat: no-repeat;
                margin-left: -26px;
                left: -14px;
                // margin-right: 5px;
            }
        }

        p {
            color: $color-gray-400;
            a{
                color:$color-blue-500;
            }
        }

        *:last-child {
            margin-bottom: 0;
        }
    }

    &.font-white{
        color: $color-white;

        .cmp-5050__subtitle {
            color: $color-white;
        }

        .cmp-5050__body {
            *, p {
                color: $color-white;
            }
        }
    }

    &.reverse-stack-order{
        @include media-breakpoint-down(md){
            .cmp-5050{
                &__content-wrapper{
                    order: 1;
                    margin-bottom: 40px;
                }
                &__image-wrapper{
                    margin-bottom: 0;
                }
            }
        }
    }
}