
.cmp-text-icons-side {
   
  .image-grid {
    .col-12{

    }
    .mb-5 {
      margin-bottom: 24px!important;
      img {
        width: 204px;
        height: 110px;
      }
    }
    > :nth-last-child(-n+2) {
      margin-bottom: 0 !important;
    }

    img {
      max-width: 100%;
    }
  }
  &.py-lg-11 {
    
    @include media-breakpoint-up(lg) {
    padding-top: 130px!important;
    padding-bottom: 130px!important;
    }
  }
}


@include media-breakpoint-up(lg) {
  .cmp-text-icons-side {
    .col-lg-4{
      max-width: calc(204px + 24px);
      max-width: calc(204px + 24px);
    }
    .col-lg-7 {
      flex: 0 0 685px;
      max-width: 685px;
    }
    .col-lg-5{
      flex: 0 0 calc(100% - 685px);
      max-width: calc(100% - 685px);
    }
    h2 {
      font-size: 54px;
      letter-spacing: 0;
      line-height: 58px;
    }

    .image-grid {
      > :nth-last-child(-n+3) {
        margin-bottom: 0 !important;
      }
    }
  }
}

@include media-breakpoint-down(xl) {
  .cmp-text-icons-side {
    .row{
      .col-lg-7,
      .col-lg-5 {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }
}