.cmp-notification-bar {
	text-align:center;
	.container{
		padding-top:10px;
		padding-bottom:10px;

		font-size: 18px;
		line-height: 26px;
		text-align: center;
		letter-spacing: 0.2px;
		@include media-breakpoint-up(lg) {
			padding-top:27px;
			padding-bottom:27px;
		}
	}
    &.bg-gray-400{
    	color:$color-white;
    	a{
            color:$color-white;
            text-decoration:underline;
        }
    }

    &.bg-blue-400, &.bg-red-400{
        a{
            color:inherit;
            text-decoration:underline;
        }
    }

    a{
        color:$color-blue-500;
    }

    &__cta a{
        color:inherit;
    	text-decoration:underline;

        &:hover {
            text-decoration: none;
        }
    }

    &__break{
    	@include media-breakpoint-up(lg) {
    		display:none;
    	}
    }
    
}