.cmp-asset-text-card {
    margin-bottom:48px;
    @include media_breakpoint_up(lg) {
        margin-bottom:60px;
    }
    
    &.no-mb {
        margin-bottom: 0;
    }

    &__card {
        position: relative;
        padding: 205px 14px 24px;
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        overflow: hidden;

        @include media_breakpoint_up(lg) {
            padding: 30px 30px 30px;
            height: 540px;
        
        }

        &.has-cta:hover {
            // opacity: 0.7;

            .cmp-asset-text-card__card-background {

                @include media_breakpoint_up(lg) {
                transform: scale(1.025);
                }
            }

            .cmp-asset-text-card__text-card{
                @include media_breakpoint_up(lg) {
                    background-color: $color-gray-100;
                }
            }
        }

        &-background {
            
            position: absolute;
            top: 0; left: 0; right: 0; bottom:0;
            transition: all 0.5s ease-in-out;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            z-index:0;
        }
    }
    
    
    &__text-card {
        z-index:10;
        background-color: white;
        border-radius: 10px;
        width: 100%;

        padding: 24px;
        

        @include media_breakpoint_up(lg) {
            // min-height: 210px;
            width: 370px;
            padding:30px;
        }

        &--eyebrow {
            color: $color-gray-400;
            font-size: 14px;
            letter-spacing: 0.2px;
            line-height: 20px;
            margin-bottom: 6px;
            text-transform: none;

            @include media_breakpoint_up(lg) {
                font-size: 16px;
                line-height: 24px;
                margin-bottom: 12px;
                @include non-latin($lang: ja, $line-height: 20px);
                @include non-latin($lang: zh, $line-height: 20px);
            }
        }

        &--headline {
            // margin-bottom:12px;
        }

        &--bodycopy {
            margin-top:12px;

            p:last-child {
                margin-bottom:0;
            }
            
        }
        &--cta {
            margin-top: 18px;

            @include media_breakpoint_up(lg) {
                margin-top: 28px;
            }

            &:hover {
                border-bottom-color: transparent !important;
            }

            &:before {
                display: block;
                position: absolute;
                top: 0; left: 0; right: 0; bottom: 0;
                content: '';
                
            }

        }
    }
}