.page-search-results {
    .search-section {
        margin: 24px 0;
        @include media-breakpoint-up(lg) {
            margin: 60px 0;
        }
        form,
        input[type="search"] {
            width: 100%;
        }
        form {
            position: relative;
            &::before {
                content: "";
                position: absolute;
                left: 0;
                top: 10px;
                width: 18px;
                height: 18px;
                background-image: get-icon("search", $color-black);
                background-repeat: no-repeat;
                background-size: contain;

                @include media-breakpoint-up(lg) {
                    top: 22px;
                    width: 24px;
                    height: 24px;
                }
            }
        }
        input[type="submit"] {
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            opacity: 0;
        }

        input[type="search"] {
            border: 0;
            padding-bottom: 12px;
            padding-left: 40px;
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;

            @include media-breakpoint-up(md) {
                padding-left: 60px;
                padding-bottom: 24px;
            }

            @include media-breakpoint-up(lg) {
                font-size: 54px;
                line-height: 58px;
            }

            &::placeholder {
                color: $color-gray-200;
            }

            &:focus,
            &:active {
                border-bottom: 2px solid $color-gray-200;
            }

            &::-webkit-search-decoration,
            &::-webkit-search-cancel-button,
            &::-webkit-search-results-button,
            &::-webkit-search-results-decoration {
                display: none;
            }
        }
        .btn-clear {
            width: 18px;
            height: 18px;
            background-image: get-icon("close", $color-black);
            background-repeat: no-repeat;
            background-size: contain;
            position: absolute;
            right: 0;
            top: 10px;
            display: none;
            border: 0;
            background-color: $color-white;

            @include media-breakpoint-up(lg) {
                top: 22px;
                width: 24px;
                height: 24px;
            }
        }
    }

    .result-row {
        align-items: center;
        margin-bottom: 24px;
    }

    .result-group {
        text-align: left;

        a {
            // display: inline-block;
            // padding: 10px 24px;
            // border-radius: 6px;
            // border: 1px solid #5576d1;
            // color: #5576d1;
            // margin-right: 10px;
            // font-size: 20px;
            @include media-breakpoint-down(sm) {
                padding: 5px 15px;
                font-size: 14px;
            }
            &.active {
                color: $color-white;
                background-color: $color-blue-500;
            }
            &:hover {
                text-decoration: none;
                color: $color-white;
                background-color: #5576d1;
            }
        }

        @include media-breakpoint-down(xl) {
            flex: 0 0 100%;
            max-width: 100%;
            margin-bottom: 24px;
        }
    }

    .result-count {
        text-align: right;

        @include media-breakpoint-down(xl) {
            flex: 0 0 100%;
            max-width: 100%;
            text-align: left;
        }
    }

    .section-result {
        // background-color: $color-gray-50;
        // padding-top: 60px;
        // padding-bottom: 60px;

        .result-href {
            display: block;
            margin-bottom: 24px;
            margin-bottom: 40px;
            // &:last-child{
            //     margin-bottom: 0;
            // }
            &:hover {
                text-decoration: none;
            }
            .link {
                color: $color-blue-500;
                word-break: break-all;
            }
            .row {
                background-color: #F8F8F8;
                padding-top: 10px;
                padding-bottom: 10px;
            }
        }

        .text {
            order: 2;
            .search-hdg {
                font-size: 20px;
                font-weight: 500;
                height: 52px;
                line-height: 26px;
                margin-bottom: 24px;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            .search-hdg-new {
                height: auto !important;
            }
        }

        .image {
            order: 1;
            width: 70%;
            @include media-breakpoint-down(sm) {
                width: 100%;
            }
            &__bg {
                padding-bottom: 100%;
                width: 100%;
                background-repeat: no-repeat;
                background-size: cover;
                @include media-breakpoint-down(sm) {
                    margin-bottom: 20px;
                }
            }
        }
    }

    .search-order {
        a {
            display: inline-block;
            padding: 10px 24px;
            border-radius: 6px;
            border: 1px solid #5576d1;
            color: #5576d1;
            margin-right: 10px;
            margin-bottom: 15px;
            font-size: 20px;
        }
    }
    @include media-breakpoint-down(sm) {
        width: 90%;
        margin: 0 auto;
    }
    // a{
    //     white-space: nowrap;
    // }
    &::-webkit-scrollbar {
        display: none;
    }

    @include media-breakpoint-down(sm) {
        .select-wrap {
            width: 100vw;
            overflow: hidden;
            margin: 0 calc(50% - 50vw);
            // height: 36px;
        }

        .search-order {
            // display: flex;
            // flex-wrap: nowrap;
            // overflow: hidden;
            // overflow-x: scroll;
            // padding: 0 24px;
            @include media-breakpoint-down(sm) {
                width: 90%;
                margin: 0 auto;
            }
            // a{
            //     white-space: nowrap;
            // }
            &::-webkit-scrollbar {
                display: none;
            }
        }
        #loadmore-search-button {
            width: 100%;
            padding-top: 8px;
            padding-bottom: 8px;
        }
    }
    @media (max-width: 576px) {
        .search-order {
            a {
                width: 100%;
                text-align: center;
                margin-bottom: 15px;
            }
        }
    }
    @media (min-width: 320px) and (max-width: 767.98px) {
        .search-section .container {
            padding: 0px;
        }
        .result-container {
            padding: 0;
        }
    }
}
