.cmp-job-departments {
    background-color: white;

    &.no-mb {
        margin-bottom: 0 !important;
    }
    
    &__card {
        background-color: $color-gray-50;
        border-radius: 10px;
        height: 310px;
        padding: 30px;
        position: relative;
        transition: 0.5s all ease;

        &:hover {
            box-shadow: 0 0 12px 0 rgba(0,0,0,0.35);
        }

        &-icon {
            width: 60px;
        }

        &-title {
            margin: 20px 0 10px;
        }

        &-body {
            font-size: 20px;
        }

        &-cta {
            margin-top: auto;

            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
            }
        }
    }
}