.cmp-cta-banner {
	padding: 36px 0;
	text-align: center;

	@include media-breakpoint-up(lg) {
		padding: 60px 0;
	}

	&.img-flush {
		padding-top: 0;	}

	& img {
		padding: 0 0 24px;
		width: 100%;

		@include media-breakpoint-up(lg) {
			padding: 0 0 60px;
		}
	}

	&__headline {
		margin-bottom: 24px;
	}

	&__subheadline {
		font-size: 20px;
		line-height: 28px;
		margin-bottom: 24px;
        @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 30px);
        @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 30px);

		@include media-breakpoint-up(lg) {
			margin-bottom: 60px;
		}
	}

	&__btn{
		margin: 0 0 24px;
		@include media-breakpoint-up(lg) {
			display: inline-block;
			margin: 0 12px;
		}
		&:last-child{
			margin-bottom: 0;
		}
	}

	&.bg-green-500, 
	&.bg-blue-500{
        p a{
        	color:inherit;
        	text-decoration:underline;
        }
    }
}