.woocommerce-Tabs-panel--specs {

	ul {
		list-style: none;
		padding: 0;
	}

	ul.column > li {
		margin: 0 0 2em 0;
		border-top: 3px solid $color-black;
		padding: 3px 0 0;
		font-weight: 600;
		font-size: 14px;
	}

	ul li ul {
		margin: 1em 0 0;
		font-weight: 400;
		font-size: 12px;
		list-style: none;
	}

	ul li ul li {
		margin: 0 0 8px 0;
		border-top: 1px solid $color-black;
		padding: 2px 0 0 0;
	}

	ul li ul li ul {
		list-style: square;
		margin: 5px 0 0 14px;
	}

	ul li ul li ul li {
		border: none;
		margin: 0;
	}

	.regulatory {
		float: right;
	}
}

