.cmp-header-overlay {
    background-color: $color-black;
    opacity: 0.6;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 998;
    display: none;
}

.cmp-header {
    background-color: $color-white;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);

    /* maybe adds IE11 fix */
    &.sticky-top {
        position: relative;
        position: sticky;
        z-index: 1200;
    }

    &.compact {
        .header-util {
            height: 0 !important;
            visibility: hidden;
            padding-top: 0 !important;
            padding-bottom: 0 !important;
        }
        .meraki-desktop-nav {
            padding-top: 18px !important;
            padding-bottom: 18px !important;
        }
    }

    &.menu-open {
        .cmp-header__container {
            box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.12);
            position: relative;
            z-index: 1001;
        }
    }

    @include media-breakpoint-up(xl) {
        .px-md-4 {
            padding-right: 38px !important;
        }
    }
    @include media-breakpoint-up(xxl) {
        .px-md-4 {
            padding-right: 48px !important;
        }
    }

    .search-icon {
        cursor: pointer;
        display: block;
        width: 18px;
        height: 18px;
        background-repeat: no-repeat;
        background-size: contain;
    }

    .chevron-icon {
        width: 18px;
        height: 18px;
        background-repeat: no-repeat;
        background-size: contain;
    }

    .close-icon {
        width: 18px;
        height: 18px;
        background-image: get-icon("close", $color-gray-300);
        background-repeat: no-repeat;
        background-size: contain;
    }

    .close-icon:hover {
        background-image: get-icon("close", $color-green-500);
    }

    .nav-icon {
        margin-right: -15px;
        padding: 15px;

        span {
            border-top: 2px solid $color-gray-400;
            display: block;
            height: 14px;
            position: relative;
            width: 15px;

            @include media-breakpoint-up(md) {
                width: 20px;
            }

            &:after,
            &:before {
                background-color: $color-gray-400;
                content: "";
                display: block;
                height: 2px;
                top: 10px;
                position: absolute;
                width: 100%;
            }
            &:before {
                top: 4px;
            }
        }

        &:hover {
            span {
                border-color: $color-green-500;

                &:after,
                &:before {
                    background-color: $color-green-500;
                }
            }
        }
    }

    .globe-icon {
        width: 14px;
        height: 14px;
        background-repeat: no-repeat;
        background-size: contain;
    }

    .search-result-loading__icon {
        text-align: center;
    }

    #header-mega-menu-search {
        height: 100%;
        position: fixed;
        top: 0;
        width: 100%;
    }
    #mobile-search-popup {
        background: $color-white;
        display: none;
        position: fixed;
        top: 61px;
        width: 100%;

        @include media-breakpoint-up(md) {
            top: 0;
        }
    }

    .search-showup {
        display: block !important;
    }

    .search-close {
        display: block;
        width: 18px;
        height: 18px;
        background-image: get-icon("close", $color-blue-500);
        background-size: contain;
        background-repeat: no-repeat;
    }

    .header-util {
        > :not(:last-child) {
            margin-right: map-get($spacers, 2);
        }

        a {
            color: $color-gray-400;
            font-size: 14px;
            font-weight: 400;
            text-decoration: none;

            &:hover {
                color: $color-green-500;
            }
        }
    }

    .meraki-desktop-nav {
        z-index: 3;
        position: relative;

        .search-demo-ctas {
            padding-left: 0;
            @include media-breakpoint-up(lg) {
                flex: 0 0 20%;
                max-width: 20%;
            }
            @include media-breakpoint-up(xxl) {
                flex: 0 0 15%;
                max-width: 15%;
            }
        }

        .btn-demo {
            font-size: 14px;
            letter-spacing: 1px;
            line-height: 18px;
            padding: 7px;

            html[lang|="en"] & {
                letter-spacing: 1.2px;
                padding: 7px 15px;
            }
        }

        > .logo {
            .custom-logo-link {
                img {
                    width: 164px;
                    height: auto;
                }
            }
            @include media-breakpoint-up(lg) {
                flex: 0 0 15%;
                max-width: 15%;
            }
            @include media-breakpoint-up(xxl) {
                flex: 0 0 30%;
                max-width: 30%;
            }
        }

        > .sections {
            flex-wrap: nowrap;
            justify-content: flex-end;

            @include media-breakpoint-up(lg) {
                flex: 65%;
                max-width: 65%;
            }
            @include media-breakpoint-up(xxl) {
                flex: 55%;
                max-width: 55%;
            }
        }
    }

    .section {
        &__header {
            cursor: pointer;
            flex-direction: row;
            justify-content: space-between;

            > a {
                background: none;
                border: none;
                color: $color-gray-500;
                font-size: 14px;
                font-weight: 500;
                margin: 0;

                @include media-breakpoint-up(md) {
                    font-size: 16px;
                }
            }

            &:hover {
                > a {
                    color: $color-green-500 !important;
                    text-decoration: none !important;
                }

                .chevron-icon {
                    background-image: get-icon("arrow-down", $color-green-500);
                }

                .search-icon {
                    background-image: get-icon("search", $color-green-500);
                }
            }

            .chevron-icon {
                width: 10px;
                height: 6px;
                transition: 0.3s ease;
                background-image: get-icon("arrow-down", $color-gray-500);
            }

            .search-icon {
                background-image: get-icon("search", $color-gray-500);
            }
        }

        &.active {
            .section__header {
                > a {
                    color: $color-green-500;
                }

                .chevron-icon {
                    background-image: get-icon("arrow-down", $color-green-500);
                    transform: rotate(180deg);
                }

                .search-icon {
                    background-image: get-icon("search", $color-green-500);
                }
            }

            .mega-menu {
                display: block;
            }
        }
    }

    .meraki-search {
        width: 100%;

        .input-group {
            width: 890px;
            border-bottom: 1px solid $color-gray-200;
        }

        .form-inline {
            width: 100%;

            > :first-child {
                flex: 1;
            }
        }

        input {
            color: $color-gray-500;
            border: none;
            background-color: transparent;
            font-size: 20px;
            font-weight: 400;

            &:focus {
                outline: none;
                box-shadow: none;
            }
        }
        ::placeholder {
            color: $color-gray-500 !important;
            opacity: 0.9;
        }

        button {
            border: none;
            background-color: transparent;
            cursor: pointer;

            > .close {
                width: 18px;
                height: 18px;
                background-image: get-icon("close", $color-gray-300);
                background-repeat: no-repeat;
                background-size: contain;
            }
        }

        #quicklinks {
            width: 100%;
        }
    }

    .mega-menu {
        @include media-breakpoint-up(lg) {
            background-color: $color-white;
            display: none;
            z-index: -1;
        }

        &.active {
            z-index: 1000;
        }

        .close-mega-menu {
            cursor: pointer;
            padding: 30px 0 0 30px;
            position: absolute;
            right: 0;
            top: 0;

            .icon {
                display: block;
                width: 12px;
                height: 12px;
                background-image: get-icon("close", $color-black);
                background-repeat: no-repeat;
                background-size: contain;
            }

            &:hover {
                .icon {
                    background-image: get-icon("close", $color-green-500);
                }
            }
        }

        .wrapper {
            background-color: $color-white;
            position: relative;
            text-align: left;

            @include media-breakpoint-down(sm) {
                padding-bottom: 36px !important;
            }

            @include media-breakpoint-down(md) {
                padding-bottom: 40px;
                border-bottom: 1px solid $color-gray-100;
            }

            .mega-menu__menubar {
                padding: 20px 0 60px;
            }

            &.has-promotion {
                display: flex;

                .mega-menu__menubar {
                    flex: 0 0 75%;
                    max-width: 75%;
                    padding-right: 60px;

                    @include media-breakpoint-up(xxxl) {
                        flex: 0 0 80%;
                        max-width: 80%;
                    }
                }

                .mega-menu__promotions {
                    border-left: 1px solid $color-gray-100;
                    flex: 0 0 25%;
                    max-width: 25%;
                    padding: 80px 0 60px 40px;
                    position: relative;

                    @include media-breakpoint-up(xxxl) {
                        flex: 0 0 20%;
                        max-width: 20%;
                    }

                    .promotion-close {
                        width: 18px;
                        height: 18px;
                        background-image: get-icon("close", $color-gray-300);
                        background-size: contain;
                        background-repeat: no-repeat;
                        display: block;
                        position: absolute;
                        right: 0;
                        top: 24px;
                    }

                    ul {
                        list-style: none;
                        margin: 36px 0 0;
                        padding: 0;

                        li {
                            margin: 0 0 40px;
                        }

                        a {
                            text-decoration: none;

                            &:hover {
                                color: $color-green-500;
                            }
                        }

                        .name {
                            font-weight: 500;
                        }

                        .headline {
                            font-size: 14px;
                            line-height: 1.5;
                        }
                    }
                }
            }

            .top {
                display: flex;
                flex-direction: column;

                > :not(:last-child) {
                    margin-right: map-get($spacers, 1);
                }

                @include media-breakpoint-only(md) {
                    flex-direction: row;

                    .item {
                        flex: 0 0 50%;
                        max-width: 50%;
                    }
                }

                .category {
                    @include media-breakpoint-up(lg) {
                        flex: 1;
                    }

                    p.header {
                        border-bottom: 1px solid $color-gray-100;
                        font-size: 14px;
                        font-weight: 500;
                        letter-spacing: 0.1em;
                        text-transform: uppercase;

                        @include media-breakpoint-down(md) {
                            margin-top: 30px !important;
                            margin-bottom: 0 !important;

                            &.header--empty {
                                display: none;
                            }
                        }
                    }

                    .options {
                        flex-direction: column;
                        .item img {
                            width: 100%;
                        }
                    }
                }

                > :only-child {
                    .options {
                        flex-direction: row;
                        flex-wrap: wrap;
                        > a {
                            flex: 1 1 33.333%;

                            .icon {
                                width: 36px;
                                margin-right: 16px !important;
                                position: relative;
                                float: left;
                                @include media-breakpoint-down(md) {
                                    margin-top: 6px;
                                }
                                @include media-breakpoint-up(lg) {
                                    width: 34px;
                                }
                            }

                            &:hover {
                                .icon-hover {
                                    opacity: 1;
                                }

                                .title {
                                    color: $color-green-500;
                                }
                            }
                        }
                    }
                }

                .item {
                    text-decoration: none;
                    padding: 30px 0 0;

                    @include media-breakpoint-up(lg) {
                        padding: 30px 0 0 0;
                    }

                    // @include media-breakpoint-up(lg) {
                    //     :nth-child(1) {
                    //         padding-top: 40px;
                    //     }
                    // }

                    p {
                        margin: 0;
                        letter-spacing: 0.2px;
                    }

                    p.title {
                        color: $color-black;
                        font-size: 14px;
                        font-weight: 500;

                        @include media-breakpoint-up(md) {
                            font-size: 16px;
                        }
                    }

                    p.headline {
                        font-size: 12px;
                        line-height: 1.5;
                        margin-top: 2px;

                        @include media-breakpoint-up(md) {
                            font-size: 14px;
                        }
                    }

                    .icon {
                        float: left;
                        margin-right: 20px !important;
                        position: relative;
                        width: 36px;

                        @include media-breakpoint-down(md) {
                            margin-top: 6px;
                        }
                        @include media-breakpoint-up(lg) {
                            width: 34px;
                        }
                    }

                    .icon-hover {
                        left: 0;
                        opacity: 0;
                        position: absolute;
                        top: 0;
                    }

                    .content {
                        float: left;
                        margin-left: 0;
                        width: calc(100% - 60px);
                    }

                    &.ety-hd {
                        .content {
                            margin-top: 6px;
                        }
                    }

                    &:hover {
                        .icon-hover {
                            opacity: 1;
                        }

                        .title {
                            color: $color-green-500;
                        }
                    }

                    &:active {
                        background-color: transparent;
                        p.title {
                            color: $color-green-500;
                        }
                    }
                }
            }

            .landing {
                // padding: 40px 0 0 0;

                @include media-breakpoint-up(lg) {
                    margin-top: 0;
                }

                > a {
                    background-image: get-icon("arrow-right2", $color-green-500);
                    background-position: center right;
                    background-size: 16px 18px;
                    background-repeat: no-repeat;
                    color: $color-green-500;
                    font-size: 14px;
                    font-weight: 500;
                    padding-right: 30px;
                    text-decoration: none;

                    @include media-breakpoint-up(md) {
                        font-size: 16px;
                    }

                    &:hover {
                        background-image: get-icon("arrow-right2", $color-green-600);
                        color: $color-green-600;
                    }
                }
            }
        }

        &.active {
            display: block;
        }
    }

    .mobile-menu {
        .wrapper {
            display: none;
        }

        &.active {
            .wrapper {
                display: block;
            }
            .section__header {
                background-color: $color-green-100;
                border-color: $color-green-100 !important;
                a {
                    color: $color-black !important;
                }
            }
        }
    }

    .featured-promotion {
        color: $color-black;

        .image {
            background-size: cover;
            border-radius: 6px 6px 0 0;
            padding-bottom: 54%;
            width: 100%;
        }

        .text-area {
            background: $color-gray-50;
            border-radius: 0 0 6px 6px;
            padding: 16px 20px 12px;
        }

        .name {
            font-size: 14px;
            line-height: 1.4;
            font-weight: 500;
            margin-bottom: 12px;
        }

        .headline {
            font-size: 12px;
            line-height: 1.4;
            margin-bottom: 12px;
        }

        .btn-tertiary-green {
            font-size: 12px;

            &:after {
                height: 10px;
                margin-left: 3px;
                vertical-align: 0;
                width: 11px;
            }
        }
    }

    .header-util-mobile {
        background: $color-gray-100;
        padding: 24px 0 2px;

        ul {
            list-style: none;
            margin: 0;
            padding: 0;

            li {
                margin: 0 0 24px;
            }

            a {
                color: $color-gray-400;
                font-size: 14px;
                font-weight: 400;
                text-decoration: none;

                &:hover {
                    color: $color-green-500;
                }
            }
        }

        .featured-promotion {
            margin-bottom: 24px;

            .text-area {
                background: $color-white;
            }
        }
    }

    .meraki-mobile-nav {
        position: fixed;
        height: 100%;
        width: 100%;
        z-index: 1000;
        top: 0;
        left: 100%;
        background-color: $color-white;
        overflow-x: hidden;
        transition: opacity 2s ease-in;
        opacity: 1;

        > .nav-content {
            position: relative;
            width: 100%;
            text-align: center;
            background-color: $color-white;

            display: flex;
            flex-direction: column;

            > .nav-header {
                height: 61px;
                width: 100%;
                border-bottom: 1px solid $color-gray-100;
                background-color: $color-white;

                > .close-icon {
                    background-image: get-icon("close", $color-gray-500);
                    cursor: pointer;
                    height: 14px;
                    width: 14px;

                    &:hover {
                        background-image: get-icon("close", $color-green-500);
                    }
                }
            }

            > .nav-body {
                .section {
                    &__header {
                        height: 61px;
                        border-bottom: 1px solid $color-gray-100;

                        > .chevron-icon {
                            background-image: get-icon("arrow-down", $color-gray-500);
                            height: 12px;
                            margin-top: 10px;
                            width: 12px;
                        }
                    }

                    &.active {
                        .mega-menu {
                            display: block;
                            left: 0;
                        }
                        .section__header {
                            > .chevron-icon {
                                margin-top: 0;
                                margin-bottom: 5px;
                            }
                        }
                    }

                    &.demo {
                        height: 60px;
                        background-color: $color-green-400;
                        text-decoration: none;
                        flex-direction: row;
                        align-items: center;
                        justify-content: flex-start;
                        border-radius: 0;

                        > p {
                            color: $color-gray-600;
                            font-size: 16px;
                            font-weight: 500;
                            text-transform: uppercase;
                        }

                        > img {
                            width: map-get($spacers, 3);
                        }
                    }

                    &.meraki-search {
                        .input-group {
                            border-bottom: 1px solid $color-gray-200;
                        }
                        .form-inline {
                            height: 61px;
                        }
                        input {
                            padding: 0;
                        }
                        button {
                            padding: 0 6px 0 0;
                        }
                    }
                }
            }
        }

        &.active {
            left: 0;
        }

        &.search-active {
            .toggle-mobile-nav {
                display: none;
            }

            #mobile-search-popup {
                display: block;
            }
        }

        .nav-footer {
            .lang-switcher-mobile {
                padding: 20px 0;
            }
        }
    }

    .meraki-mobile-header {
        height: 61px;
        width: 100%;
        background-color: $color-white;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        > .nav-icon {
            cursor: pointer;
        }

        .search-icon {
            background-image: get-icon("search", $color-gray-500);
            background-position: 50%;
            background-size: 18px;
            height: 42px;
            position: absolute;
            right: 48px;
            top: 10px;
            width: 42px;

            &:hover {
                background-image: get-icon("search", $color-green-500);
            }

            @include media-breakpoint-up(md) {
                right: 72px;
            }
        }
    }
}

@include media-breakpoint-down(md) {
    .cmp-header {
        .mega-menu {
            &__header {
                height: 61px;
                width: 100%;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                border-bottom: 1px solid $color-gray-100;
                cursor: pointer;

                > p {
                    font-size: 16px;
                    font-weight: 500;
                    color: $color-gray-300;
                    letter-spacing: 0.2px;
                    margin: 0;
                }

                > .chevron-icon {
                    background-image: get-icon("arrow-left", $color-gray-300);
                }
            }

            #icon-child {
                position: absolute;
                right: 0;
                top: 0;
                z-index: 999999;
                width: 66px;
                height: 60px;
                background-size: 18px;
                background-position: center;
            }

            .wrapper {
                box-shadow: none;
                min-height: calc(100% - 61px);

                .top {
                    > {
                        margin: 0 0;
                    }

                    > :not(:last-child) {
                        margin-right: 0;
                    }

                    .category {
                        width: 100%;
                    }

                    .category .options,
                    .options {
                        flex-direction: row;
                        flex-wrap: wrap;
                        > a {
                            flex: 1 1 100%;

                            &.ety-hd {
                                .content {
                                    margin-top: 12px;
                                }
                            }
                        }
                    }

                    .item {
                        .icon {
                            margin-right: 12px !important;
                        }

                        &.mr-1 {
                            margin-right: 20px !important;
                        }
                    }
                }
            }
        }

        .meraki-search {
            form {
                border-bottom: 1px solid $color-gray-200;
            }

            input {
                font-size: 16px;
                font-weight: 500;
                color: $color-gray-500;
            }

            .search-icon {
                background-image: get-icon("search", $color-gray-300);
                height: 20px;
                width: 20px;
            }
        }

        .logo {
            .custom-logo-link {
                img {
                    width: 113px;
                    height: auto;
                }
            }
        }
    }

    .nav-menu-highlight {
        padding: 40px;

        .col-md-6:first-child {
            margin-bottom: 12px;
        }

        .btn {
            width: 100%;
            padding-top: 8px;
            padding-bottom: 8px;
        }

        @include media-breakpoint-up(md) {
            padding-left: 40px;
            padding-right: 40px;
        }
    }
}

@include media-breakpoint-up(lg) {
    .cmp-header.transparent-header:not(.menu-open) {
        background-color: transparent;
        background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
        box-shadow: none;
        width: 100%;

        .meraki-desktop-nav {
            > .logo {
                .custom-logo-link {
                    img {
                        filter: brightness(0) invert(1);
                    }
                }
            }
        }

        .section {
            .section__header {
                a {
                    color: $color-white;
                }
            }

            .chevron-icon {
                width: 10px;
                height: 6px;
                background-image: get-icon("arrow-down", $color-white);
            }

            .search-icon {
                background-image: get-icon("search", $color-white);
            }

            &:hover {
                .section__header {
                    a {
                        color: $color-green-500;
                    }
                }

                .chevron-icon {
                    background-image: get-icon("arrow-down", $color-green-500);
                }

                .search-icon {
                    background-image: get-icon("search", $color-green-500);
                }
            }
        }

        .header-util {
            > a,
            a.selector {
                color: $color-white;

                &:hover {
                    color: $color-green-500;
                }
            }
        }
    }
}

.landing {
    padding: 40px 0 0 0;
}
.top a:nth-child(1) {
    @media (min-width: 768px) and (max-width: 959.98px) {
        padding-top: 40px !important;
    }
}
.top a:nth-child(2) {
    @media (min-width: 768px) and (max-width: 959.98px) {
        padding-top: 40px !important;
    }
}
@media (min-width: 768px) and (max-width: 959.98px) {
    .nav-body .section__header {
        padding-left: 40px !important;
        padding-right: 40px !important;
    }
}
@media (min-width: 320px) and (max-width: 767.98px) {
    .nav-body .section__header {
        padding-left: 24px !important;
