.webinar-result{
    min-height: 300px;
    text-align: center;
    .content-row{
        width: auto;
        text-align: left;
    }
    .col-12{
        margin-bottom: 12px;
        margin-top: 12px;
    }
    .btn{
        margin-top: 80px;
    }
    .noresult{
        strong{
            font-size: 36px;
            font-family: $font-headline;
            color: $color-gray-400;
            line-height: 1.2;
            @include non-latin-fonts();
        }
        p{
            font-size: 16px;
            color: $color-gray-400;
        }
    }
    @include media-breakpoint-up(lg) {
        .noresult{
            strong{
                font-size: 72px;
            }
            p{
                font-size: 20px;
            }
        }
    }
}

.desktop-result{
    position: relative;
}
.loading{
    position: relative;
    left: 0;
    right: 0;
    top: 20px;
    bottom: 0;
    display: none;
}
.loading__area{
    position: relative;
    width: 100%;
    height: 100%;
}
.loading__icon{
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 0%);
}