.woocommerce {

	.open-left-nav {
		margin: 0 0 24px;

		@include media_breakpoint_up(lg) {
			display: none;
		}

		.btn {
			color: $color-black;

			&:before {
				background-image: get-icon('arrow-right', $color-black);
				background-size: 12px 13px;
				vertical-align: -4px;
				width: 16px;
			}

			&:hover,
			&:focus {
				border-color: $color-black !important;	
				color: $color-black;
			}
		}
	}

	.left-nav {
		background: $color-gray-100;
		left: -800px;
		position: absolute;
		top: 0;
		transition: left 0.5s;
		z-index: 10;

		@include media_breakpoint_up(lg) {
			background: transparent;
			left: 0;
			position: static;
			top: 0;
			transition: none;
			z-index: auto;
		}

		&.active {
			left: 0;
		}

		.close {
            background-image: get-icon('close', $color-black);
            background-repeat: no-repeat;
            background-position: 7px 15px;
            background-size: 20px 20px;
			cursor: pointer;
			display: block;
			height: 42px;
			right: 0;
			top: 0;
			position: absolute;
			width: 42px;
            
            &:hover{
                background-image: get-icon('close', $color-green-500);
            }

            @include media_breakpoint_up(lg) {
            	display: none;
            }
		}

		&__wrap {
			min-width: 290px;
			padding: 12px;

			@include media_breakpoint_up(lg) {
				min-width: 100%;
				padding: 0;
			}
		}

		&__title {
			font-weight: 700;
			margin: 0 0 6px;
			text-transform: capitalize;
		}

		&__list {
			font-size: 14px;
			list-style: none;
			padding: 0 0 0 6px;

			.active {

				a {
					color: $color-green-500;
					text-decoration: underline;
				}
			}
		}
	}
}