
.cmp-header-microsite {
    background-color: $color-white;
    
    /* maybe adds IE11 fix */
    &.sticky-top {
        position:relative;
        position:sticky;
        z-index: 1200;
    }

    &.compact {
        box-shadow: 0 2px 4px 0 rgba(0,0,0,0.12);
        .header-util { 
            height:0 !important; 
            visibility: hidden;
            padding-top:0 !important;
            padding-bottom:0 !important;
        }
        .meraki-desktop-nav { padding-top: 18px !important; padding-bottom: 18px !important;}

    }

    .search-icon {
        display: block;
        width: 18px;
        height: 18px;
        background-repeat: no-repeat;
        background-size: contain;
    }

    .chevron-icon {
        width: 18px;
        height: 18px;
        background-repeat: no-repeat;
        background-size: contain;
    }

    .close-icon {
        width: 18px;
        height: 18px;
        background-image: get-icon( 'close', $color-gray-300 );
        background-repeat: no-repeat;
        background-size: contain;
    }

    .close-icon:hover{
        background-image: get-icon( 'close', $color-green-500 );
    }

    .nav-icon {
        margin-right: -15px;
        padding: 15px;

        span {
            border-top: 2px solid $color-gray-400;
            display: block;
            height: 14px;
            position: relative;
            width: 15px;

            @include media-breakpoint-up(md) {
                width: 20px;
            }

            &:after,
            &:before {
                background-color: $color-gray-400;
                content: '';
                display: block;
                height: 2px;
                top: 10px;
                position: absolute;
                width: 100%;
            }
            &:before {
                top: 4px;
            }
        }

        &:hover {
            span {
                border-color: $color-green-500;

                &:after,
                &:before {
                    background-color: $color-green-500;
                }
            }
        }
    }

    .globe-icon {
        width: 14px;
        height: 14px;
        background-repeat: no-repeat;
        background-size: contain;
    }

    .header-util {
        > :not(:last-child) {
            margin-right: map-get($spacers, 2 );
        }

        a {
            color: $color-gray-400;
            font-size: 14px;
            font-weight: 400;
            text-decoration: none;
        }
    }

    .meraki-desktop-nav {
        z-index: 3;
        position: relative;

        .search-demo-ctas {
            padding-left: 0;
        }

        .btn-demo {
            font-size: 14px;
            letter-spacing: 1px;
            line-height: 18px;
            padding: 7px;

            html[lang|="en"] & {
                letter-spacing: 1.2px;
                padding: 7px 15px;
            }
        }

        > .logo {
            
                img {
                    // width: 164px;
                    // height: auto;
                    height: 45px;
                    width: auto;
                }
            
        }

        > .sections {
            flex-wrap: wrap;

             html[lang|="en"] & {
                justify-content: space-around;
            }
        }

    

    .section {
        position: relative;

        &__header {
            flex-direction: row;
            justify-content: space-between;

            cursor: pointer;

            > a {
                margin: 0;
                font-size: 16px;
                color: $color-gray-500;
                font-weight: 500;
                background: none;
                border:none;

                // &:focus {
                //     outline: -webkit-focus-ring-color auto 1px;
                // }
            }

            &:hover {
                > a {
                    text-decoration: none !important;
                    
                }

                .chevron-icon {
                    background-image: get-icon( 'arrow-down', $color-blue-500 );
                }

                .search-icon {
                background-image: get-icon( 'search', $color-blue-500 );

                }

            }

            .chevron-icon {
                width: 10px;
                height: 6px;
                background-image: get-icon( 'arrow-down', $color-gray-500 );
            }

            .search-icon {
                background-image: get-icon( 'search', $color-gray-500 );
            }
        
            
        }

        &__dropdown {
            display: none;
            opacity: 0;
            
            ul {
                list-style: none;

                a:hover {
                    text-decoration: none !important;
                }
            }
        }

        &.active { // <---
            .section__header {
                > a {
                    //color: $color-green-500;
                    font-weight: 500;

                }

                .chevron-icon {
                    background-image: get-icon( 'arrow-up', $color-gray-500 );
                }

                .search-icon {
                    //background-image: get-icon( 'search', $color-green-500 );
                }

            }
           
            .section__dropdown {   
                opacity: 1;
                display: block;
                position: absolute;
                top: calc(100% + 9px);
                z-index: 1000;
                left: 0;
                width: 208px;
                border-radius: 12px;
                background-color: white;
                box-shadow: 0 0 12px 0 rgba(0,0,0,0.35);
            }

            .mega-menu {
                display: block; 
            }
        }
    }
    
    }

    
    
    .meraki-search {
        width: 100%;

        .input-group {
            width: 890px;
            border-bottom: 1px solid $color-gray-200;
        }

        .form-inline {
            width: 100%;

            > :first-child {
                flex: 1;
            }
        }

        input {
            color: $color-gray-500;
            border: none;
            background-color: transparent;
            font-size: 20px;
            font-weight: 400;

            &:focus {
                outline: none;
                box-shadow: none;
            }
        }
        ::placeholder {
            color: $color-gray-500 !important;
            opacity: 0.9;
        }

        button {
            border: none;
            background-color: transparent;
            cursor: pointer;

            > .close {
                width: 18px;
                height: 18px;
                background-image: get-icon( 'close', $color-gray-300 );
                background-repeat: no-repeat;
                background-size: contain;
            }
        }

        .close-mega-menu {
            cursor: pointer;
            width: 18px;
            height: 18px;
            background-image: get-icon( 'close', $color-gray-300 );
            background-repeat: no-repeat;
            background-size: contain;
            background-color: $color-gray-100;

            &:hover {
                background-image: get-icon( 'close', $color-green-500 );
            }
        }
    }


    .mega-menu {
        position: absolute;
        left: 0;
        width: 100%;
        z-index: -1;

        background-color: $color-white;
        box-shadow: 50vw 0 0 0 $color-white, -50vw 0 0 0 $color-white;

        display: none;

        &.active {
            z-index: 1000;
        }
        > .close-mega-menu {
            position: absolute;
            right: 0;
            cursor: pointer;
            width: 20px;
            height: 20px;
            background-image: get-icon( 'close', $color-gray-300 );
            background-repeat: no-repeat;
            background-size: contain;

            &:hover {
                background-image: get-icon( 'close', $color-green-500 );
            }
        }

        .wrapper {
            background-color: $color-gray-100;
            box-shadow: 50vw 0 0 0 $color-gray-100, -50vw 0 0 0 $color-gray-100;
            text-align: left;

            .top {
                padding-left: 12px;
                padding-right: 12px;

                > :not(:last-child) {
                    margin-right: map-get($spacers, 2 );
                }

                .category {
                    flex: 1;

                    p.header {
                        border-bottom: 1px solid $color-gray-100;
                        color: $color-gray-400;
                        font-size: 12px;
                        font-weight: 500;
                        letter-spacing: 0.1em;
                        text-transform: uppercase;

                        @include media-breakpoint-down(md) {
                            margin-top: 30px !important;
                            margin-bottom: 0px !important;
                        }
                    }

                    .options {
                        flex-direction: column;

                        .item img {
                            width: 100%;
                        }
                    }
                }

                > :only-child {
                    .options {
                        flex-direction: row;
                        flex-wrap: wrap;
                        > a {
                            flex: 1 1 33.333%;
                            
                            .icon {
		                        width: 36px;
		                        margin-right: 16px !important;
		                        position: relative;
		                        float:left;
		                        @include media-breakpoint-down(md) {
		                        	margin-top: 6px;
		                        }
		                        @include media-breakpoint-up(lg){
		                            width: 34px;
		                        }
		                    }
                            
                            &:hover {
		                        .icon-hover {
		                            opacity: 1;
		                        }
		                        
		                        .title {
		                            color:$color-green-500;
		                        }
		                        
		                    }
		                    
                        }
                    }
                }
                
                > .item {
					width:33.33%;
				}
                
                .item {
                    text-decoration: none;
                    padding: 18px 0;

                    p {
                        margin: 0;
                        letter-spacing: 0.2px;
                    }

                    p.title {
                        font-size: 20px;
                        color:$color-black;
                    }

                    p.headline {
                        font-size: 14px;
                        line-height: 1.5;
                        margin-top: 6px;

                        @include media-breakpoint-up(lg) {
                            line-height: 1.25;
                        }
                    }

                    .icon {
                        width: 36px;
                        margin-right: 20px !important;
                        position: relative;
                        float:left;
                        @include media-breakpoint-down(md) {
                        	margin-top: 6px;
                        }
                        @include media-breakpoint-up(lg) {
                            width: 34px;
                        }
                    }

                    .icon-hover {
                        position: absolute;
                        top: 0; left: 0;
                        opacity: 0;
                    }
                                
                
                    .content {
                        margin-left: 0px;
                        @include media-breakpoint-down(md) {
                        	margin-left: 0px;
                        }
                        float:left;
                        width: calc(100% - 60px);
                    }
                    &.ety-hd {
                        .content {
                            margin-top: 6px;
                        }
                    }
                

                    &:hover {
                        .icon-hover {
                            opacity: 1;
                        }
                        .title {
                            color:$color-green-500;
                        }
                        
                    }

                    &:active {
                        background-color: transparent;
                        p.title {
                            color: $color-green-500;
                        }
                    }
                }

            }

            .landing {
                border-top: 1px solid $color-gray-200;
                width: auto;
                margin-left: 12px;
                margin-right: 12px;
                > a {
                    font-size: 20px;
                    text-decoration: none;
                    color: $color-gray-500;

                    &:hover {
                        color: $color-green-500;
                    }
                }
            }
        }

        &.active {
            display: block;
        }

    }

}


@include media-breakpoint-down(md) {
    .cmp-header-microsite {
    
		.header-util-mobile {
            background: $color-gray-100;
            padding: 24px 0 2px;
	
            ul {
                list-style: none;
                margin: 0;
                padding: 0;

                li {
                    margin: 0 0 24px;
                }

                a {
                    color: $color-gray-400;
                    font-size: 14px;
                    font-weight: 400;
                    text-decoration: none;

                    &:hover {
                        color: $color-green-500;
                    }
                }
            }
	    }    
       
        .mega-menu {
            position: fixed;
            height: 100%;
            width: 100%;
            z-index: 99999;
            top: 0;
            left: 100%;
            overflow-x: hidden;
            transition: opacity 2.0s ease-in;
            opacity: 1;
            box-shadow: none;

            display: block;
            &__header {
                height: 61px;
                width: 100%;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                border-bottom: 1px solid $color-gray-100;

                cursor: pointer;

                > p {
                    font-size: 16px;
                    font-weight: 500;
                    color: $color-gray-300;
                    letter-spacing: 0.2px;
                    margin: 0;
                }

                > .chevron-icon {
                    background-image: get-icon( 'arrow-left', $color-gray-300 );
                }

            }
            
            #icon-child {
                position: absolute;
                right: 0;
                top: 0;
                z-index: 999999;
                width: 66px;
                height: 60px;
                background-size: 18px;
                background-position: center;
            }
            

            .wrapper {
                box-shadow: none;
                min-height: calc(100% - 61px);

                .top {
                    > {
                        margin: 0 0;
                    }

                    > :not(:last-child) {
                        margin-right: 0;
                    }

                    > :only-child {
                        .options {
                            flex-direction: row;
                            flex-wrap: wrap;
                            > a {
                                flex: 1 1 100%;

                                &.ety-hd {
                                    .content {
                                        margin-top: 12px;
                                    }
                                }
                            }
                        }
                    }


                    .category {
                        width: 100%;
                    }

                    .item {
                        padding: 18px 0 0px !important;
                        width: 100%;
                        .icon {
                        	margin-right: 12px !important;
                        }
                       
                        p.title {
                            font-size: 16px;
                            color: $color-gray-500;
                            
                        }
                        &.mr-1 {
                            margin-right: 20px !important;
                        }
                    }
                }

                .landing {
                    > a {
                        font-size: 16px;
                    }
                }
            }

        }

        .meraki-search {
            form {
                border-bottom: 1px solid $color-gray-200;
            }

            input {
                font-size: 16px;
                font-weight: 500;
                color: $color-gray-500;
            }

            .search-icon {
                width: 20px;
                height: 20px;
                background-image: get-icon( 'search', $color-gray-300 );
            }
        }

        .meraki-mobile-nav {
            position: fixed;
            height: 100%;
            width: 100%;
            z-index: 1000;
            top: 0;
            left: 100%;
            background-color: $color-white;
            overflow-x: hidden;
            transition: opacity 2.0s ease-in;
            opacity: 1;
            

            > .nav-content {
                position: relative;
                width: 100%;
                // text-align: center;
                background-color: $color-white;

                display: flex;
                flex-direction: column;

                > .nav-header {
                    height: 61px;
                    width: 100%;
                    border-bottom: 1px solid $color-gray-100;
                    background-color: $color-white;
                    
                    > .close-icon {
                        background-image: get-icon( 'close', $color-gray-500 );
                        cursor: pointer;
                        width:14px;
                        height: 14px;
                    }
                }

                > .nav-body {
                    .section {
                       &__header {
                            height: 61px;
                            border-bottom: 1px solid $color-gray-100;
                            justify-content: space-between;
                            font-weight: 500;
                            font-size: 14px;

                            > .chevron-icon {
                                width: 14px;
                                height: 14px;
                                background-image: get-icon( 'arrow-right', $color-gray-500 );
                                transform: rotate(90deg);
                                transition: 0.25s transform ease;
                            }

                            a {
                                display: block;
                                width: 100%;

                                &:hover {
                                    text-decoration: none;
                                }
                            }

                        }

                        &__dropdown {
                            display: none;
                            opacity: 0;
                            
                            ul {
                                list-style: none;
                                
                                a {
                                    font-weight: 500;
                                    display: block;
                                    width: 100%;
                                    font-size: 14px;

                                   &:hover {
                                        // color: $color-blue-500 !important;
                                        text-decoration: none !important;
                                    }
                                }
                            }
                        }
                        
                        

                        &.active {
                            .section__dropdown {   
                                opacity: 1;
                                display: block;
                                width: 100%;
                                background-color: transparent;
                                // box-shadow: 0 0 12px 0 rgba(0,0,0,0.35);
                                border-bottom: 1px solid $color-gray-100;
                            }

                            .section__header {
                                border-bottom: 0;                                
                                .chevron-icon {
                                    transform: rotate(-90deg);
                                }

                            }

                            a, a:hover {
                                text-decoration: none;
                            }
                        }

                        &.demo {
                            height: 60px;
                            
                            text-decoration: none;

                            flex-direction: row;
                            align-items: center;
                            justify-content: flex-start;
                            border-radius: 0px;

                            > p {
                                color: $color-gray-600;
                                font-size: 16px;
                                font-weight: 500;
                                text-transform: uppercase;
                            }

                            > img {
                                width: map-get($spacers, 3 );
                            }

                            .btn {
                                width: 100%;
                                text-align:center;
                            }
                        }

                        &.meraki-search {
                            .input-group {
                                border-bottom: 1px solid $color-gray-200;
                            }
                            .form-inline {
                                height: 61px;
                            }
                            input {
                                padding: 0;
                            }
                            button {
                                padding: 0 6px 0 0;
                            }
                        }

                        .search-icon {
                            width: 18px;
                            height: 18px;
                            background-image: get-icon( 'search', $color-gray-500 );
                        }
                    }
                }
            }

            &.active {
                left: 0;
            }

           
        }
        .meraki-mobile-header {
            position: fixed;
            top: 0;
            left: 0;
            height: 61px;
            width: 100%;
            background-color: $color-white;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            > .nav-icon {
                cursor: pointer;
            }
        }

        .logo {
            
                img {
                    // width: 113px;
                    // height: auto;
                    width: auto;
                    height: 30px;
                }
            
        }

        &.blue {

            .header-util-mobile a:hover,
            .section__dropdown ul a:hover {
                color: $color-blue-400;
            }
    
            .section.active .section__header {
                background-color: $color-blue-100;
            }

            .nav-icon:hover {
                span {
                    border-color: $color-blue-400;

                    &:after,
                    &:before {
                        background-color: $color-blue-400;
                    }
                }
            }

            .cmp-language-switcher__selector:hover {
                color: $color-blue-400 !important;

                .globe-icon {
                    background-image: get-icon('globe', $color-blue-400) !important;
                }

                .chevron-icon {
                    background-image: get-icon('arrow-down', $color-blue-400) !important;
                }
            }

            .cmp-language-switcher__menu__item:hover {
                color: $color-blue-400;
            }
        }
    
        &.green {
            .header-util-mobile a:hover,
            .section__dropdown ul a:hover {
                color: $color-green-400;
            }
    
            .section.active .section__header {
                background-color: $color-green-100;
            }
        } 
    }


}

@include media-breakpoint-up(lg) {
    .cmp-header-microsite.transparent-header:not(.menu-open) {
        background-color: transparent;
        background-image: linear-gradient(180deg, #000000 0%, rgba(0,0,0,0.00) 100%);
        width: 100%;

        .meraki-desktop-nav {
            > .logo {
                .custom-logo-link {
                    img {
                        filter: brightness(0) invert(1);
                    }
                }
            }
        }

        .section {

            .section__header {

                a {
                    color: $color-white;
                }
            }

            .chevron-icon {
                width: 10px;
                height: 6px;
                background-image: get-icon( 'arrow-down', $color-white );
            }

            .search-icon {
                background-image: get-icon( 'search', $color-white );
            }

            &:hover {
                .section__header {

                    a {
                        color: $color-green-500;
                    }
                }

                .chevron-icon {
                    background-image: get-icon( 'arrow-down', $color-green-500 );
                }

                .search-icon {
                    background-image: get-icon( 'search', $color-green-500 );
                }
            }
        }

        .header-util {

            > a, a.selector {
                color: $color-white;

                &:hover {
                    color: $color-green-500;
                }
            }
        }
    }

    &.blue {

        .header-util a:hover,
        .header-util-mobile a:hover,
        .section__dropdown ul a:hover {
            color: $color-blue-400;
        }

        .cmp-language-switcher__selector:hover {
            color: $color-blue-400 !important;

            .globe-icon {
                background-image: get-icon('globe', $color-blue-400) !important;
            }

            .chevron-icon {
                background-image: get-icon('arrow-down', $color-blue-400) !important;
            }
        }

        .cmp-language-switcher__menu__item:hover {
            background: $color-blue-100;
            color: $color-gray-400 !important;
        }
    }

    &.green {
        .header-util a:hover,
        .header-util a:hover,
        .section__dropdown ul a:hover {
            color: $color-green-400;
        }

        .section__header:hover {

            .chevron-icon {
                background-image: get-icon('arrow-down', $color-green-500) !important;
            }
        }
    } 
}
