.woocommerce-Tabs-panel--description {

	.title {
		margin: 0 0 24px;
	}

	.cta-aside {
		background: $color-gray-100;
		margin-top: 24px;
		max-width: 320px;
		padding: 24px;

		.button {
			border-radius: 6px;
			line-height: 1.4;
			margin-top: 1em;
			padding: 10px 25px 7px;

			&:after {
				display: none;
			}

			@extend .btn-primary-green;
		}

		@include media_breakpoint_up(lg) {
			margin-top: 0;

			.button {
				font-size: 14px;
			}
		}
	}
}

