.genesis-skip-link {
	margin-bottom:0;
}

.wc-block-grid {
	@include make-container();
	@include make-container-max-widths();
}

.wc-block-grid,
.container {
	padding-left: $grid-gutter-width;
	padding-right: $grid-gutter-width;

	.container {
		padding-left: 0;
		padding-right: 0;
	}

	@include media-breakpoint-up(md) {
		padding-left: 40px;
		padding-right: 40px;
	}

	@include media-breakpoint-up(lg) {
		padding-left: 60px;
		padding-right: 60px;
	}

	@include media-breakpoint-up(xl) {
		padding-left: 80px;
		padding-right: 80px;
	}

	@include media-breakpoint-up(xxl) {
		padding-left: 128px;
		padding-right: 128px;
	}
}

@mixin full-bleed {
	left: 50%;
	margin-left: -50vw;
	margin-right: -50vw;
	position: relative;
	width: 100vw;
}

.full-bleed {
	@include full-bleed;
}

.hidden {
	visibility: hidden;
}

.nowrap {
	white-space: nowrap !important;
}

.mobile-only{
	display: none;
}

.desktop-only{
	display: block;
}

@include media-breakpoint-down( xl ){
	.desktop-only{
		display: none;
	}
	.mobile-only{
		display: block;
	}
}