//$data-svg-prefix: 'data:image/svg+xml;utf-8,';
$data-svg-prefix: 'data:image/svg+xml,';

@function str-replace($string, $search, $replace: '') {
  $index: str-index($string, $search);
  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
  }
  @return $string;
}

@function get-icon( $icon, $color: #fff ) {

	@if 'color' != type-of( $color ) {

		@warn 'The requested color - "' + $color + '" - was not recognized as a Sass color value.';
		@return null;
	}

	@if map-has-key( $icons, $icon ) {

		$icon:        map-get( $icons, $icon );
		$placeholder: '%%COLOR%%';
		$data-uri:    url($data-svg-prefix + str-replace($icon, $placeholder, $color ));

		@return str-replace( $data-uri, '#', '%23' );
	}

	@warn 'The requested icon - "' + $icon + '" - is not defined in the $icons map.';
	@return null;
}
