.woocommerce {

	.product-collateral {
		margin: 60px 0;

		&__body {
			margin: 24px 0 0;

			ul {
				font-size: 14px;
				list-style: none;
				margin: 0;
				padding: 0;

				@include media-breakpoint-up(md) {
					column-count: 2;
					column-gap: 20px;
				}

				li {
					margin: 0 0 12px;

					&.pdf {
						background: transparent url('../images/icon-pdf-download.svg') no-repeat 0 50%;
						background-size: 30px;
						padding: 0 0 0 36px;
					}

					.missing {
						color: $color-gray-300;
					}
				}
			}

			.btn {
				margin-top: 24px;

				@include media-breakpoint-up(xxxl) {
					margin-top: 0;
				}
			}
		}
	}
}

