
.cmp-video-gallery {
    &__iamge {
        position: relative;
        margin-bottom: 24px;
        width: 100%;
        padding-bottom: 54%;
        background-size: cover;
        background-repeat: no-repeat;
        .icon-play {
            background-image: url('/wp-content/themes/genesis-meraki/images/button-play-fff.svg');
            position: absolute;
            height: 50px;
            width: 50px;
            z-index: 999;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);    
        }
    }
    &__info {
        padding-bottom: 24px;
        @include media-breakpoint-down(lg) {
            text-align: center;
        }
    }
    p a{
        color:$color-blue-500 !important;
    }
}


