.single-case_studies {
	background-color: $color-gray-50;
	  
	.post-case_studies {
		background-color: $color-white;
		
		&__detail-section {
			margin-bottom: 56px;
		}

		&__hero {
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center center;

			padding-top: 60px;
			padding-bottom: 84px;

			@include media-breakpoint-up(lg) {
				padding-top: 172px;
				padding-bottom: 216px;
			}

			&-title {
				color: white;

				&.black {
					color : $color-black;
				}
			}
			&-highlight {
				max-width: 1000px;
				color:white;

				@include media-breakpoint-down(lg) {
					font-size: 32px;
					line-height: 40px;
				}

				&.black {
					color : $color-black;
				}
			}
		}

		.col-content {

			padding-top: 10px;
			padding-bottom: 10px;
			color: $color-black;

			@include media-breakpoint-up(md) {
				padding: 40px 20px;
			}
			
			h5 {
				margin-top:46px;
			}
			figure.wp-caption {
				width: 100% !important;
				margin-top: 24px; 
				margin-bottom: 24px; 

				@include media-breakpoint-down(lg) {
					margin: 24px -24px;
					width: auto !important;
					border-radius: 0;

				}

				img {
					margin:0;
				}

				figcaption {
					margin-top: 24px; 
					font-size: 16px;
					font-family: $font-sans-serif;
					@include non-latin-fonts();

					@include media-breakpoint-down(lg) {
						margin-left: 24px;
						margin-right: 24px
					}
				}
			}
			img {
				border-radius: 10px;

				@include media-breakpoint-down(lg) {
					margin: 24px -24px;
					width: auto;
					border-radius: 0;
				}

				&.alignleft {
					margin-right: 24px;
					margin-top: 24px;
					margin-bottom: 24px;
					float:left;
					width: 60%;
				}

				&.alignright {
					margin-left: 24px;
					margin-top: 24px;
					margin-bottom: 24px;
					float:right;
					width: 60%;
				}

				&.aligncenter {
					margin: 24px auto;
					width: 60%;
					display:block;

				}

				
				&.aligncenter, &.alignleft, &.alignright {
					@include media-breakpoint-down(md) {
						margin: 24px 0;
						float: none;
						width: 100%;
						border-radius: 10px;
					}
				};
			}

			blockquote {
				margin: 48px 0 0 0;
				padding-left: 36px;
				padding-top: 48px;
				padding-bottom: 36px;
				position:relative;
				font-weight: normal;
				font-family: $font-sans-serif;
				line-height: 40px;
				color: $color-blue-500;
				@include non-latin-fonts();
				
				@include media-breakpoint-down(md) {
					margin: 40px 0 0 0;
					padding-left: 36px;
					padding-top: 36px;
				}

				@include media-breakpoint-down(sm) {
					padding-left: 0;
					padding-top: 30px;

				} 

				& p {
					display: inline-block;
					position: relative;
					font-size: 32px;
					line-height: 40px;

					&:last-child {
						margin-bottom: 0;
					}

					@include media-breakpoint-down(sm) {
						font-size: 20px;
						line-height: 30px;
					}
				}

				&::before {
					content: "";
					position: absolute;
					left: 0px;
					top: 0px;
					
					width: 36px;
					height: 36px;

					background-image: url(../images/blockquote-quote.png);
					background-position: center center;
					background-repeat: no-repeat;
					background-size: contain;
					color: $color-blue-500;

					@include media-breakpoint-down(sm) {
						width:24px;
						height: 24px;
					}
				}
			}
			
			cite {
				margin-left: 36px;
				color: $color-gray-300;
				font-style: normal;
				font-size: 24px;
				font-weight: 500;
				letter-spacing: 0.2px;
				line-height: 32px;
				display: block;
				position: relative;
				margin-top: 0;
				padding-top: 36px;
				padding-bottom: 36px;

				@include media-breakpoint-down(sm) {
					font-size: 16px;
					line-height: 18px;
					margin-left: 0;
					@include non-latin($lang: zh, $line-height: 20px);
				}

				em {
					margin-top: 12px;
					display: inline-block;
				}

				&:before {
					content:'';
					display: block;
					position: absolute;
					top: 0;
					width: 90px;
					border-top: 2px solid $color-blue-500;

				}
			}

			img {
				width: 100%;
				height: 100%;
				max-width: none;
			}

			h1,h2,h3,h4,h5,h6,p{
				margin-bottom: 16px;
			}

			h2,h3{
				font-size: 20px;
				letter-spacing: 0.2px;
				line-height: 26px;
				@include media-breakpoint-up(md){
					font-size: 32px;
					letter-spacing: 0.5px;
					line-height: 36px;
				}
			}

			p,li {
				@include media-breakpoint-up(md) {
					font-size: 20px;
					line-height: 1.5em;
				}
			}
		}

		.nav {
			display: block;
			padding: 14px 0 14px 32px;
			color: $color-gray-50;
			font-weight: 700;
			background: $color-gray-50;
			border-radius: 5px 5px 0 0;
			opacity: 0.98;
			transition: all 0.2s;
		}

		.headline {
			padding: 30px 0;
			.col-customer-logo-image {
				display: flex;
				align-items: center;
				img {
					max-width: 350px;
					height: auto;
				}
				@include media-breakpoint-down(md) {
					margin-top:50px;
				}
			}
		}

		&__image-box {
			display: block;
			position: relative;
		}

		.casestudy__icon-play {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 80px;
			height: 80px;
			background-image: url('/wp-content/themes/genesis-meraki/images/button-play-fff.svg');
			background-size: contain;
		}

		.container--bottom {
			background-color: $color-white;
			
			.col-sidebar {

				padding-top: 26px;
				padding-bottom: 20px;
				
				@include media-breakpoint-up(md) {
					padding-top: 40px;
				}
				
				.row-highlights {
				margin-bottom: 20px;
				
				.highlights-label {
					font-weight: 700;
					font-size: 0.9em;
					padding-bottom: 20px;
					text-transform: uppercase;
					color: $color-black;
					border-bottom: 1px dotted #ccc;
				}
				.highlights-list {
					border-bottom: 1px dotted #ccc;
					padding: 0.6em 0;

				
				}
				}
				.row-webinars_events {
					margin-bottom: 20px;
					.webinars_events-label {
						margin: 0.5em 0 1.2em;
						font-weight: 700;
						font-size: 0.9em;
						text-transform: uppercase;
						color: $color-black;
					}
					.webinars_events-list {
						margin-bottom: 0.8em;
					}
				}
				.row-readmore {
					margin-bottom: 20px;
					.readmore-label {
						margin: 0.5em 0 1.2em;
						font-weight: 700;
						font-size: 0.9em;
						text-transform: uppercase;
						color: $color-black;
					}
					.readmore-list {
						margin-bottom: 0.8em;
						.readmore-list-url {
						&::before {
							content:"";
							width: 15px;
							display: inline-block;
							background-image: url('/wp-content/themes/genesis-meraki/images/file-download-icon.png');
							background-size: 15px 15px;
							height: 15px;
						}
						}
					}
				}
				.row-language {
					margin-bottom: 20px;
					.language-label {
						margin: 0.5em 0 1.2em;
						font-weight: 700;
						font-size: 0.9em;
						text-transform: uppercase;
						color: $color-black;
					}
					.language-list {
						margin-bottom: 0.8em;
					}
				}
			}
		}

		article .entry-content {
		display: none;
		}

		&__taxonomy {
			&-heading {
				font-size: 20px; 
				line-height: 30px;
			}					
		
			&-list {
				list-style: none;
				margin: 0;
				padding: 0;
			
				&-item {
					display: flex;
					flex-direction: row;
					align-items: center;
					justify-content: flex-start;
					
					img {
						width: 25px;
						margin-right: 12px;
					}

					
				}
			}

			.wpml-ls-display {
				display: none;
			}
		}


		&__sidebar {
			&-container {
				@include media-breakpoint-up(md) {
				position: absolute;
				top:0; 
				left:0;
				padding-left: $spacer;
				width: 100%;
				height: 100%;
				}
			}

			&-sticky {
				padding-top: 0px;

				@include media-breakpoint-up(md) {
					position: -webkit-sticky;
					position: sticky;
					top: 0;
					padding-top: 40px;
					padding-bottom: 20px;
					padding-left: 0;
				}

				body.anchor-nav-sticky & {
					padding-top: 80px;
				}
			}
		}

		&__utility {
			&-list {
				position: relative;
				
				&--item {
					padding-left: 36px;

					a {
						color: $color-blue-500;
						font-weight: 500;
						text-transform: uppercase;

						&:before {
							position: absolute;
							content: '';
							width: 24px;
							height: 24px;
							left: 6px;
							background-repeat: no-repeat;
							background-position: center center;
						}

						&.share-icon:before {
							background-image: url(../images/icon-case_study-share.svg);
						}	

						&.download-icon:before {
							background-image: url(../images/icon-case_study-download.svg);
						}
					}
				}
			}
		}

		&__share {
			border-radius: 12px;
			box-shadow: rgba(0,0,0,0.35) 0 0 12px;
			background-color: $white;
			padding: 24px;
			width: auto;
			display: inline-block;

			@include media-breakpoint-down(md) {
				display: block;
			}
			
			&-list {
				list-style: none;
				position: relative;
				padding-left: 36px;
				margin-bottom: 0;

				&--item {
					@include media-breakpoint-down(md) {

					}
					&:last-child {

						@include media-breakpoint-up(lg) {
							margin-bottom:0 !important;
						}
					}

					a {
						color: $color-gray-500;

						&:before {
							position: absolute;
							content: '';
							width: 24px;
							height: 24px;
							left: 0px;
							background-repeat: no-repeat;
							background-position: center center;
						}

						&.email-icon:before {
							background-image: url(../images/icon-case_study-email.svg);
						}

						&.linkedin-icon:before {
							background-image: url(../images/icon-case_study-linkedin.svg);
						}
					}
				}
			}
		}
	}
}
 

body.anchor-nav-sticky  .post-case_studies__sidebar-sticky {
	padding-top: 80px;
}