.cmp-asset-multi-paragraph {
	min-height: 490px;
	position: relative;
	margin-bottom: 60px;
	
	&.no-mb {
		margin-bottom: 0;
	}

	h2{
		margin-bottom: 36px;
		line-height: 72px;
	}
	hr{
		margin-top: 0;
		margin-bottom: 24px;
	}
	.btn{
		line-height: 1;
		margin-bottom: 36px;
	}
	&__image {
		&--desktop{
			background-position: 50% 50%;
			background-repeat: no-repeat;
			background-size: cover;
			display: none;
			height: 100%;
			left: 0;
			position: absolute;
			top: 0;
			width: 50%;
			z-index: 1;

			@include media-breakpoint-up(lg) {
				display: block;
			}
		}

		&--mobile{
			display: block;
			height: auto;
			width: 100%;

			@include media-breakpoint-up(lg) {
				display: none;
			}
		}
	}

	&__text{
		padding: 24px 0 60px;

		p {
			color: $color-gray-400;
		}	

		h4 {
			margin-bottom: 16px;
		}

		@include media-breakpoint-up(lg) {
			padding-top: 96px;

			h4, p {
				padding-right: 20%;
			}
			
			p {margin-bottom: 20px;}
		}
	}

	.post-edit-link {
		background: rgba(255,255,255,0.5);
		bottom: 0;
		left: 10px;
		padding: 6px;
		position: absolute;
		z-index: 2;
	}
}