.block-6-up {
	margin: 60px 0;

	&.no-mb {
		margin-bottom: 0;
	}
	
	h2 {
		margin-bottom: 12px;
	}

	&__item {
		color: $color-gray-400;
		margin-top: 24px;

		@include media-breakpoint-up(lg) {
			margin-top: 48px;
		}
			
		&__img {
			height: 80px;
			margin-bottom: 16px;
			
			img {
				display: block;
				width: auto;
				height: 75px;
				
			}
		}

		&__body {
			@include media-breakpoint-up(lg) {
				font-size: 20px;
				letter-spacing: 0.2px;
				line-height: 28px;
				@include non-latin($lang: ja, $line-height: 30px);
				@include non-latin($lang: zh, $line-height: 30px);
			}
			a {
				color: $color-blue-500;
			}
		}
	}

	@include media-breakpoint-up(lg) {
		margin: 96px 0 120px;

		.col-item {

			&[data-count="0"],
			&[data-count="1"] {

				.block-6-up__item {
					margin-top: 12px;
				}
			}
		}
	}
}