.woocommerce-Tabs-panel--compare {

	.meraki-woocommerce-tab-panel {
		overflow-x: scroll;

		@include media_breakpoint_up(lg) {
			overflow-x: auto;

			html[lang|="de"] & {
				overflow-x: scroll;
			}
		}

		&-inner {
			min-width: 650px;

			@include media_breakpoint_up(lg) {
				min-width: 100%;
			}
		}
	}

	.tablepress {

		img {
			height: auto;
			max-width: 100%;
			width: auto;
		}
	}

	.compare-table,
	.tablepress {
		table-layout: fixed;
		width: 100%;

		html[lang|="de"] & {
			table-layout: auto;
		}

		* {
			font-size: 14px !important;
			font-weight: normal;
			line-height: 1.4;
		}

		tr {
			border-top: 1px solid #ccc;
			padding-top: 40px;
			vertical-align: top;
		}

		thead {
			tr {
				border-top: none
			}

			th,
			td{
				font-weight: 700;
			}

			td {
				padding: 0.5em 1% 0em;
				vertical-align: middle;
			}
		}

		th {
			font-weight: 700;
			padding-bottom: 1em;

			h2 a {
				font-size: 18px;
			}
		}

		th a {
			font-weight: 700;
		}

		tbody td {
			border-left: none;
			width: 5% !important;
			width: 23.75%;
			padding: 0.5em 1% 2em;
		}

		tbody td:first-child {
			border-left: none;
			padding-left: 0;
			font-weight: 700;
		}

		&.minor td {
			padding: 0.5em 1%;
		}

		td:first-child {
			width: 5%;
		}

		th a {
			display: block;
			text-align: center;
		}

		thead td {
			padding-top: 15px;
			padding-bottom: 5px;
		}

		tbody th {
			padding-top: 15px;
			padding-bottom: 20px;
		}

		tbody td {
			padding-top: 20px;
			padding-left: 10px;
			padding-right: 10px;
			padding-bottom: 20px;
		}

		&.MR70 tbody td:nth-child(2),
		&.MR74 tbody td:nth-child(3),
		&.MR84 tbody td:nth-child(4),
		&.MX60 tbody td:nth-child(2),
		&.MX60W tbody td:nth-child(3),
		&.MX80 tbody td:nth-child(4),
		&.MX100 tbody td:nth-child(5),
		&.MX400 tbody td:nth-child(6),
		&.MX600 tbody td:nth-child(7) {
			background-color: $color-gray-100;
		}

		&.MS22 td:nth-child(2),
		&.MS22 td:nth-child(3) {
			background-color: $color-gray-100;
		}

		&.MS42 td:nth-child(4),
		&.MS42 td:nth-child(5) {
			background-color: $color-gray-100;
		}

		&.MR20 tbody th:nth-child(1),
		&.MR30H tbody th:nth-child(1),
		&.MR33 tbody th:nth-child(1),
		&.MR42 tbody th:nth-child(1),
		&.MR42E tbody th:nth-child(1),
		&.MR45 tbody th:nth-child(1),
		&.MR52 tbody th:nth-child(1),
		&.MR53 tbody th:nth-child(1),
		&.MR53E tbody th:nth-child(1),
		&.MR55 tbody th:nth-child(1) {
			background-color: $color-white;
		}

		&.MR20 tbody tr:nth-child(1),
		&.MR30H tbody tr:nth-child(2),
		&.MR33 tbody tr:nth-child(3),
		&.MR42 tbody tr:nth-child(4),
		&.MR42E tbody tr:nth-child(5),
		&.MR45 tbody tr:nth-child(6),
		&.MR52 tbody tr:nth-child(7),
		&.MR53 tbody tr:nth-child(8),
		&.MR53E tbody tr:nth-child(9),
		&.MR55 tbody tr:nth-child(10) {
			background-color: $color-gray-100;
		}

		&.MX60 th:nth-child(2),
		&.MX60W th:nth-child(3),
		&.MX80 th:nth-child(4),
		&.MX100 th:nth-child(5),
		&.MX400 th:nth-child(6),
		&.MX600 th:nth-child(7) {
			border-bottom: 4px solid $color-black;
		}

		&.MS22 th:nth-child(2),
		&.MS22 th:nth-child(3) {
			border-bottom: 4px solid $color-black;
		}

		&.MS42 th:nth-child(4),
		&.MS42 th:nth-child(5) {
			border-bottom: 4px solid $color-black;
		}

	}

	#compare-table {
		th {
			width: 23.75%;
			padding: 0.5em 1% 2em;

			&:first-child {
				width: 5%
			}
		}
	}

	#compare-table-appliances th:nth-child(3) img {
		margin-top: -10px
	}

	#compare-table-appliances th:nth-child(3)>h3 {
		margin-top: -8px
	}
}
