.cmp-card-view {

    &.no-mb {
        margin-bottom: 0!important;
    }

    &.no-mt {
        margin-top: 0!important;
    }
    
    &__content-row {
        padding-top: 10px;
    }

    &__image {
        margin: 0 24px;
        padding-bottom: 54%;
        background-size: contain;
        background-repeat: no-repeat;
        border-radius: 10px 10px 0 0;
        background-position: center;
    }

    &__item-block {
        display: block;
        margin: 0 auto;
        max-width: 432px;
        width: 100%;
        padding-top: 24px;
        border-radius: 10px;
        text-align: center;
        background-color: $color-white;

        &.permalink:hover {
            box-shadow: 0 0 12px 0 rgba(0,0,0,0.25);
            text-decoration: none !important;

            .btn-tertiary-green {
                border-bottom: 1px solid $color-green-600 !important;
                display:inline;
            }
        }
        .btn-tertiary-green {
            display:inline;
        }
    }

    &__info {
        padding: 24px;
    }


	        
    	

    &__item {
        padding-bottom: 24px;

        &-title {
            font-weight: 500;
            margin-bottom: 12px;
            color: $color-black;
            letter-spacing: 0.2px;
        }

        &-eyebrow {
            font-size: 16px;
            line-height: 24px;
            @include non-latin($lang: ja, $line-height: 20px);
            @include non-latin($lang: zh, $line-height: 20px);

            a {
                color: $color-gray-400;
                letter-spacing: 0.2px;
            }
            &.text {
                margin-bottom: 36px;
            }
        }

        &.two-col {
            &:nth-child(odd) {
                @include media-breakpoint-up(lg) {
                    .cmp-card-view__item-block {
                        float: right;
                    }
                }
            }
        }
    }

    .owl-stage {
        display: flex;
    }

    .owl-dots {
        width: 100%;
        text-align: center;
    }

    .owl-dot {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: $color-gray-100;
        border: 0;
        margin-left: 5px;
        margin-right: 5px;
        padding: 0;

        &.active {
            background-color: $color-green-500
        }

        span {
            display: none
        }
    }

    .owl-nav button {
        position: absolute;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        border: 0;
        background-repeat: no-repeat;
        background-position: center center;
        background-color: transparent;

        &.disabled {
            opacity: 0.5;
        }

        &.owl-prev {
            left: 0;
            top: 50%;
            background-image: url(../images/owl-nav-prev.png);
        }

        &.owl-next {
            right: 0;
            top: 50%;
            background-image: url(../images/owl-nav-next.png);
        }

        span {
            display: none
        }
    }

    .container {
        position: relative;
        overflow-x: hidden;
        
        .cmp-card-view__item .cmp-card-view__info div.btn
    	{
    		border: 0px !important;
    	}
	
    }
}
