.cmp-global-search {
    height: 100%;
    text-align: left;
    z-index: 99;

    .search-order {
        .quickLinks {
            display: inline-block;
            padding: 10px 24px;
            border-radius: 6px;
            border: 1px solid #5576d1;
            color: #5576d1;
            margin-right: 10px;
            margin-bottom: 15px;
            font-size: 20px;
        }
    }

    &__wrapper {
        height: 100%;
        overflow-y: auto;
        padding-bottom: 24px;
        padding-top: 24px;

        @include media-breakpoint-up(md) {
            padding-bottom: 96px;
            padding-top: 96px;
        }

        @include media-breakpoint-up(lg) {
            padding-bottom: 120px;
            padding-top: 120px;
        }
    }

    &__input {
        margin-bottom: 24px;

        @include media-breakpoint-up(md) {
            margin-bottom: 40px;
        }

        form {
            position: relative;
            width: 100%;

            &::before {
                background-image: get-icon("search", $color-black);
                background-repeat: no-repeat;
                background-size: contain;
                content: "";
                height: 18px;
                left: 5px;
                position: absolute;
                top: 22.5%;
                width: 18px;

                @include media-breakpoint-up(md) {
                    height: 28px;
                    width: 28px;
                }
            }
        }
        input {
            border: 0;
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;
            padding-bottom: 24px;
            padding-left: 40px;
            width: 100%;

            @include media-breakpoint-up(md) {
                font-size: 48px;
                letter-spacing: 0.4px;
                line-height: 58px;
                padding-left: 75px;
            }

            &::placeholder {
                color: $color-gray-200;
            }

            &:focus,
            &:active {
                border-bottom: 2px solid $color-gray-200;
            }
        }
        .close-search {
            background-image: get-icon("close", $color-black);
            background-repeat: no-repeat;
            background-size: contain;
            height: 18px;
            width: 18px;
            position: absolute;
            right: 0;
            top: 10px;

            @include media-breakpoint-up(md) {
                height: 24px;
                top: 22px;
                width: 24px;
            }
        }
    }

    &__quicklinks {
        ul {
            list-style-type: none;
            margin: 0;
            padding: 0;
        }
        li {
            display: block;

            a {
                display: block;
                padding: 12px 24px;
                text-align: center;
            }

            @include media-breakpoint-up(md) {
                display: inline-block;
                margin-right: 24px;
            }
        }
    }
    .row {
        margin-left: -15px !important;
        margin-right: -15px !important;
    }

    &__result {
        margin-top: 40px;
        position: relative;

        .noresult {
            font-size: 18px;
            font-weight: 500;
            margin: 20px 0 40px;

            @include media-breakpoint-up(md) {
                font-size: 20px;
                margin: 60px 0;
            }
        }

        .row {
            margin-left: -30px;
            margin-right: -30px;
        }

        .see-all-row {
            margin-bottom: 60px;
            margin-top: 60px;

            .col {
                padding-left: 30px;
                padding-right: 30px;
            }

            .see-all {
                background-repeat: no-repeat;
                background-image: get-icon("arrow-right", $color-blue-500);
                background-size: 14px 14px;
                background-position: center right;
                font-weight: 500;
                padding-right: 24px;
                text-transform: uppercase;
            }
        }
    }

    .cmp-global-searchresult {
        // padding-left: 30px;
        // padding-right: 30px;

        &__wrapper {
            margin-bottom: 40px;
        }

        &__posttype {
            margin-bottom: 12px;
        }

        &__title {
            font-size: 20px;
            font-weight: 500;
            height: 52px;
            line-height: 26px;
            margin-bottom: 24px;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        &__image {
            background-color: $color-gray-50;
            background-position: center;
            background-size: cover;
            min-height: 0;
            padding-bottom: 75%;
            width: 75%;
            @include media-breakpoint-down(sm) {
                width: 100%;
            }
        }
    }

    .global-search-card-link:hover {
        text-decoration: none;
        .cmp-global-searchresult__title {
            color: $color-blue-500;
            text-decoration: underline;
        }
    }
    .active {
        background-color: #5576d1;
        color: #fff !important;
    }
    @media (max-width: 576px) {
        .quickLinks {
            width: 100%;
            text-align: center;
            margin-bottom: 15px;
        }
    }
    @media (min-width: 320px) and (max-width: 767.98px) {
        .search-section .container {
            padding: 0px;
        }
        .result-container {
            padding: 0;
        }
    }
}
