@import "../base/colors.scss";
@import "../base/bootstrap-overrides.scss";

.cmp-footer {
  background-color: $color-gray-50;

  .top {
    .logo {
      .custom-logo-link {
          img {
              width: 205px;
              height: auto;
          }
      }
    }

    .category {
      > ul {
        list-style: none;
        padding: 0;
        margin: 0;
      }

      &__header {
        font-weight: 500;
        font-size: 14px;
        letter-spacing: 1.75px;
        color: $color-gray-400;
        text-transform: uppercase;
      }

      &__option, &__copy {
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0.175px;
        color: $color-gray-500;

        a {
          font-size: 14px;
          font-weight: 400;
          line-height: 24px;
          letter-spacing: 0.175px;
          color: $color-gray-500;
          text-decoration: none;

          @include media-breakpoint-up(lg) {
            font-size: 16px;
            font-weight: 400;
            letter-spacing: 0.2px;
            line-height: 1.5px;
            
          }

        }
      }
    }
  }

  .social {
    width: 100%;
    border-top: 1px $color-gray-200 solid;

    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    &__icons {
      > ul {
        list-style: none;
        padding: 0;

        > li {
          float: left;

          img { 
            width: 22px;
            opacity: 0.8;
          }
        }
      }
    }

    &__credits {
      > p {
        color: $color-gray-400;
        font-size: 14px;
        margin: 0;
      }
    }

    > * a {
      color: $color-gray-400;
      text-decoration: none;
    }
  }
}

@include media-breakpoint-down(sm) {
  .cmp-footer {

    .top {
      .logo {
        .custom-logo-link {
            img {
                width: 164px;
                height: auto;
            }
        }
      }

      .category:nth-last-child(n+3) {
        padding-bottom: map-get($spacers, 3 )
      }
    }


    .social {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
    .social__icons{
      padding-top:36px;
    }
    .container .pb-5{
      padding-bottom:36px !important;
    }
  }
}