.cmp-casestudy-result{
    margin-bottom: 24px;
    span{
        display: block;
    }
    &__card:hover{
        a{
            text-decoration: none;
        }
    }
    .tax-detail{
        &__title{
            font-size: 36px;
            line-height: 1;
            margin-bottom: 12px;
            color: $color-gray-400;
            b{
                color: $color-green-500;
                font-weight: normal;
            }
        }
        &__name{
            font-size: 36px;
            line-height: 1;
            margin-bottom: 24px;
            color: $color-green-500;
        }
        &__description{
            color: $color-gray-400;
            margin-bottom: 0;
            max-width: 70%;
        }
    }

    .webinar-event{
  
        &__title{
            font-size: 18px;
            font-weight: bold;
            margin-bottom: 12px;
            margin-top: 20px;
        }
        ul{
            list-style-type: none;
            margin: 0;
            padding: 0;
            li{
                padding: 5px 0 5px 0px;
                position: relative;
                &::before{
                    content: '';
                    background-image: url(../images/webinar-icon.png);
                    background-size: contain;
                    background-repeat: no-repeat;
                    width: 24px;
                    height: 24px;
                    position: absolute;
                    left: 0;
                }
                a{
                    color: $color-gray-400;
                    font-size: 14px;
                }
            }
        }
    }
}