.cmp-featured-jobs {

    &.no-mb {
        margin-bottom: 0 !important;
    }

    &__section-title {

    }

    &__item {
        border: 2px solid #C9C9C9;
        border-radius: 12px;
        height: 100%;
        display: flex;
        flex-direction: column;

        &-cta-row {
            margin-top: auto;
        }
        
        &-icon {
            width: 25px;
            
        }

        &-subtitle {
            font-size: 14px;
            line-height: 18px;
            letter-spacing: 0.2px;
            color: $color-gray-500;
            font-weight: normal;
            @include non-latin($lang: zh, $line-height: 20px);
        }
        
        &-body {
            height: 74px;
            overflow:hidden;
            
        }
        &-posted {
            font-size: 14px;
            line-height: 20px;
        }

        &-cta {
            margin-top: auto;
            
        }

        &-share {
            &-cta {
                
            a {
                color: $color-blue-500;
                font-weight: 500;
                text-transform: uppercase;

                &:before {
                    position: absolute;
                    content: '';
                    width: 24px;
                    height: 24px;
                    left: 6px;
                    background-repeat: no-repeat;
                    background-position: center center;
                }

                &.share-icon:before {
                    background-image: url(../images/icon-case_study-share.svg);
                }	
            }
        }

        border-radius: 12px;
		box-shadow: rgba(0,0,0,0.35) 0 0 12px;
		background-color: $white;
		padding: 24px;
		width: auto;
		display: inline-block;
        position: absolute;
        z-index: 1000;
        top: 100%;
		@include media-breakpoint-down(md) {
			display: block;
		}
			
		&-list {
			list-style: none;
			position: relative;
			padding-left: 36px;
			margin-bottom: 0;

			&--item {
				@include media-breakpoint-down(md) {
    			}
	    		&:last-child {
					@include media-breakpoint-up(lg) {
		    			margin-bottom:0 !important;
					}
				}

				a {
					color: $color-gray-500;
					&:before {
						position: absolute;
						content: '';
						width: 24px;
						height: 24px;
						left: 0px;
						background-repeat: no-repeat;
						background-position: center center;
					}
						&.email-icon:before {
						background-image: url(../images/icon-case_study-email.svg);
					}
						&.linkedin-icon:before {
						background-image: url(../images/icon-case_study-linkedin.svg);
					}
				}
			}
    	}
    }
    
    }

    
}