.jobs-masthead-search {
    align-items: center;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    &.no-mb {
        margin-bottom: 0;
    }
    
    &.text-color-white {
    	color: $color-white;
    }
    &.text-color-black {
    	color: $color-gray-500;
    }

    /* variations */
    &.masthead-full {
        background-color: $color-gray-200;
        height: auto;
        min-height: 580px;
        overflow: hidden;

        @include media-breakpoint-down(lg) {
            min-height:540px;
        }

        .container {
            padding-bottom: 120px;
            padding-top: 160px;
        
            @include media-breakpoint-down(lg) {
                padding-bottom: 60px;
                padding-top: 120px;
            }
        }
    
        &.text-center {
            padding-top: 264px;
            padding-bottom: 192px;

            @include media-breakpoint-down(lg) {
                padding-top: 0px;
                padding-bottom: 120px;
            }

            .cmp-masthead-hero__cta-wrap {
                margin-top: 48px !important;
            }
        }

        &.transparent-header{
            @include media-breakpoint-up(lg) {
                padding-top: 120px;
            }
        }
    }
   
    &.hero {
        background-size: 0;

        &.no-mb {
            margin-bottom: 0;
        }

        .container{
            @include media-breakpoint-down(lg) {
                padding-top: 36px;
                padding-bottom: 72px;
            }
        }
        .cmp-masthead-hero__headline{ 
            color: $color-black;
            font-size:36px;
            line-height: 42px;
            font-weight:400;
            margin-bottom:.5em;

            @include media-breakpoint-up(lg) {
                font-size: 54px;
                line-height: 58px;
            }
        }

        .cmp-masthead-hero__subheadline,
        .cmp-masthead-hero__body {
            color: $color-gray-400;
        }

        .cmp-masthead-hero__subheadline {
            color: $color-gray-400;
            font-size: 16px;
            font-weight: 400;
            letter-spacing: 0.2px;
            line-height: 24px;
            @include non-latin($lang: ja, $line-height: 20px);
            @include non-latin($lang: zh, $line-height: 20px);

            @include media-breakpoint-up(lg) {
                font-size: 20px;
                line-height: 28px;
                @include non-latin($lang: ja, $letter-spacing: 0.2px, $line-height: 30px);
                @include non-latin($lang: zh, $letter-spacing: 0.2px, $line-height: 30px);
            }
        }

        .cmp-masthead-hero--Image__mobile{
            width: 100%;
            height: auto;
        }
        @include media-breakpoint-up(lg) {
            background-size: cover;
            margin: 0 0 120px;
            padding: 96px 0;

            .cmp-masthead-hero--Image__mobile{
                display:none;
            }

            .text-center .row, .text-left .row, .text-right .row{
                text-align: left;
                background-color: $color-white;
                width: calc(50% - 12px);
                padding:60px 60px 84px;
                border-radius:12px;
                margin:0;
                box-sizing: border-box;
                display:inline-block;

                -moz-box-shadow: 0 0 12px rgba(0,0,0,0.5);
                -webkit-box-shadow: 0 0 12px rgba(0,0,0,0.5);
                box-shadow: 0 0 12px rgba(0,0,0,0.5);
            }
        }
           
    }
    // &.hero.transparent-header{
    //     margin-top:0;
    //     @include media-breakpoint-up(lg) {
    //         padding-top:120px;
    //         .container{
    //             padding-top:60px;
    //         }
    //     }
    // }

    /* end variations */

    &__body,
    &__headline,
    &__subheadline,
    &__eyebrow, 
    &__statistic-value,
    &__statistic-copy
    {
        color: $color-white;

        .text-color-black & {
            color: $color-black;
        }
    }

    &__body {
        *:last-child {
            margin-bottom: 0;
        }
    }

    &__subheadline {
        max-width: 650px;

        @include media-breakpoint-up(lg) {
            font-size: 20px;
            letter-spacing: 0.2px;
            line-height: 28px;
            @include non-latin($lang: ja, $line-height: 30px);
            @include non-latin($lang: zh, $line-height: 30px);
        }

        .text-left & {
            margin-left: 0;
            margin-right: auto;
        }

        .text-center & {
            font-family: $font-headline;
            margin-left: auto;
            margin-right: auto;
            position:relative;
            display:block;
            @include non-latin-fonts();

            @include media-breakpoint-down(lg) {
                
                align-items:center;
            }

            @include media-breakpoint-up(lg) {
                font-size: 32px;
                letter-spacing: 0.5px;
                line-height: 36px;
            }
        }

        .text-right & {
            margin-left: auto;
            margin-right: 0;
        }
    }

    &__cta-2 {
        margin-top: 24px;
        display: block;
        border: 0 !important;

        &:hover {
            border: 0 !important;
        }

        @include media-breakpoint-up(lg) {
            display: inline;
            padding: 4px 24px 4px;
            
        }
    }

    .container {

        .btn-secondary-blue
        {
            border-color: $color-blue-600!important;
        }

        .owl-nav {
            display: none;
        }
    }
    
    &.text-color-black{
		.filter-group{    
	    	.keyword-input{
	    		&::before{
	    			background-image: get-icon('search', $color-gray-500);
	    		}
	    		input{
	    			color: $color-gray-500;
					border: 2px solid $color-gray-500;
				}	    	
	    	}
    	}
    }
    
	&.text-color-white{
		.filter-group{    
	    	.keyword-input{
	    		&::before{
	    			background-image: get-icon('search', $color-white);
	    		}
	    		input{
	    			color: $color-white;
					border: 2px solid $color-white;
				}
	    	}
    	}
    }
    
    .job-search-form {

	    .filter-group{
	    
	    	@include media-breakpoint-up(lg){
	            float:left;
	        }
	        
	        &-button{
		        @include media-breakpoint-up(lg){
		            float:left;
		            margin-top: 0px!important;
		            margin-left: 25px;
		        }
	        }

	    	.keyword-input{
		        width: 650px;
		        position: relative;

		        @include media-breakpoint-down(md){
		            width: 100%;
		            margin-bottom: 24px;
		        }

		        &::before{
		            content: '';
		            width: 18px;
		            height: 18px;
		            
		            background-size: contain;
		            background-position: left center;
		            position: absolute;
		            z-index: 9;
		            left: 20px;
		            top: 50%;
		            transform: translateY(-50%);
		        }

		        input{
		            width: 100%;
		            padding: 8px 50px 8px 50px;
		            border-radius: 5px;
		            font-size: 14px;
		        	background: transparent;
		        }	        
		    }
	    }
    }
}
