.btn {
	border: 2px solid !important;
	border-radius: 6px;
	color: $color-black;
	font-family: $font-sans-serif;
	font-size: 14px;
	font-weight: 500;
	letter-spacing: .035em; /*1.2px;*/
	padding: 4px 24px 4px;
	text-decoration: none;
	text-transform: uppercase;
	display:inline-block;
	@include non-latin-fonts();

	&:focus {
		outline: 0 !important;
		box-shadow: none !important;
	}

	&:hover {
		text-decoration: none;
	}

	@include media_breakpoint_up(lg) {
		font-size: 16px;
		line-height: 19px;
		letter-spacing: 1.2px;
		padding: 10px 24px 10px;
		@include non-latin($lang: ja, $letter-spacing: 1.4px, $line-height: 24px);
		@include non-latin($lang: zh, $letter-spacing: 1.4px, $line-height: 24px);
	}

	&-reset{
		&-filter{
			text-transform: capitalize;
			color: $color-blue-500;
		}
	}
	// Include rules for both btn-primary and btn-primary-green inorder to solve a plugin error 
	&-primary,
	&-primary-green {
		&, &:active {
			background-color: $color-green-500!important;
			border-color: $color-green-500 !important;
			color: $color-white;
		}

		&:hover,  &:focus {
			background-color: $color-green-600!important;
			border-color: $color-green-600!important;
			color: $color-white;
		}

		&.inactive {
			background-color: $color-gray-200!important;
			border-color: $color-gray-200!important;
			color: $color-gray-500;
		}
		&-blue{
			background-color: $color-blue-500!important;
			border-color: $color-blue-500 !important;
			color: $color-white !important;
		}
	}
