	// 		color: $color-gray-500;
	// 	}
	// }

	&-secondary {
		&-white {
			border-color: $color-white!important;
			background-color: transparent !important;
			&, &:active {
				background-color: transparent;
				color: $color-white;
			}

			&:hover,  &:focus {
				border-color: $color-white!important;
				background-color: $color-white !important;
				color: $color-gray-500 !important;
			}

		}

		&-blue {
			font-size: 14px;
			border-color: $color-blue-600!important;
			&, &:active {
				background-color: transparent!important;
				color: $color-blue-600;
			}

			&:hover,  &:focus {
				background-color: $color-blue-600!important;
				color: $color-white!important;
			}

		}

		&-green {
			font-size: 14px;
			border-color: $color-green-500!important;
			&, &:active {
				background-color: transparent!important;
				color: $color-green-500;
			}

			&:hover,  &:focus {
				background-color: $color-green-600!important;
				border-color: $color-green-600!important;
				color: $color-white !important;
			}

		}

		&-whiteblue {
			font-size: 14px;
			border-color: $color-blue-500!important;
			&, &:active {
				background-color: transparent!important;
				color: $color-blue-500;
			}

			&:hover,  &:focus {
				background-color: $color-blue-500!important;
				color: $color-white!important;
			}

		}

	}


	&-tertiary {
		&-green {
			background-color: transparent !important;
			border: 0!important;
			border-bottom: 1px solid transparent!important;
			border-radius: 0;
			color: $color-green-600;
			padding: 0;
			text-align: left;
			vertical-align: middle;
			@include non-latin($lang: ja, $letter-spacing: 0.5px);
			@include non-latin($lang: zh, $letter-spacing: 0.5px);

			&:after {
				@include tertiary-arrow();
			}

			&:hover, &:focus {
				//border-bottom: 1px solid $color-green-600!important;	
				border-radius: 0;
				color: $color-green-600;
				background-color: transparent !important;
			}

			&.btn-back {
				font-size: 12px;

				&:after {
					display: none;
				}
				&:before {
					@include tertiary-arrow();
					transform: rotate(180deg);
				}
			}
		}

		&-white {
			background-color: transparent !important;
			border: 0!important;
			border-bottom: 1px solid transparent !important;
			border-radius: 0;
			color: $color-white;
			padding: 0;
			text-align: left;
			vertical-align: middle;
			@include non-latin($lang: ja, $letter-spacing: 0.5px);
			@include non-latin($lang: zh, $letter-spacing: 0.5px);
			
			&:after {
				@include tertiary-arrow('../images/arrow_white.png');
			}

			&:hover, &:focus {
				border-bottom: 1px solid $color-white!important;
				border-radius: 0;
				color: $color-white;
				background-color: transparent !important;
			}

			&.btn-back {
				font-size: 12px;

				&:after {
					display: none;
				}
				&:before {
					@include tertiary-arrow('../images/arrow_white.png');
					transform: rotate(180deg);
				}
			}
		}
	}

	&-quaternary{
		&-black{
			text-align: left;
			padding: 0 0 6px;
			border: 0 !important;
			border-bottom: 2px solid transparent !important;
			border-radius: 0;
			font-size: 20px;
			font-weight: 700;
			line-height: 26px;
			letter-spacing: 0.2px;
			text-transform: none;

			span {
				padding-right: 12px;

				@include media-breakpoint-up(lg) {
					padding-right: 20px;
				}
			}

			&:hover {
				border-bottom: 2px solid $color-black !important;
			}

			@include media-breakpoint-up(lg) {
				text-align: center;
				font-family: $font-headline;
				font-size: 32px;
				font-weight: 700;
				line-height: 36px;
				letter-spacing: 0.5px;
				@include non-latin-fonts();
			}
			
			&:after {
				background-image: url("data:image/svg+xml,%3Csvg width='36px' height='36px' viewBox='0 0 36 36' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18 0C8.05887 0 0 8.05887 0 18C0 27.9411 8.05887 36 18 36C27.9411 36 36 27.9411 36 18C36 8.05887 27.9411 0 18 0ZM18 1.5C27.1127 1.5 34.5 8.8873 34.5 18C34.5 27.1127 27.1127 34.5 18 34.5C8.8873 34.5 1.5 27.1127 1.5 18C1.5 8.8873 8.8873 1.5 18 1.5ZM27.7622 18L19.8621 25.9001L18.9843 25.0223L25.1273 18.8793L8.06896 18.8793L8.06896 17.3793L25.3859 17.3793L18.9843 10.9777L19.8621 10.0999L27.7622 18Z' fill='%231A1A1A' fill-rule='evenodd' stroke='none' /%3E%3C/svg%3E");
				width: 36px;
				height: 36px;
				background-repeat: no-repeat;
				background-position: center center;
				display: inline-block;
				content: '';
				margin: 12px 0 0;
				vertical-align: -12px;

				@include media-breakpoint-up(lg) {
					vertical-align: -6px;
				}
			}
		}
	}
}
